import { Command } from "commander"
import { CommandPlugin } from "../../core/cli/CLI"
import { Settings } from "../../core/settings/Settings"
import { isTrue } from "../../fe/Boolean"
import { Obj } from "../../fe/Object"

/**
 * Used by sync and sync-file
 */
export const SkipUpdateArg: CommandPlugin = {
  beforeParse: (cmd: Command) =>
    cmd.option(
      "--skip-updates",
      "DANGEROUS: If you want to immediately import specific files or directories and skip any pending library maintenance tasks (like library rebuilds), use this argument. Note that asset aggregation may be incorrect after using this command if there are updates pending."
    ),
  afterParse: (opts: Obj) => {
    if (isTrue(opts.skipUpdates)) {
      Settings.noModelUpdates.envValue = true
    }
  }
}
