import { lazy } from "../../core/Lazy"
import { compact, isNotEmpty } from "../../fe/Array"
import { blank, mapNotBlank } from "../../fe/Blank"
import { eql } from "../../fe/Eql"
import { map } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { gt, sigFigs } from "../../fe/Number"
import { allKeysDefined, keys, values } from "../../fe/Object"
import { fmt } from "../../fe/Units"
import { definedAndEql } from "../Eql"
import { geohashDistanceMeters } from "../GeoHash"
import { mkLogger } from "../Logger"
import { validRotations } from "../img/Dimensions"
import { toImageHash } from "../img/ImageHash"
import {
  compareImageHashes,
  isSimilarImageHash
} from "../img/ImageHashComparison"
import { Settings } from "../settings/Settings"
import { CapturedAt } from "../tags/CapturedAt"
import {
  findInequalFields,
  whyExposureSettingsNotSimilar
} from "../tags/ExifUid"
import { isVideoMimeType } from "../tags/MimeTypes"
import { IAssetFile } from "./IAssetFile"

const logger = lazy(() => mkLogger("sync-file.AssetFileComparator"))

function whyInequal(
  af1: IAssetFile,
  af2: IAssetFile,
  field: keyof IAssetFile,
  normalizer: (a: any, b: any) => [any, any] = (a, b) => [a, b]
): Maybe<string> {
  const a = af1[field]
  const b = af2[field]
  if (blank(a) || blank(b) || eql(a, b)) return
  const [an, bn] = normalizer(a, b)
  return eql(an, bn) || eql(a, bn) || eql(an, b)
    ? undefined
    : `Different ${String(field)}: ${a}  ${b}`
}

function lc(s: any) {
  return mapNotBlank(s, ea => ea.toLowerCase().trim().normalize())
}

function lc2(a: any, b: any) {
  return [lc(a), lc(b)] as [any, any]
}

export const VideoImageMismatchMsg = "Videos  images"

function whyExifUidNotEql(
  af1: IAssetFile,
  af2: IAssetFile,
  field: "cameraId" | "lensId" | "imageId"
) {
  const ne = findInequalFields(af1[field], af2[field])
  return ne == null
    ? undefined
    : `Different ${field} (${ne.aKey}:${ne.aValue}  ${ne.aKey}:${ne.aValue})`
}

export function whyNotSameAsset(
  needle: Maybe<IAssetFile>,
  haystack: Maybe<IAssetFile>,
  siblings: IAssetFile[]
) {
  {
    const why = whyNotSimilarAssetFile(needle, haystack)
    if (why != null) return why
  }
  if (Settings.assetAggregation.valueOrDefault === "intersection") {
    for (const sib of siblings) {
      // don't test again:
      if (sib.id !== haystack?.id) {
        const why = whyNotSimilarAssetFile(needle, sib)
        if (why != null) return why
      }
    }
  }
  return
}

export function isSimilarAssetFile(
  af1: Maybe<IAssetFile>,
  af2: Maybe<IAssetFile>
): boolean {
  return whyNotSimilarAssetFile(af1, af2) == null
}

/**
 * Only compares these specific AssetFiles. You probably want whyNotSameAsset.
 */
export function whyNotSimilarAssetFile(
  af1: Maybe<IAssetFile>,
  af2: Maybe<IAssetFile>
): Maybe<string> {
  if (!allKeysDefined(af1, "width", "height", "mimetype", "sha")) {
    return "af1 was missing required fields"
  }
  if (!allKeysDefined(af2, "width", "height", "mimetype", "sha")) {
    return "af2 was missing required fields"
  }

  if (definedAndEql(af1.sha, af2.sha)) {
    logger().debug("matching SHA", { af1, af2 })
    // Same SHA is always the same asset
    return
  }

  // TODO: Live Photos support will need to revisit this!
  const af1IsVideo = isVideoMimeType(af1.mimetype)
  const af2IsVideo = isVideoMimeType(af2.mimetype)

  if (af1IsVideo !== af2IsVideo) {
    return logger().tap({
      msg: "whyNotIsSimilar",
      result: VideoImageMismatchMsg,
      meta: {
        af1: af1.uri,
        af2: af2.uri,
        af1IsVideo,
        af2IsVideo
      }
    })
  }

  const af1ca = CapturedAt.fromAssetFile(af1)
  const af2ca = CapturedAt.fromAssetFile(af2)
  if (af1ca == null) return "af1 missing capturedAt metadata"
  if (af2ca == null) return "af2 missing capturedAt metadata"

  const capturedAtIsFuzzy = af1ca.isFuzzy || af2ca.isFuzzy

  if (
    capturedAtIsFuzzy &&
    Settings.allowFuzzyDateImageHashMatches.valueOrDefault
  ) {
    const ih1 = toImageHash(af1 as any)
    const ih2 = toImageHash(af2 as any)
    if (
      // allow image hashes to be _pretty_ close
      isSimilarImageHash(ih1, ih2, {
        rotationsToCheck: validRotations(af1, af2)
      })
    ) {
      logger().info("allowFuzzyDateImageHashMatches mode: image hashes match", {
        af1,
        af2
      })
      return
    }
  }

  if (!af1ca.overlaps(af2ca)) {
    return `captured-at ${af1ca.toISOStringWithPrecision()} != ${af2ca.toISOStringWithPrecision()}`
  }

  // If any of these *don't* match, we're definitely a different photo. Order
  // doesn't really matter.
  const fieldDiff =
    whyInequal(af1, af2, "make", lc2) ?? whyInequal(af1, af2, "model", lc2)
  // DON'T CHECK FOR GEOHASH HERE! it might be a bit different!

  if (fieldDiff != null) return fieldDiff

  const dist = map(geohashDistanceMeters(af1.geohash, af2.geohash), ea =>
    sigFigs(ea, 2)
  )
  const fields = {
    cameraId: whyExifUidNotEql(af1, af2, "cameraId") ?? null,
    imageId: whyExifUidNotEql(af1, af2, "imageId") ?? null,
    lensId: whyExifUidNotEql(af1, af2, "lensId") ?? null,
    exposureSettings: whyExposureSettingsNotSimilar(af1, af2) ?? null,
    closeGeoHash: gt(dist, Settings.gpsErrorMeters.valueOrDefault)
      ? "geohashes are " + fmt(dist!) + "m different: too far apart"
      : null
  }

  logger().debug("whyNotSimilar()", { fields })

  const mismatched = compact(values(fields))
  if (isNotEmpty(mismatched)) {
    return mismatched.join(";")
  }

  if (Settings.useImageHashes.valueOrDefault) {
    const ih1 = toImageHash(af1 as any)
    const ih2 = toImageHash(af2 as any)
    if (ih1 != null && ih2 != null) {
      const ihc = compareImageHashes(ih1, ih2, {
        capturedAtIsFuzzy
      })
      return true === ihc?.isSimilar ? undefined : ihc?.whyNotSimilar
    }
  }

  // If we aren't using image hashing, we have to lean (hard) on metadata:
  const fieldMatches = keys(fields).filter(k => fields[k] == null)

  return fieldMatches.length >=
    Settings.minMatchesWithoutImageHashes.valueOrDefault
    ? undefined
    : "Insufficient metadata correlation"
}
