import { compact } from "../../fe/Array"
import { minuteMs, weekMs } from "../../fe/Date"
import { fmtDuration } from "../../fe/FmtDuration"
import { Maybe } from "../../fe/MaybeTypes"
import { lt } from "../../fe/Number"
import { mkLogger } from "../Logger"
import { mapGt0 } from "../Number"
import { Average } from "./Average"
import { avg } from "./Vector"

export class ETA {
  private readonly remainingMs = new Average(10)
  private readonly logger = mkLogger("ETA")

  push(ms: Maybe<number>) {
    mapGt0(ms, ea => this.remainingMs.push(ea))
  }

  clear() {
    this.remainingMs.clear()
  }

  avg() {
    return avg(
      compact([this.remainingMs.weightedSampleAvg, this.remainingMs.sampleMode])
    )
  }

  fmtEstimate(delta = 1): Maybe<string> {
    return mapGt0(this.avg(), ea => {
      const eta = ea * delta
      if (eta < minuteMs) {
        return "less than a minute remains"
      }
      if (
        // no crazy-long estimates:
        eta < weekMs &&
        // No wildly-fluctuating estimates. 1 sigma should
        // be < 1/3 mean (people may be irritated with higher fluctuations)
        lt(this.remainingMs.sampleStdDev, ea / 5)
      ) {
        return (
          "about " +
          fmtDuration(eta, 1, { plural: "remains", singular: "remain" })
        )
      } else {
        this.logger.debug(
          "Skipping remaining, avgMillisRemaining is too wiggly",
          {
            avg: eta,
            ...this.remainingMs.stats()
          }
        )
        return
      }
    })
  }
}
