import { Knex } from "knex"
import { DoneWrapper } from "../../core/async/Done"
import { end, EndableRanks, ending } from "../../core/async/Endable"
import { lazyAsync } from "../../core/async/LazyAsync"
import { BoundedList } from "../../core/BoundedList"
import { DirectoryEntry } from "../../core/fs/DirectoryEntry"
import { DirectoryWalker } from "../../core/fs/DirectoryWalker"
import { containedByNativePath } from "../../core/fs/Path"
import { PosixFile } from "../../core/fs/PosixFile"
import { recentSyncReports, syncReport } from "../../core/fs/SyncReport"
import { unprocessedPaths_ } from "../../core/fs/SyncReportReader"
import { lazy } from "../../core/Lazy"
import { mkLogger } from "../../core/Logger"
import { ETA } from "../../core/math/ETA"
import { sum } from "../../core/math/Vector"
import { isTest } from "../../core/NodeEnv"
import { mapGt0 } from "../../core/Number"
import { Settings } from "../../core/settings/Settings"
import { capitalize } from "../../core/String"
import { isPaused } from "../../core/work/IsPaused"
import { maxConcurrentImports } from "../../core/work/MaxCpus"
import { Percents } from "../../fe/api/ProgressState"
import { compactBlanks, isEmpty } from "../../fe/Array"
import { firstNotBlank, mapNotBlank } from "../../fe/Blank"
import { isTrue } from "../../fe/Boolean"
import { secondMs } from "../../fe/Date"
import { Latch } from "../../fe/Latch"
import { map } from "../../fe/Maybe"
import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { round, sigFigs, toInt } from "../../fe/Number"
import { fmt, plur } from "../../fe/Units"
import { AssetFile } from "../model/AssetFile"
import { Progress, ProgressUpdatePojo } from "../model/Progress"
import { ProgressMetaNames, ProgressMetaRecord } from "../model/ProgressMeta"
import { FileProcessor } from "../sync-file/ImportResult"
import { AssetFileQueue } from "./AssetFileQueue"
import { Sync } from "./Sync"

// For rusty's volume2, there were 739197 folders in 4899456376 bytes.
// That's ~800K folders in ~5TB of data: 4899456376 / 739197 = 6628 bytes
// per folder. That seems OKish?

// const DirectoryContentsEstimateBytes = 5 * KB

/**
 * Manages a DirectoryWalker, AssetProcessor, and cleanup queue, and restoring
 * previous state when available.
 *
 * Also assembles Progress reports.
 */
export class DirectorySync extends DoneWrapper implements Sync {
  static async for({
    root,
    rootUri,
    fileProcessor
  }: {
    root: PosixFile
    rootUri: string
    fileProcessor: FileProcessor
  }): PromiseMaybe<DirectorySync> {
    if (!(await root.clear().isDirectory())) {
      mkLogger("sync.DirectorySync").warn(
        "Cannot sync " + root + ": it's not a directory (or not readable)",
        {
          root,
          rootUri
        }
      )
      return
    }
    return new DirectorySync(
      root,
      rootUri,
      await AssetFileQueue.for(root, fileProcessor)
    )
  }

  readonly start = Date.now()
  private _isScanning = true // assume we start scanning
  private readonly eta = new ETA()
  readonly recentlyScannedDirs = new BoundedList<string>(10)
  scannedDirsCount = 0
  private directoryWalker: Maybe<DirectoryWalker>

  /**
   * We need two flags here: a public "done" latch (that we get from
   * DoneWrapper), and a private "we're almost done, but we need to do some
   * final cleanup" latch, which is this:
   */
  readonly preDone = new Latch()

  private priorProcessedImageCount = 0
  private priorProcessedVideoCount = 0

  protected constructor(
    readonly root: PosixFile,
    readonly rootUri: string,
    readonly afq: AssetFileQueue
  ) {
    super(
      "sync.DirectorySync(" + root + ")",
      () => this.onEnd(),
      EndableRanks.first
    )
    void this.doneLatch.observe(this.run())
  }

  private async onEnd() {
    await end(this.directoryWalker)
    await this.saveSyncProgress.refresh()
    await syncReport.prior()?.close()
  }

  // NO TTL! This must be the same instance for the same directory sync run.
  readonly _progress = lazy(() =>
    Progress.insertNew({ uri: this.rootUri, volume: this.root.nativePath })
  )

  readonly forceDirectoryScan = lazy(() => {
    const forceSync = Settings.forceSync.valueOrDefault
    const isArgvPath = Settings.argvScanPaths.values.some(ea =>
      this.root.eql(ea)
    )
    return this.logger.tap({
      msg: "forceDirectoryScan",
      result: forceSync || isArgvPath,
      meta: { forceSync, isArgvPath }
    })
  })

  readonly initialMeta = lazy<PromiseMaybe<ProgressMetaRecord>>(async () =>
    this.forceDirectoryScan() ? undefined : this._progress().getMetaAsRecord()
  )

  // lazy to throttle:
  readonly saveSyncProgress = lazy(
    () => Progress.saveSyncState(this),
    isTest ? 100 : secondMs
  )

  // lazy to throttle:
  readonly saveLastScannedDirectory = lazy(
    async () => {
      const p = this._progress()
      p.setMeta(
        ProgressMetaNames.scannedDirectoryCount,
        this.scannedDirsCount.toString()
      )
      const lastScannedDirectory = this.recentlyScannedDirs.at(-1)
      if (lastScannedDirectory != null) {
        p.setMeta(ProgressMetaNames.lastScannedDirectory, lastScannedDirectory)
      }
    },
    isTest ? 100 : secondMs
  )

  readonly directoryListener = (dir: DirectoryEntry) => {
    return mapNotBlank(dir?.nativePath, ea => {
      this.scannedDirsCount++
      this.recentlyScannedDirs.push(ea)
      this.logger.debug("directoryListener()", {
        scannedDirsCount: this.scannedDirsCount,
        dir: dir.nativePath
      })
      return this.saveLastScannedDirectory()
    })
  }

  readonly earliestStartTime = lazy(async () => {
    const times = await Progress.times()
    const priorTimes = times.find(ea => ea.uri === this.rootUri)
    return priorTimes?.lastStartedAt ?? Date.now()
  }) // cache forever

  async #maybeRunDirectorySync_() {
    const forceDirectoryScan = this.forceDirectoryScan()
    const initialMeta = await this.initialMeta()

    if (!forceDirectoryScan && isTrue(initialMeta?.completedDirectoryScan)) {
      return this.logger.tap({
        msg: "#maybeRunDirectorySync_(): no-op",
        result: undefined,
        meta: { forceDirectoryScan, initialMeta }
      })
    }

    const progress = this._progress()
    this.scannedDirsCount = toInt(initialMeta?.scannedDirectoryCount) ?? 0

    this.directoryWalker = new DirectoryWalker(
      (await this.root.directoryEntry())!,
      this.afq.fileListener,
      this.directoryListener,
      initialMeta?.lastScannedDirectory
    )
    await this.directoryWalker.awaitEnd()
    this.logger.info("maybeRunDirectorySync_() completed directoryWalker", {
      scannedDirsCount: this.scannedDirsCount
    })
    progress.setMeta(ProgressMetaNames.completedDirectoryScan, "true")
    return
  }

  private async maybeEnqueueStaleAssetFiles() {
    if (
      !Settings.forceSync.valueOrDefault &&
      isTrue((await this.initialMeta())?.enqueuedStaleFiles)
    ) {
      this.logger.info(
        "maybeEnqueueStaleAssetFiles() no-op: prior enqueuedStaleFiles."
      )
    } else {
      const progress = this._progress()
      const earliestStartTime = await this.earliestStartTime()

      this.logger.info("maybeEnqueueStaleAssetFiles() starting enqueue", {
        assetFileQueuePendingCount: await this.afq.pendingCount()
      })
      AssetFile.ops().batched({
        onResults: async (arr: AssetFile[]) =>
          this.afq.enqueueOrTouchAssetFiles(arr),
        qb: (qb: Knex.QueryBuilder) =>
          qb
            .whereLike("uri", this.rootUri + "%")
            .andWhere("updatedAt", "<", earliestStartTime)
      })
      this.logger.info("maybeEnqueueStaleAssetFiles() finished enqueue", {
        assetFileQueuePendingCount: await this.afq.pendingCount()
      })
      progress.setMeta(ProgressMetaNames.enqueuedStaleFiles, "true")
    }
    return
  }

  private readonly retryEnqueued = lazy(async () => {
    const recent = await recentSyncReports()
    if (isEmpty(recent)) {
      this.logger.warn("retryEnqueued(): no-op: no recent sync reports")
      return
    }
    const stuck = await unprocessedPaths_({
      syncReportJsons: recent,
      acceptPath: ea =>
        containedByNativePath({
          ancestor: this.root,
          descendant: ea
        })
    })
    this.logger.info(
      "retryEnqueued(): parsed prior sync reports to find stuck paths.",
      {
        stuck_length: stuck.length,
        stuck
      }
    )
    await this.afq.enqueueWork(stuck)
  })

  private readonly run = lazy(async () => {
    const meta = await this.initialMeta()

    this.logger.info("setup()", {
      meta
    })

    // Restore prior state:

    map(meta?.lastScannedDirectory, ea => this.recentlyScannedDirs.push(ea))
    mapGt0(
      meta?.processedImageCount,
      ea => (this.priorProcessedImageCount = ea)
    )
    mapGt0(
      meta?.processedVideoCount,
      ea => (this.priorProcessedVideoCount = ea)
    )

    await this.#maybeRunDirectorySync_()

    this.logger.info("setup() setting isScanning to false.")
    this._isScanning = false

    await this.afq.waitForCompletion()

    if (1 > 2 || Settings.retryEnqueued.valueOrDefault) {
      this.afq.clearRecentlyProcessed()
      await this.retryEnqueued()
      await this.afq.waitForCompletion()
    }

    await this.maybeEnqueueStaleAssetFiles()

    // We don't need to short-circuit this. We either have work to do or we
    // don't.

    await this.afq.waitForCompletion()

    // We're complete! Let's do some cleanup if we're not shutting down:
    if (!this.ended) {
      this.logger.info("DONE! Marking Progress as complete and updating meta.")
      await this.preDone.resolve()
      const p = await this.progress.refresh()
      p!.setMeta("completedDirectorySync", "true")
      p!.upsert({ completedAt: Date.now() })
      await this.end()
    }
  })

  get processedImageCount() {
    return this.priorProcessedImageCount + this.afq.processedImageCount
  }

  get processedVideoCount() {
    return this.priorProcessedVideoCount + this.afq.processedVideoCount
  }

  processedCount() {
    return this.processedImageCount + this.processedVideoCount
  }

  pendingCount() {
    return this.afq.pendingCount()
  }

  get isScanning() {
    return this._isScanning
  }

  /**
   * Terrible hack to just give *some* progress without requiring statting every
   * file for it's size (which is excruciatingly slow over NASes).
   *
   * NEVER SHOW THIS NUMBER TO THE USER. IT IS BOGUS.
   */
  async estimatedScannedPct(): Promise<number> {
    if (!this.isScanning) return 100
    // this will be undefined for UNC directories:
    // const estTotFiles = await this.estimatedTotalFileCount
    // const scanDirEst =
    //   gte0(estTotFiles) && this.scannedDirsCount <= estTotFiles
    //     ? (100 * this.scannedDirsCount) / estTotFiles
    //     : undefined
    // https://www.desmos.com/calculator/jwzkxpulmt
    const bogusEst = 100 / -(this.scannedDirsCount / 500 + 1) + 100
    // const result = clamp(0, 100, avg([scanDirEst, bogusEst])!)
    return sigFigs(bogusEst, 4)
  }

  readonly progress = lazyAsync({
    desc: this.name + ".progress",
    timeoutMs: secondMs,
    ttlMs: 250,
    later: async () => {
      const done = this.isDone() || this.preDone.isResolved()
      const paused = isPaused() || ending()
      const state = done ? "done" : paused ? "paused" : "processing"

      const dek: string[] = []
      if (!done && !paused) {
        mapNotBlank(this.afq.recentFileProgress(), ea => dek.push(ea))
        if (this.isScanning) {
          dek.push(
            "Scanning " +
              firstNotBlank(
                this.recentlyScannedDirs.shiftOrFirst(),
                this.root.nativePath
              )
          )
        }
      }

      const msg: string[] = []

      let counts =
        "Processed " +
        plur(this.processedImageCount, "photo") +
        " and " +
        plur(this.processedVideoCount, "video") +
        "."

      if (this.processedImageCount + this.processedVideoCount > 0) {
        msg.push(counts)
      }

      const pendingCount = await this.pendingCount()
      if (!done && pendingCount > 0) {
        msg.push(
          this.isScanning ? "At least" : "",
          fmt(pendingCount),
          "remain to be processed"
        )
        counts += ` ${this.isScanning ? "At least" : ""} ${fmt(
          pendingCount
        )} to do.`
      }
      if (pendingCount < 10) {
        dek.push((await this.afq.pendingContent()).join(", "))
      }
      dek.push(compactBlanks(msg).join(" "))
      if (pendingCount < 10) {
        dek.push()
      }

      const hed: string[] = []
      hed.push(capitalize(state))
      if (!done) map(this.eta.fmtEstimate(), ea => hed.push(ea))

      const p = this._progress()

      if (this.processedImageCount > 1) {
        p.setMeta(
          ProgressMetaNames.processedImageCount,
          String(this.processedImageCount)
        )
      }
      if (this.processedVideoCount > 1) {
        p.setMeta(
          ProgressMetaNames.processedVideoCount,
          String(this.processedVideoCount)
        )
      }

      const pcts = await this.percents()

      const obj: ProgressUpdatePojo = {
        state,
        hed: hed.join(", "),
        dek,
        updatedAt: Date.now(),
        ...pcts
      }

      if (state === "done") {
        obj.completedAt = Date.now()
      }

      return this.logger.tap({
        msg: ".progress()",
        level: "info",
        result: p.assignFromPojo(obj)
      })
    }
  })

  async percents(): Promise<Percents> {
    if (this.isDone()) {
      return {
        completePct: 100,
        incompletePct: 0,
        scanningPct: 0
      }
    }

    // How much has already been scanned:
    const scannedPct = round(await this.estimatedScannedPct())

    // How much remains to be scanned:
    const scanningPct = 100 - scannedPct

    const processed = this.processedCount()
    const pending = await this.pendingCount()

    const completePct =
      pending === 0 && processed === 0
        ? 0
        : round(scannedPct * (processed / (pending + processed)))
    const incompletePct = 100 - (completePct + scanningPct)
    const p = { completePct, incompletePct, scanningPct }
    this.logger.debug("percents(): ", { scannedPct, processed, pending, p })

    if (sum([p.completePct, p.incompletePct, p.scanningPct]) !== 100) {
      this.logger.warn("percents(): BUGGED", {
        p,
        processed,
        pending,
        scannedPct,
        scanningPct,
        incompletePct,
        completePct
      })
    }
    if (scannedPct === 100 && processed > 7) {
      const afq = this.afq
      const imgConcurrency = maxConcurrentImports()
      const imgCount = afq.pendingImagesCount()
      const vidCount = afq.pendingVideoCount()

      const imgMs =
        imgCount === 0
          ? undefined
          : map(afq.avgImageProcessingTime, ms =>
              sigFigs((imgCount * ms) / imgConcurrency, 2)
            )

      const vidMs =
        vidCount === 0
          ? undefined
          : map(afq.avgVideoProcessingTime, ms => sigFigs(vidCount * ms, 2))

      const eta = sum([imgMs, vidMs])

      if (eta > 0) {
        this.logger.info("ETA", {
          eta,
          imgMs,
          vidMs,
          imgCount,
          imgConcurrency,
          vidCount
        })
        this.eta.push(eta)
      }
    }
    return p
  }
}
