import { partition } from "../../core/Array"
import { withBoundedConcurrency } from "../../core/async/Promises"
import { time } from "../../core/async/PromiseTimer"
import { UpdateAssetArgs } from "../../core/event/PhotoStructureEvents"
import { submitTask_ } from "../../core/event/SharedState"
import { BaseFile } from "../../core/fs/BaseFile"
import {
  isSimpleFile,
  SimpleFile,
  toNativePath_
} from "../../core/fs/SimpleFile"
import { SyncFileStates, syncReport } from "../../core/fs/SyncReport"
import { l } from "../../core/licensing/Licensing"
import { mkLogger } from "../../core/Logger"
import { sum } from "../../core/math/Vector"
import { Settings } from "../../core/settings/Settings"
import { isAssetFileExt, isVideoExt } from "../../core/tags/FileTypes"
import { mkAssetUrl } from "../../core/web/WebUrls"
import { compact, compactBlanks, isEmpty, isNotEmpty } from "../../fe/Array"
import { notBlank } from "../../fe/Blank"
import { isTrue } from "../../fe/Boolean"
import { Maybe } from "../../fe/MaybeTypes"
import { toS } from "../../fe/toS"
import { PS_LIBRARY_SCHEME } from "../../fe/URI"
import { AssetFile } from "../model/AssetFile"
import { QueueItem } from "../stats/QueueItem"
import { FileProcessor } from "../sync-file/ImportResult"
import { WorkQueue } from "../WorkQueue"
import { precheckFiles } from "./PrecheckFiles"

/**
 * AssetFileQueue manages receiving files (presumably from DirectoryIterator),
 * pushing those files onto work queues, and popping files off when the
 * work iterator says there's free CPU. Those files are then imported by
 * AssetFileImporter.
 */
export class AssetFileQueue {
  private readonly logger = mkLogger("AssetFileQueue")

  private readonly imgQueue: WorkQueue
  private readonly vidQueue: WorkQueue

  static async for(rootDir: SimpleFile | string, fileProcessor: FileProcessor) {
    return new AssetFileQueue(toNativePath_(rootDir), fileProcessor)
  }

  private constructor(rootNativePath: string, _fileProcessor: FileProcessor) {
    this.imgQueue = WorkQueue.mk({
      queueNames: ["img:" + rootNativePath],
      processItem: qi => _fileProcessor(qi.contents)
    })
    this.vidQueue = WorkQueue.mk({
      queueNames: ["vid:" + rootNativePath],
      processItem: qi => _fileProcessor(qi.contents),
      concurrent: false
    })
  }

  async waitForCompletion() {
    await Promise.all([this.imgQueue.awaitDrain(), this.vidQueue.awaitDrain()])
  }

  clearRecentlyProcessed() {
    this.imgQueue.recentlyProcessed.clear()
    this.vidQueue.recentlyProcessed.clear()
  }

  get currentPaths() {
    return [
      ...this.imgQueue.currentQueueContents(),
      ...this.vidQueue.currentQueueContents()
    ]
  }

  get currentBaseFiles() {
    return this.currentPaths.map(ea => BaseFile.for(ea))
  }

  hasPath(nativePath: string) {
    for (const path of this.currentPaths) {
      if (nativePath === path) return true
    }
    return false
  }

  get currentVideoJobCount(): number {
    return this.vidQueue.currentWorkCount()
  }

  get processedCount(): number {
    return this.processedImageCount + this.processedVideoCount
  }

  get processedImageCount(): number {
    return this.imgQueue.processedCount()
  }

  get processedVideoCount(): number {
    return this.vidQueue.processedCount()
  }

  get avgImageProcessingTime() {
    return this.imgQueue.processMs.p84
  }

  get avgVideoProcessingTime() {
    return this.vidQueue.processMs.p84
  }

  async stats() {
    return {
      pendingCount: await this.pendingCount(),
      processedImageCount: this.processedImageCount,
      processedVideoCount: this.processedVideoCount,
      avgImageProcessingTime: this.avgImageProcessingTime,
      avgVideoProcessingTime: this.avgVideoProcessingTime
    }
  }

  currentCounterState() {
    return {
      processedImageCount: this.processedImageCount,
      processedVideoCount: this.processedVideoCount
    }
  }

  private itemQuery(queueIds: number[]) {
    // DON'T GET THIS NEAR ASYNC (knex will try to execute the query)
    return QueueItem.query().whereIn("queueId", queueIds)
  }

  private async queueIds() {
    return [...this.imgQueue.queueIds(), ...this.vidQueue.queueIds()]
  }

  /**
   * The count of enqueued and currently-being-processed items.
   */
  async pendingCount() {
    return QueueItem.dbl.pluckFirst<number>(
      this.itemQuery(await this.queueIds()).count("id")
    )
  }

  async pendingContent(limit = 10) {
    const queueIds = await this.queueIds()
    return QueueItem.dbl.pluckAll<string>(
      this.itemQuery(queueIds).select("contents").limit(limit)
    )
  }

  pendingImagesCount() {
    return this.imgQueue.pendingWorkCount()
  }

  pendingVideoCount() {
    return this.vidQueue.pendingWorkCount()
  }

  async etaMs() {
    return sum([this.imgQueue.etaMs(), this.vidQueue.etaMs()])
  }

  // fmtProgressEvent(pe: ProgressEvtWithPath) {
  //   const p = BaseFile.for(pe.path).baseWithGrandparent
  //   return pe.done === true
  //     ? "Processed " + p
  //     : compactBlanks([p, mapNotBlank(pe.op, ea => `(${ea})`)]).join(" ")
  // }

  /**
   * Try to give a taste of what we're working on.
   * @override
   */
  recentFileProgress(): Maybe<string[]> {
    return compact([
      this.imgQueue.recentlyProcessed.last()?.contents,
      this.vidQueue.recentlyProcessed.last()?.contents
    ])
  }

  async enqueueWork(input: (SimpleFile | string)[]) {
    if (isEmpty(input)) return
    const [videos, images] = partition(input.filter(isAssetFileExt), isVideoExt)
    this.logger.info("enqueueWork()", {
      input: input.map(toS),
      videos: videos.map(toS),
      images: images.map(toS)
    })
    this.imgQueue.enqueueWork(
      images.map(ea => ({ contents: toNativePath_(ea) }))
    )
    this.vidQueue.enqueueWork(
      videos.map(ea => ({ contents: toNativePath_(ea) }))
    )
  }

  async enqueueOrTouchAssetFiles(assetFiles: AssetFile[]) {
    const freshAssetFileIds: number[] = []
    const copyToLibrary =
      Settings.copyAssetsToLibrary.valueOrDefault && !(await l())
    const staleFiles: SimpleFile[] = []
    await withBoundedConcurrency({
      name: "enqueueOrTouchAssetFiles",
      laters: assetFiles.map(af => async () => {
        const pf = await af.posixFile_()
        const syncProgress = {
          path: pf?.nativePath ?? af.uri,
          url: mkAssetUrl(af.assetId)?.toString()
        }
        if (true === (await af.isFileDeleted())) {
          af.delete()
          const obj: UpdateAssetArgs = {
            assetId: af.assetId!,
            forceRebuildPreviews: isTrue(af.shown)
          }
          void submitTask_("repairAsset", obj)
          syncReport().onProgress({
            ...syncProgress,
            from: "enqueueOrTouch()",
            details: "file no longer exists",
            state: SyncFileStates.deleted
          })
        }
        if (pf == null) {
          this.logger.warn("Failed to decode URI to existing file", { af })
          return
        }
        const whyStale = await af.whyNotMatchesFile()
        const needToCopy =
          copyToLibrary &&
          isTrue(af.shown) &&
          !af.uri.startsWith(PS_LIBRARY_SCHEME)
        if (notBlank(whyStale) || needToCopy) {
          // Are we the shown assetfile, but we're supposed to be copying assets
          // to the library?
          syncReport().onProgress({
            ...syncProgress,
            from: "enqueueOrTouch()",
            state: SyncFileStates.enqueued,
            details: compactBlanks([
              whyStale,
              needToCopy
                ? "PS_COPY_ASSETS_TO_LIBRARY is true and this primary variant isn't in the library"
                : ""
            ]).join("; ")
          })
          staleFiles.push(pf)
        } else {
          syncReport().onProgress({
            ...syncProgress,
            from: "enqueueOrTouch()",
            details: "DB AssetFile is already in sync",
            state: SyncFileStates.noop
          })
          freshAssetFileIds.push(af.id!)
        }
      })
    })
    AssetFile.touch(freshAssetFileIds)
    await this.enqueueWork(staleFiles)
    this.logger.info("enqueueOrTouchAssetFiles()", {
      freshAssetFileIds,
      staleFiles
    })
  }

  async enqueueSimpleFiles(arr: SimpleFile[]) {
    if (isNotEmpty(arr)) {
      for (const ea of arr) {
        syncReport().onProgress({
          path: toNativePath_(ea),
          from: "AssetFileQueue",
          state: SyncFileStates.enqueued
        })
      }
      await this.enqueueWork(arr)
    }
  }

  /**
   * Give this method to DirectoryIterator's constructor
   */
  readonly fileListener = async (possibleAssets: (SimpleFile | string)[]) => {
    const arr = possibleAssets.map(ea =>
      isSimpleFile(ea) ? ea : BaseFile.for(ea)
    )
    this.logger.debug("fileListener()", arr)
    if (Settings.forceSync.valueOrDefault) {
      return this.enqueueSimpleFiles(arr)
    } else {
      const updateFreshFiles = true
      const { fresh, stale } = await time("sync.precheckFiles", () =>
        precheckFiles(arr, updateFreshFiles)
      )
      this.logger.debug("fileListener()", { fresh, stale })
      return this.enqueueSimpleFiles(stale)
    }
  }
}
