import { Deferred } from "../../core/async/Deferred"
import { thenOrElse } from "../../core/async/Promise"
import { PosixFile } from "../../core/fs/PosixFile"
import { lazy } from "../../core/Lazy"
import { Progress } from "../model/Progress"
import { Sync } from "./Sync"

/**
 * FileSync doesn't save progress to the db.
 */
export class FileSync implements Sync {
  private _ended = false
  readonly name: string
  readonly sync: Deferred<any>

  constructor(
    readonly root: PosixFile,
    sync: Promise<any>
  ) {
    this.name = "FileSync(" + root + ")"
    this.sync = new Deferred(this.name).observe(sync)
  }

  get ended() {
    return this._ended
  }

  end() {
    this._ended = true
  }

  isDone() {
    return this.sync.isSettled
  }

  donePromise() {
    return this.sync.promise
  }

  readonly uri = lazy(() =>
    thenOrElse(this.root.uri_(), () => "file:///" + this.root.posixPath)
  )

  get volume() {
    return this.root.nativePath
  }

  readonly _progress = lazy(async () =>
    Progress.insertNew({ uri: await this.uri(), volume: this.volume })
  )

  async progress() {
    const p = await this._progress()
    return p.assignFromPojo({
      state: this.sync.isPending ? "processing" : "done"
    })
  }
}
