import { PosixFile } from "../core/fs/PosixFile"
import { expensiveFileFiltersFor } from "../core/fs/PosixFileFilters"
import { isValidFile } from "../core/img/ValidFile"
import { PredicateBundle } from "../core/predicates/Predicates"
import { modelDb } from "./model/ModelDb"
import { notBlocklistedSha } from "./model/ShaBlocklist"

export function libraryFileFiltersFor(
  file: PosixFile,
  { validateFile = false }: { validateFile?: boolean } = {}
): PredicateBundle<PosixFile> {
  const arr = expensiveFileFiltersFor(file)
  if (modelDb() != null) arr.push({ notBlocklistedSha })
  if (validateFile) arr.push({ isValidFile })
  return arr
}
