import { end, EndableRanks } from "../../core/async/Endable"
import { EndableWrapper } from "../../core/async/EndableWrapper"
import { MaybeLaterMaybe } from "../../core/async/Later"
import { onError } from "../../core/error/OnError"
import { PosixFile } from "../../core/fs/PosixFile"
import { syncReport } from "../../core/fs/SyncReport"
import { lazy } from "../../core/Lazy"
import { blank } from "../../fe/Blank"
import { Latch } from "../../fe/Latch"
import { map } from "../../fe/Maybe"
import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { toA } from "../../fe/toA"
import { FileProcessor } from "../sync-file/ImportResult"
import { DirectorySync } from "./DirectorySync"
import { FileSync } from "./FileSync"
import { Sync } from "./Sync"
import { posixFileToUriPath } from "./UriPath"

export async function syncForPath(
  nativePath: string,
  fileProcessor: FileProcessor
): PromiseMaybe<Sync> {
  if (blank(nativePath)) return

  const root = PosixFile.for(nativePath)

  if (await root.isDirectory()) {
    const up = await posixFileToUriPath(root)
    if (up == null) {
      syncReport().onProgress({
        path: nativePath,
        from: "syncForPath()",
        state: "failed",
        details: "Cannot import: URI path could not be built for that path."
      })
      await syncReport().close()
      return
    } else {
      return DirectorySync.for({
        root,
        rootUri: up.uri,
        fileProcessor
      })
    }
  } else {
    return new FileSync(root, fileProcessor(nativePath))
  }
}

/**
 * Expose multiple Sync instances as a Sync
 */
export class Syncs extends EndableWrapper implements Sync {
  static forPaths(paths: string[], fileProcessor: FileProcessor): Syncs {
    return new Syncs(
      "import paths fs sync",
      toA(paths).map(path => () => syncForPath(path, fileProcessor))
    )
  }

  private currentSync: Maybe<Sync>
  private readonly _done = new Latch()
  constructor(
    name: string,
    private readonly laters: MaybeLaterMaybe<Sync>[]
  ) {
    super(`Syncs(${name})`, () => end(this.currentSync), EndableRanks.first)
    void this.run()
  }

  // only run once:
  private run = lazy(async () => {
    try {
      for (const later of this.laters) {
        const next = await later()
        if (next == null) continue
        this.currentSync = next
        await next.donePromise()
      }
      void this._done.resolve()
    } catch (err: any) {
      onError("Syncs.run() failed", err)
      void this._done.reject(err)
    }
  })

  isDone(): boolean {
    return !this._done.isPending()
  }

  donePromise(): Promise<void> {
    return this._done.promise
  }

  async progress() {
    return map(this.currentSync, ea => ea.progress())
  }
}
