import { parseJSON } from "../../core/JSON"
import { lazy } from "../../core/Lazy"
import { mkLogger } from "../../core/Logger"
import { forceContextOrSetting } from "../../core/settings/ForceContext"
import { toGt0 } from "../../fe/Number"
import { WorkQueue } from "../WorkQueue"
import { Asset } from "../model/Asset"
import { QueueItem } from "../stats/QueueItem"
import { assetPostUpsertTasks_ } from "../sync-file/AssetPostUpsertTasks"

export const AssetPostUpsertQueueName = "AssetPostUpsertTasks"

const logger = lazy(() => mkLogger("sync.AssetPostUpsertQueue"))

export function mkAssetPostUpsertQueue() {
  return WorkQueue.mk({
    queueNames: [AssetPostUpsertQueueName],
    concurrent: true,
    async processItem(qi: QueueItem) {
      const obj = parseJSON(qi.contents)
      const assetId = toGt0(obj?.assetId) ?? toGt0(qi.contents)
      if (assetId == null) {
        logger().error("invalid assetId", { qi })
        return
      }
      const asset = Asset.ops().findById(assetId)
      if (asset == null) {
        logger().error("asset not found", { assetId })
        return
      }
      return assetPostUpsertTasks_(
        asset,
        forceContextOrSetting({ skipAssetTagging: false, skipPreviews: false })
      )
    }
  })
}
