import { Knex } from "knex"
import { lazy } from "../../core/Lazy"
import { EndableRanks, ending } from "../../core/async/Endable"
import { EndableWrapper } from "../../core/async/EndableWrapper"
import { thenOrElse } from "../../core/async/Promise"
import { ETA } from "../../core/math/ETA"
import { max } from "../../core/math/Vector"
import { isNotEmpty } from "../../fe/Array"
import { mapNotBlankOr } from "../../fe/Blank"
import { Latch } from "../../fe/Latch"
import { Maybe } from "../../fe/MaybeTypes"
import { groupBy } from "../../fe/MultiMap"
import { clamp } from "../../fe/Number"
import { fmt } from "../../fe/Units"
import { RebuildingURI } from "../../fe/api/ProgressState"
import { TagRoots } from "../../fe/api/Tag"
import { addFileUriTagsToAsset } from "../curators/FilePathTagger"
import { Asset } from "../model/Asset"
import { AssetFile } from "../model/AssetFile"
import { Operation } from "../model/Operation"
import { Progress } from "../model/Progress"
import { Sync } from "./Sync"

export class DirectoryTaggerOperation extends EndableWrapper implements Sync {
  readonly start = Date.now()
  private readonly _eta = new ETA()
  readonly _done = new Latch()
  private doneCount: Maybe<number>
  private todoCount: Maybe<number>

  constructor(readonly priorProgress?: Progress) {
    super("TagAssetsWithFiles", () => null, EndableRanks.first)
    void this._done.observeQuietly(
      Operation.applyOnce(
        { name: "applyNewTagger", value: TagRoots.fs, version: 1 },
        () => this._run()
      )
    )
  }

  isDone() {
    return !this._done.isPending()
  }

  donePromise() {
    return this._done.promise
  }

  readonly _progress = lazy(
    () =>
      thenOrElse(this.priorProgress, () =>
        Progress.insertNew({ uri: RebuildingURI, volume: "" })
      )
    // NO TTL! This needs to be the same instance for the same process.
  )

  async progress() {
    if (this.isDone()) {
      return (await this._progress()).assignFromPojo({
        state: "done",
        hed: "Finished rebuilding your library ",
        dek: [],
        completePct: 100,
        incompletePct: 0,
        scanningPct: 0
      })
    }
    const eta = this.eta()
    if (eta == null || this.doneCount == null || this.todoCount == null) return

    const p = await this._progress()

    const complete = this.doneCount / (this.todoCount + this.doneCount)
    const completePct = clamp(0, 100, Math.round(100 * complete))
    const est = this._eta.fmtEstimate()
    const hed =
      `Adding ${TagRoots.fs} tags to your library` +
      mapNotBlankOr(est, ea => ", " + ea, "")
    return p.assignFromPojo({
      state: "processing",
      hed,
      dek: [
        `Tagged ${fmt(this.doneCount)} URIs, ${fmt(this.todoCount)} remain.`
      ],
      completePct,
      incompletePct: 100 - completePct,
      scanningPct: 0 // everything's been scanned, so 0.
    })
  }

  eta() {
    if (
      this.todoCount == null ||
      this.doneCount == null ||
      this.todoCount === 0
    )
      return undefined
    const elapsedMs = Date.now() - this.start
    const msPerItem = elapsedMs / this.doneCount
    const msRemaining = msPerItem * this.todoCount
    this._eta.push(msRemaining)
    return this._eta.avg()
  }

  private async _run() {
    this.todoCount = AssetFile.dbl.pluckFirstf<number>(q =>
      onlyShownAssetFiles(q.countDistinct("AssetFile.id"))
    )
    this.doneCount = 0
    return AssetFile.dbl.batched<{
      assetId: number
      assetFileId: number
      uri: string
    }>({
      onResults: arr => {
        for (const byAsset of groupBy(arr, ea => ea.assetId).values()) {
          if (ending()) return
          addFileUriTagsToAsset(
            byAsset[0].assetId,
            byAsset.map(ea => ea.uri)
          )
          this.doneCount! += byAsset.length
          this.todoCount! -= byAsset.length
        }
      },
      qb: (qb, priors) => {
        qb = onlyShownAssetFiles(
          qb.select({
            assetId: "AssetFile.assetId",
            assetFileId: "AssetFile.id",
            uri: "AssetFile.uri"
          })
        )
        if (isNotEmpty(priors)) {
          qb = qb.andWhere(
            "AssetFile.id",
            ">",
            max(priors.map(ea => ea.assetFileId))!
          )
        }
        return qb
      }
    })
  }
}

function onlyShownAssetFiles(qb: Knex.QueryBuilder) {
  return Asset.shownUnhidden(
    qb.from("AssetFile").join("Asset", "Asset.id", "AssetFile.assetId")
  )
}
