import { SyncFileState, SyncFileStates } from "../../core/fs/SyncReport"
import { notBlank } from "../../fe/Blank"
import { gt0 } from "../../fe/Number"
import { Asset } from "../model/Asset"
import { AssetFile } from "../model/AssetFile"

interface Result {
  path: string
  uri?: string
  uris?: string[]
  state: SyncFileState
  details?: string
}

export interface ImportRejectedResult extends Result {
  state: typeof SyncFileStates.rejected
}

export interface ImportSuccessResult extends Result {
  assetId: number
  assetFileId: number
  assetFile: AssetFile
  asset: Asset
}

export interface ImportErrorResult extends Result {
  error: string
}

export type ImportResult =
  | ImportRejectedResult
  | ImportSuccessResult
  | ImportErrorResult

export function isImportResult(obj: any): obj is ImportResult {
  return obj != null && notBlank(obj.path) && SyncFileStates.has(obj.state)
}

export function isErrorResult(obj: any): obj is ImportErrorResult {
  return notBlank(obj.error) || obj.state === SyncFileStates.failed
}

export function isSuccessResult(obj: any): obj is ImportSuccessResult {
  return (
    obj != null &&
    !isErrorResult(obj) &&
    gt0(obj.assetId) &&
    gt0(obj.assetFileId) &&
    isImportResult(obj) // < they have to be in this order
  )
}

export interface FileProcessor {
  (nativePath: string, retries?: number): Promise<ImportResult>
}
