import { Rate } from "batch-cluster"
import events from "events"
import { lazy } from "../core/Lazy"
import { isTest } from "../core/NodeEnv"
import { ShortCommandTimeoutMs } from "../core/ShortCommandTimeoutMs"
import { sortIgnoreCase } from "../core/String"
import { TTLArray } from "../core/TTLArray"
import { EndableRanks } from "../core/async/Endable"
import { EndableInterval } from "../core/async/EndableInterval"
import { Promises } from "../core/async/Promises"
import { InternalError } from "../core/error/InternalError"
import { onError } from "../core/error/OnError"
import { ee } from "../core/event/EventEmitter"
import { TypedEventEmitter } from "../core/event/TypedEventEmitter"
import { Average } from "../core/math/Average"
import { isTooBusy } from "../core/work/CpuUsage"
import { doNotRun, whyDoNotRun } from "../core/work/DoNotRun"
import { isPaused } from "../core/work/IsPaused"
import { maxConcurrentImports } from "../core/work/MaxCpus"
import { compactBlanks, isEmpty } from "../fe/Array"
import { blank } from "../fe/Blank"
import { minuteMs, secondMs } from "../fe/Date"
import { stringify } from "../fe/JSON"
import { Latch } from "../fe/Latch"
import { Maybe } from "../fe/MaybeTypes"
import { gt0, round } from "../fe/Number"
import { SyncOrAsync } from "../fe/SyncOrAsync"
import { throttle } from "../fe/Throttle"
import { Queue, WorkItem } from "./stats/Queue"
import { QueueItem } from "./stats/QueueItem"

export interface WorkQueueEvents {
  processed: (qi: QueueItem, elapsedMs: number) => void
  drain: () => void
}

/**
 * Class that feeds work from a Queue to SyncFile jobs.
 *
 * Used for importing new files and updating assets and asset files.
 */
export class WorkQueue extends EndableInterval {
  readonly processRate = new Rate()
  readonly processMs = new Average()

  readonly ee = new events.EventEmitter() as TypedEventEmitter<WorkQueueEvents>

  // Support non-async isDone():
  #isDone = false

  // The Deferred payloads are used to track current QueueItems:
  readonly p: Promises

  readonly recentlyProcessed = new TTLArray<QueueItem>(minuteMs)

  static mk(args: {
    queueNames: string[]
    processItem: (qi: QueueItem) => SyncOrAsync<any>
    concurrent?: boolean
    endWhenDone?: boolean
  }) {
    const queueNames = sortIgnoreCase(compactBlanks(args.queueNames))
    if (isEmpty(queueNames))
      throw new Error("WorkQueue.mk: internal error: blank queueNames")
    return new WorkQueue(
      queueNames,
      args.processItem,
      args.concurrent,
      args.endWhenDone
    )
  }

  /**
   * @param _itemIsReady `true` if the given item can run
   */
  private constructor(
    readonly queueNames: string[],
    private readonly _processItem: (qi: QueueItem) => SyncOrAsync<any>,
    concurrent: boolean = true,
    endWhenDone?: boolean
  ) {
    const name = `WorkQueue(${queueNames.join(",")})`
    super({
      name,
      callback: () => this.#runChunkLater(),
      intervalMs: isTest ? 250 : minuteMs,
      onEnd: () => this.#onEnd(),
      rank: EndableRanks.first,
      endTimeoutMs: ShortCommandTimeoutMs
    })
    this.p = new Promises(name, concurrent ? maxConcurrentImports : () => 1)
    this.p.ee.on("vacancy", () => this.#runChunkLater())
    this.p.ee.on("drain", () => this.#checkIfDone())
    if (endWhenDone === true) {
      void this.awaitDrain().then(() => this.end())
    }
    ee().on("resume", this.#onResume)
  }

  #onEnd(): any {
    ee().off("resume", this.#onResume)
    return this.awaitDrain()
  }

  readonly on = this.ee.on.bind(this.ee)

  readonly queues = lazy(() =>
    Queue.ops().upsert(compactBlanks(this.queueNames).map(name => ({ name })))
  )

  readonly queueIds = lazy(() => this.queues().map(ea => ea.id!))

  async awaitDrain() {
    if (this.isDone()) return
    const l = new Latch()
    this.ee.once("drain", () => l.resolve())
    void this.#runChunk()
    await l.promise
  }

  isDone() {
    return this.ended || this.#isDone
  }

  #checkIfDone() {
    if (this.ended) return true
    const priorDone = this.#isDone
    this.#isDone =
      0 === this.currentWorkCount() &&
      0 === this.queueItemCount() &&
      0 === this.queueItemCount.refresh()

    if (!priorDone && this.#isDone) {
      this.#logState.refresh()
      this.ee.emit("drain")
    }
    return this.#isDone
  }

  isRunnable() {
    return !this.isDone() && !this.p.isFull()
  }

  #clearCaches() {
    this.queueItemCount.unset()
  }

  findQueue_(queueName?: string): Queue {
    const arr = this.queues()
    if (blank(queueName)) return arr[0]
    const q = arr.find(ea => ea.name === queueName)
    if (q == null) {
      throw new InternalError(
        "WorkQueue: unknown queue name " + stringify(queueName)
      )
    } else {
      return q
    }
  }

  enqueueWork(items: WorkItem[], queueName?: string): QueueItem[] {
    const queue = this.findQueue_(queueName)
    this.logger.debug("enqueueWork()", { items, queue })
    const upserted = queue.upsertWorkItems(items)
    this.#isDone = false
    this.#clearCaches()
    this.#runChunkLater()
    return upserted
  }

  postProcessItem(item: QueueItem) {
    // make sure the sync queue doesn't have this content scheduled:
    item.delete()
    this.logger.info("postProcessItem()", { item })
    this.#clearCaches()
    if (!this.#checkIfDone()) this.#runChunkLater()
  }

  get recentFileProgress(): Maybe<string[]> {
    return this.recentlyProcessed.map(ea => ea.contents)
  }

  processedCount() {
    return this.processRate.eventCount
  }

  currentWorkCount() {
    return this.p.pendingWithName(this.name).length
  }

  /**
   * AKA the count of "not started" work items
   */
  pendingWorkCount() {
    return this.queueItemCount() - this.currentWorkCount()
  }

  /**
   * @return the total number of items in the queue (including those items
   * currently being processed)
   */
  readonly queueItemCount = lazy(() => {
    const queueIds = this.queueIds()
    return QueueItem.dbl.pluckFirstf<number>(qb =>
      qb.countDistinct("id").whereIn("queueId", queueIds)
    )
  })

  percents() {
    const done = this.processedCount()
    const todo = this.queueItemCount()
    // this.eta.push(mapGt0(this.processRate.msPerEvent, ea => ea * todo))
    const completePct = round(100 * (done / (todo + done)))
    return { done, todo, completePct, incompletePct: 100 - completePct }
  }

  currentQueueItems(): QueueItem[] {
    return this.p.payloadsWithName(this.name)
  }

  currentQueueContents() {
    return this.currentQueueItems().map(ea => ea.contents)
  }

  private currentQueueItemIds() {
    return this.currentQueueItems().map(ea => ea.id)
  }

  etaMs() {
    const p84 = this.processMs.n <= 6 ? undefined : this.processMs.p84
    return p84 == null ? undefined : this.queueItemCount() * p84
  }

  state() {
    return {
      ...this.p.stats(),
      isDone: this.#checkIfDone(),
      freeSlots: this.p.freeSlots(),
      currentWork: this.currentQueueContents(),
      pendingWork: this.queueItemCount(),
      next10: this.#next(10).map(ea => ea.contents),
      recentlyProcessedLast10: this.recentlyProcessed
        .slice(-10)
        .map(ea => ea.contents),
      isRunnable: this.isRunnable(),
      ...this.percents(),
      etaMs: this.etaMs()
    }
  }

  // lazy to prevent too many status log entries
  readonly #logState = lazy(
    () => {
      this.logger.debug("status", this.state())
    },
    (isTest ? 1 : 30) * secondMs
  )

  #next(limit?: number): QueueItem[] {
    return QueueItem.ops().allf(q => {
      q = q
        .distinct()
        .whereIn("queueId", this.queueIds())
        .andWhere(q1 => q1.whereNotIn("id", this.currentQueueItemIds()))
        .orderBy("id")
      if (gt0(limit)) {
        q = q.limit(limit)
      }
      return q
    })
  }

  // This starts runChunk later if it's not already been enqueued.
  #runChunkLater(delayMs = 10) {
    setTimeout(() => this.#runChunk(), delayMs)
  }

  readonly #onResume = () => this.#runChunkLater()

  readonly #logNoOp = lazy(
    () =>
      this.logger.info("runChunk(): no-op", {
        whyDoNotRun: whyDoNotRun(this),
        isFull: this.p.isFull(),
        pendingWorkCount: this.pendingWorkCount(),
        queueItemCount: this.queueItemCount(),
        freeSlots: this.p.freeSlots()
      }),
    (isTest ? 1 : 30) * secondMs
  )

  doNotStartMoreWork() {
    return (
      isPaused() ||
      this.p.isFull() ||
      isTooBusy() ||
      doNotRun(this) ||
      this.pendingWorkCount() === 0
    )
  }

  // WorkRunner will call this to make sure we pop work off.
  readonly #runChunk = throttle({
    minCallDelayMs: 10,
    f: () => {
      // AVOID INFINITE LOOPS: MAKE SURE NONE OF THIS CODE WRAPS AROUND AND
      // DIRECTLY CALLS #runChunk(): (runChunkLater is OK)

      if (isPaused()) return // do not check if done, do not pass go

      if (isTooBusy() && !this.p.isFull() && this.pendingWorkCount() > 0) {
        // special case: we have work to do, but something else is making us
        // busy. Check system load a couple seconds:
        this.#runChunkLater(7 * secondMs)
        this.#logNoOp()
      } else if (this.doNotStartMoreWork()) {
        this.#logNoOp()
      } else {
        this.#logState()
        const next = this.#next(this.p.freeSlots())
        this.logger.debug("runChunk():", { next })
        // Try to fetch the next N items from the queue
        for (const payload of next) {
          void this.p.enqueue({
            name: this.name,
            payload,
            l: () => this.#processItem(payload)
          })
        }
      }
      this.#checkIfDone()
    }
  })

  async #processItem(item: QueueItem) {
    try {
      const start = Date.now()
      await this._processItem(item)
      const elapsedMs = Date.now() - start
      this.recentlyProcessed.push(item)
      this.processRate.onEvent()
      this.processMs.push(elapsedMs)
      this.ee.emit("processed", item, elapsedMs)
    } catch (error) {
      onError(this.name + " failed to process item " + item.contents, error)
    } finally {
      this.postProcessItem(item)
    }
  }
}
