import { lazy } from "../../core/Lazy"
import { mkLogger } from "../../core/Logger"
import { thenMap } from "../../core/async/Promise"
import { PosixFile } from "../../core/fs/PosixFile"
import { Settings } from "../../core/settings/Settings"
import { CapturedAt } from "../../core/tags/CapturedAt"
import {
  joinTagPath,
  leafIsExcluded,
  omitAncestorTags
} from "../../core/tags/TagPaths"
import { uniqBy } from "../../fe/Array"
import { map } from "../../fe/Maybe"
import { PromiseMaybe } from "../../fe/MaybeTypes"
import { thenCollect } from "../../fe/Promise"
import { TagPath } from "../../fe/api/Tag"
import { Asset } from "../model/Asset"
import { tagFilesWithAlbums } from "./AlbumTagger"
import { cameraTagFile } from "./CameraTagger"
import { dateTagFile } from "./DateTagger"
import { tagAssetPaths } from "./FilePathTagger"
import { keywordTagFiles } from "./KeywordTagger"
import { lensTagFile } from "./LensTagger"
import { normalizeTagRoot, tagDeltas } from "./Taggers"
import { typeTagFiles } from "./TypeTagger"
import { whoTagFiles } from "./WhoTagger"

export interface AssetTaggingResult {
  add: TagPath[]
  remove: TagPath[]
}

const logger = lazy(() => mkLogger("curators.AssetTagger"))

/**
 * should only be invoked from AssetPostUpsertTasks and specs
 */
export async function tagAndUpsertAsset_(
  asset: Asset
): PromiseMaybe<AssetTaggingResult> {
  const assetFiles = (await asset.getExistingAssetFiles())!

  logger().info("tagAndUpsertAsset()", {
    asset,
    existing_uris: assetFiles.map(ea => ea.uri)
  })

  const existingPrimaryAssetFile = assetFiles.find(ea => ea.shown)
  const primaryVariant = await existingPrimaryAssetFile?.posixFile_()

  if (existingPrimaryAssetFile == null || primaryVariant == null) {
    logger().throw("tagAndUpsertAsset(): no existing or primary asset file", {
      asset,
      existingPrimaryAssetFile,
      primaryVariant,
      doNotSend: true,
      fatal: false
    })
    return
  }

  const priorPrimaryAF = asset.getShown()

  if (priorPrimaryAF?.id !== existingPrimaryAssetFile.id) {
    logger().warn(
      "Changing primary asset we're using a different variant for tagging.",
      {
        newPrimary: existingPrimaryAssetFile.uri,
        priorPrimary: priorPrimaryAF?.uri
      }
    )
    asset.setShown_(existingPrimaryAssetFile)
  }

  const files = await thenCollect(assetFiles, ea => ea.posixFile_())
  const uris = asset.getAssetFiles().map(ea => ea.uri)

  const priorTagPaths = asset.getTagPaths()
  const capturedAts = await asset.getCapturedAts()

  const tagResult = await tagAsset({
    primaryVariation: primaryVariant,
    files,
    priorTagPaths,
    capturedAts,
    uris
  })
  logger().info("tagAndUpsertAsset", tagResult)

  const excludedTag = tagResult.add.find(ea =>
    leafIsExcluded(ea, Settings.keywordBlocklist.values)
  )
  if (excludedTag != null) {
    if (asset.shown === true) {
      logger().info("this asset has an excluded tag, marking excluded", {
        excludedTag
      })
      asset.excludedAt = Date.now()
    } else {
      logger().info(
        "this asset has an excluded tag, and it wasn't shown yet, so we're removing it.",
        { excludedTag }
      )
      await Asset.remove({
        assetId: asset.id!,
        blocklistShas: false,
        unlinkAssetFiles: false
      })
      return logger().throw(
        "tagAndUpsertAsset(): asset had excluded tag. Removed.",
        {
          excludedTag,
          uris,
          files: files.map(ea => ea.nativePath)
        }
      )
    }
  }

  const result = map(tagResult, async ea => {
    Asset.addTags(asset.id!, ea.add)
    Asset.removeTags(asset.id!, ea.remove)
    asset.tags = undefined // < clear tags cache
    return ea
  })

  await asset.updateFromFiles()
  return result
}

export async function tagAsset({
  primaryVariation,
  files,
  priorTagPaths,
  capturedAts,
  uris
}: {
  primaryVariation: PosixFile
  files: PosixFile[]
  priorTagPaths: string[][]
  capturedAts: CapturedAt[]
  uris: string[]
}): Promise<AssetTaggingResult> {
  const tagPaths: TagPath[] = []

  if (files.some(ea => ea.nativePath === primaryVariation.nativePath)) {
    // good caller, GOOD
  } else {
    files.unshift(primaryVariation)
  }

  //
  // NOTE: IF YOU CHANGE ANY TAGGERS, REMEMBER TO UPDATE AssetVersion!
  //

  if (Settings.tagCamera.valueOrDefault) {
    await thenMap(cameraTagFile(primaryVariation), ea => {
      logger().debug("Camera tag for " + primaryVariation + ":", ea)
      tagPaths.push(ea)
    })
  }

  if (Settings.tagLens.valueOrDefault) {
    await thenMap(lensTagFile(primaryVariation), ea => {
      logger().debug("Lens tag for " + primaryVariation + ":", ea)
      tagPaths.push(ea)
    })
  }

  await thenMap(dateTagFile(primaryVariation, capturedAts), ea => {
    logger().debug("Date tag for " + primaryVariation + ":", ea)
    tagPaths.push(ea)
  })

  const whoTags = (await whoTagFiles(files)) ?? []
  logger().debug("whoTagFiles for " + primaryVariation + ":", whoTags)
  tagPaths.push(...whoTags)

  await thenMap(keywordTagFiles(files, whoTags), arr => {
    logger().debug("Keyword tags for " + primaryVariation + ":", arr)
    tagPaths.push(...arr)
  })

  if (Settings.tagFileType.valueOrDefault) {
    await thenMap(typeTagFiles(files), arr => {
      logger().debug("MIME type tags for " + primaryVariation + ":", arr)
      tagPaths.push(...arr)
    })
  }

  await thenMap(tagAssetPaths(uris), arr => {
    logger().debug("file paths for uris", { uris, arr })
    tagPaths.push(...arr)
  })

  await thenMap(tagFilesWithAlbums(files), arr => {
    logger().debug("Album tags for " + primaryVariation + ":", arr)
    tagPaths.push(...arr)
  })

  const after = omitAncestorTags(
    uniqBy(tagPaths.map(normalizeTagRoot), joinTagPath)
  )

  const result = await tagDeltas(priorTagPaths, after)
  logger().info("tagAsset(" + primaryVariation + ")", {
    priorTagPaths,
    after,
    result
  })

  return result
}
