import { Knex } from "knex"
import { mkLogger } from "../../core/Logger"
import { rateLimited } from "../../core/RateLimited"
import { errorToS } from "../../core/error/Error"
import { onError } from "../../core/error/OnError"
import { toWrappedError } from "../../core/error/WrappedError"
import { count } from "../../fe/Array"
import { notBlank } from "../../fe/Blank"
import { stringify } from "../../fe/JSON"
import { clamp, gt0 } from "../../fe/Number"
import { parseJSON } from "../../fe/ParseMaybe"
import { plur } from "../../fe/Units"
import { EmptyDeletedURI, EmptyRemovedURI } from "../../fe/api/ProgressState"
import { Asset, RemovableColumn } from "../model/Asset"
import { AssetFile } from "../model/AssetFile"
import { Operation } from "../model/Operation"
import { Progress } from "../model/Progress"

const logger = mkLogger("ops.ApplyExcluded")

export async function maybeApplyExcluded() {
  return Operation.applyIfPending({ name: "applyExcluded" }, op =>
    applyRemovable(op, false)
  )
}
export async function maybeApplyDeleted() {
  return Operation.applyIfPending({ name: "applyDeleted" }, op =>
    applyRemovable(op, true)
  )
}

async function applyRemovable(op: Operation, unlinkAssetFiles: boolean) {
  const progress = Progress.insertNew(
    unlinkAssetFiles
      ? {
          uri: EmptyDeletedURI,
          state: "processing",
          volume: "",
          scanningPct: 0,
          hed: "Removing deleted assets from library and disk"
        }
      : {
          uri: EmptyRemovedURI,
          state: "processing",
          volume: "",
          scanningPct: 0,
          hed: "Removing excluded assets from library"
        }
  )
  // const unlinkAssetFiles = op.name === "applyDeleted"
  const { assetCount, assetFileCount } = parseJSON(op.value) as any

  if (!gt0(assetCount) || !gt0(assetFileCount)) {
    logger.throw("Refusing to " + op.name + ": invalid model counts", {
      op
    })
  }

  const col: RemovableColumn = unlinkAssetFiles ? "deletedAt" : "excludedAt"
  const qb = (q: Knex.QueryBuilder) =>
    q.where("Asset.shown", 0).andWhere("Asset." + col, op.createdAt!)

  const assetIds = Asset.dbl.pluckAllf<number>(q => qb(q.select("Asset.id")))
  if (assetIds.length > assetCount) {
    logger.throw("Refusing to " + op.name + ": too many assets!", {
      op,
      assetCount: assetIds.length
    })
  }

  if (unlinkAssetFiles) {
    const dbAssetFileCount = AssetFile.dbl.pluckFirstf<number>(q =>
      qb(
        q
          .countDistinct("AssetFile.id")
          .join("Asset", "Asset.id", "AssetFile.assetId")
      )
    )

    if (dbAssetFileCount > assetFileCount) {
      logger.throw("Refusing to " + op.name + ": too many impacted files!", {
        op,
        dbAssetFileCount
      })
    }
  }

  const deletedMeta: {
    nativePath: string
    unlinked: boolean
  }[] = []

  function mkDek(index: number) {
    const total = assetIds.length
    const dek = [
      `Removed ${plur(total, "asset")} from your library.` +
        (index >= total
          ? ""
          : ` ${plur(total - index, "asset")} need to be removed.`)
    ]
    if (unlinkAssetFiles) {
      const deletedFiles = count(deletedMeta, ea => ea.unlinked)
      dek.push(plur(deletedFiles, "file") + " deleted from disk.")
      const notDeletedFiles = count(deletedMeta, ea => !ea.unlinked)
      if (notDeletedFiles > 0) {
        dek.push(
          plur(deletedFiles, "file") +
            " could not be deleted from disk. Check the logs for details."
        )
      }
    }
    return dek
  }

  const maybeUpdateProgress = rateLimited({
    name: "applyRemovable",
    f: async (index: number) => {
      if (index < 0) return
      progress.completePct = clamp(0, 100, Math.round(index / assetIds.length))
      progress.incompletePct = clamp(0, 100, 100 - progress.completePct)
      progress.dek = mkDek(index)
      progress.upsert()
    },
    minCallDelayMs: 500
  })

  logger.info("applying...", { op, assetIds })
  // We run this serially, as the database updates are going to be applied
  // serially anyway:
  try {
    for (const assetId of assetIds) {
      const result = await Asset.remove({
        assetId,
        blocklistShas: true,
        unlinkAssetFiles
      })
      deletedMeta.push(
        ...(result?.deletedMeta.filter(ea => notBlank(ea.nativePath)) as any)
      )
      await maybeUpdateProgress(assetIds.indexOf(assetId))
    }
    op.upsert({ completedAt: Date.now() })
    progress.hed = unlinkAssetFiles
      ? "Emptied trash."
      : "Excluded assets removed."
    progress.dek = mkDek(assetIds.length)
    progress.state = "done"
    progress.completedAt = Date.now()
    progress.upsert()
    logger.info("Complete!", { op })
  } catch (cause) {
    const error = toWrappedError("Could not apply " + stringify(op), { cause })
    onError(error)
    progress.upsert({
      hed: "Failed: " + errorToS(error),
      state: "done",
      completedAt: Date.now()
    })
  }
}
