import { ending } from "../../core/async/Endable"
import { EndableInterval } from "../../core/async/EndableInterval"
import { broadcastEvent_ } from "../../core/event/BroadcastEvent"
import { isPaused } from "../../core/work/IsPaused"
import { sortBy } from "../../fe/ArraySort"
import { eql } from "../../fe/Eql"
import { MaybeSyncOrAsync } from "../../fe/SyncOrAsync"
import { Progress, ProgressPojo, ProgressRateMs } from "../model/Progress"
import { runTagMaintenance } from "../tag/TagSql"
import { Sync } from "./Sync"

export class ProgressUpdater extends EndableInterval {
  private prior: ProgressPojo[] = []
  constructor(readonly sync: () => MaybeSyncOrAsync<Sync>) {
    super({
      name: "ProgressUpdater",
      callback: () => this.onInterval(),
      intervalMs: ProgressRateMs()
    })
  }

  async onInterval() {
    if (ending() || isPaused()) {
      this.prior = []
      return
    }
    const s = await this.sync()
    if (s == null) {
      this.prior = []
      return
    }
    const arr = await Progress.saveSyncState(s)
    const current = sortBy(arr, ea => ea.uri).map(ea => ea.toSyncState())
    if (!eql(this.prior, current)) {
      void broadcastEvent_("updateProgress")
      await runTagMaintenance()
      this.prior = current
    }
  }
}
