import { createReadStream } from "fs"
import { NODE_STREAM_INPUT, parse, ParseConfig, ParseResult } from "papaparse"
import { Transform } from "stream"
import { pipeline } from "stream/promises"
import { notBlank } from "../../fe/Blank"
import { sortIgnoreCase } from "../String"
import { Newline } from "./CRLF"
import { readLines_ } from "./ReadLines"
import { SimpleFileOrString, toNativePath_ } from "./SimpleFile"
import {
  FileDoneStates,
  SyncFileStates,
  SyncReportRow,
  SyncReportState
} from "./SyncReport"

const DefaultOptions = {
  header: true,
  skipEmptyLines: "greedy"
} as ParseConfig

export async function readSyncReport_(
  nativePath: string
): Promise<ParseResult<SyncReportRow>> {
  const content = (await readLines_(nativePath)).join(Newline)
  return parse<SyncReportRow>(content, DefaultOptions)
}

class EnqueueStream extends Transform {
  constructor(
    readonly enqueued = new Set<string>(),
    readonly settled = new Set<string>(),
    readonly accept: (nativePath: string) => boolean,
    readonly fileDoneStates: Set<SyncReportState>
  ) {
    super({ objectMode: true })
  }

  _write(chunk: any, _encoding: BufferEncoding, next: () => void) {
    if (
      chunk != null &&
      SyncFileStates.includes(chunk.state) &&
      notBlank(chunk.path) &&
      this.accept(chunk.path)
    ) {
      const row = chunk as SyncReportRow
      if (row.state === "enqueued") {
        this.enqueued.add(row.path)
      } else if (this.fileDoneStates.has(row.state as any)) {
        this.settled.add(row.path)
      }
    }
    next()
  }
}

export async function unprocessedPaths_({
  syncReportJsons,
  acceptPath = () => true,
  fileDoneStates = FileDoneStates
}: {
  syncReportJsons: SimpleFileOrString[]
  acceptPath?: (nativePath: string) => boolean
  fileDoneStates?: Set<SyncReportState>
}): Promise<string[]> {
  const enqueued = new Set<string>()
  const settled = new Set<string>()
  for (const json of syncReportJsons) {
    await pipeline(
      createReadStream(toNativePath_(json)),
      parse(NODE_STREAM_INPUT, DefaultOptions),
      new EnqueueStream(enqueued, settled, acceptPath, fileDoneStates)
    )
  }
  const result = [...enqueued].filter(ea => !settled.has(ea))
  return sortIgnoreCase(result)
}
