import { lazy } from "../../core/Lazy"
import { mkLogger } from "../../core/Logger"
import { valpath } from "../../core/Object"
import { PosixFile } from "../../core/fs/PosixFile"
import { Settings } from "../../core/settings/Settings"
import { readRawTags } from "../../core/tags/ExifTool"
import { readJsonSidecar } from "../../core/tags/JsonSidecar"
import { renderNameTag } from "../../core/tags/Names"
import { tagRefToS } from "../../core/tags/TagPaths"
import { isNotEmpty, uniq } from "../../fe/Array"
import { blank } from "../../fe/Blank"
import { flatten } from "../../fe/Flatten"
import { PromiseMaybe } from "../../fe/MaybeTypes"
import { values } from "../../fe/Object"
import { TagPath, TagRoots } from "../../fe/api/Tag"
import { toA } from "../../fe/toA"

const logger = lazy(() => mkLogger("curators.WhoTagger"))

export async function whoTagFiles(files: PosixFile[]): PromiseMaybe<TagPath[]> {
  // Don't bother to read tags, just read JSON payloads:
  const names: string[] = []

  if (Settings.tagJsonFaces.valueOrDefault) {
    const jsonSidecars: PosixFile[] = []
    for (const f of files) {
      jsonSidecars.push(...toA(await f.jsonSidecars()))
    }
    for (const ea of jsonSidecars) {
      const tags = await readJsonSidecar(ea)
      const arr = tags?.peopleNames
      if (isNotEmpty(arr)) {
        names.push(...arr)
      }
    }
  }

  if (Settings.tagFaceRegions.valueOrDefault) {
    for (const f of files) {
      const tags = await readRawTags(f, true) // include sidecars
      // Expect something like
      // {
      //   "Area": {
      //     "H": 0.0653789,
      //     "Unit": "normalized",
      //     "W": 0.0318743,
      //     "X": 0.214086,
      //     "Y": 0.178058
      //   },
      //   "Name": "First Last",
      //   "Type": "Face"
      // }

      // or:
      // "Regions": {
      //   "AppliedToDimensions": {
      //     "H": 1704,
      //     "Unit": "pixel",
      //     "W": 2272
      //   },
      //   "RegionList": [{
      //     "ALGArea": {
      //       "H": 0.176056,
      //       "W": 0.132042,
      //       "X": 0.363116,
      //       "Y": 0.288146
      //     },
      //     "DLYArea": {
      //       "H": 0.181338,
      //       "W": 0.112236,
      //       "X": 0.363116,
      //       "Y": 0.290786
      //     },
      //     "Name": "Joe Bloggs",
      //     "NameAssignType": "manual",
      //     "Type": "Face"

      if (tags != null) {
        for (const v of values(tags)) {
          const regionList = (v as any)?.RegionList
          if (Array.isArray(regionList)) {
            for (const region of regionList) {
              if (region["Type"] === "Face") {
                names.push(region["Name"])
              }
            }
          }
        }
      }
    }
  }

  if (isNotEmpty(Settings.whoTags.values)) {
    for (const f of files) {
      const tags = await readRawTags(f, true) // include sidecars
      for (const field of Settings.whoTags.values) {
        names.push(...toA(valpath(tags, field)))
      }
    }
  }

  const result = uniq(flatten(uniq(names).map(nameTag)))

  return logger().tap({
    msg: "whoTagFiles()",
    level: "info",
    result,
    meta: { names, files: files.map(ea => ea.nativePath) }
  })
}

const whoRootsLowerCased = uniq(
  [TagRoots.Who, ...Settings.rootTagWhoAliases.values].map(ea =>
    ea.toLowerCase()
  )
)

export function isWhoTag(t: TagPath): boolean {
  return whoRootsLowerCased.includes(tagRefToS(t[0]).toLowerCase())
}

export function nameTag(s: string | string[]) {
  if (blank(s)) return
  if (Array.isArray(s)) {
    if (isWhoTag(s)) {
      s.shift()
    }
    return s.length === 0
      ? undefined // < weird: just a "Who":
      : s.length === 1
      ? renderNameTag(s[0]) // < "Who/First Last"
      : [[TagRoots.Who, ...s]] // < "Who/Person/Name": retain the specified hierarchy
  } else {
    return renderNameTag(s)
  }
}
