import { InternalErrorFlag } from "../../core/error/ErrorFlags"
import { AssetPreviewInfo } from "../../core/img/AssetPreviews"
import { buildAssetPreviews_, Previews } from "../../core/img/Previews"
import { mkLogger } from "../../core/Logger"
import {
  ForceContext,
  forceContextOrSetting
} from "../../core/settings/ForceContext"
import { uriIsEquivalent } from "../../core/uri/UriNormalization"
import { isEmpty } from "../../fe/Array"
import { defer } from "../../fe/Defer"
import { eql } from "../../fe/Eql"
import { PromiseMaybe } from "../../fe/MaybeTypes"
import { gt0 } from "../../fe/Number"
import { pick } from "../../fe/Object"
import { toA } from "../../fe/toA"
import { Asset } from "../model/Asset"
import { AssetFile } from "../model/AssetFile"

const logger = defer(() => mkLogger("sync-file.UpdateAssetPreviews"))

export async function updateAssetPreviews_(
  args: {
    assetId?: number
    asset?: Asset
    assetFiles?: AssetFile[]
    ac?: AbortSignal
  } & Partial<ForceContext>
): PromiseMaybe<AssetPreviewInfo & { noop?: boolean }> {
  const assetId =
    args.assetId ??
    args.asset?.id ??
    toA(args.assetFiles).find(ea => gt0(ea.assetId))?.assetId
  const fc = forceContextOrSetting(args)
  const log = logger().addContext(".Asset:" + assetId)

  if (fc.skipPreviews) {
    log.warn("skipPreviews is true: no-op")
    return
  }

  if (!gt0(assetId)) {
    return log.throw("invalid assetId", { assetId })
  }

  const asset = args.asset ?? Asset.ops().findById(assetId)
  if (asset == null) {
    return log.throw("Asset:" + assetId + " not found")
  }

  log.throwIfAborted(args.ac)

  try {
    // assetPreviews assumes it can get native paths:
    const assetFiles = await asset.getExistingAssetFiles()
    if (isEmpty(assetFiles)) {
      log.warn("no existing assetFiles", { asset })
      return
    }

    const ap = Previews.instance()!.ap(assetId)
    const priorInfo = await ap.readInfo()

    log.throwIfAborted(args.ac)

    // We use buildAssetPreviews_ here instead of ap.build() because
    // buildAssetPreviews_ may be shimmed to run on a worker process:
    const newInfo: AssetPreviewInfo = await buildAssetPreviews_({
      ...fc,
      assetId,
      assetFiles
    })

    // Make sure the "shown" asset file variant is stored:
    const primaryAssetFile =
      assetFiles.find(ea => ea.id === newInfo?.assetFileId) ??
      assetFiles.find(ea => uriIsEquivalent(newInfo?.uri, ea.uri))

    log.info("buildAssetPreviews_()", { result: newInfo, primaryAssetFile })
    const primaryPosixFile = await primaryAssetFile?.posixFile_()

    if (
      primaryAssetFile == null ||
      primaryPosixFile == null ||
      true !== (await primaryPosixFile?.exists())
    ) {
      return log.throw(
        "buildAssetPreviews_() returned invalid assetFileId" +
          InternalErrorFlag,
        {
          newInfo,
          assetFiles: assetFiles.map(ea => pick(ea, "id", "uri")),
          primaryAssetFile,
          primaryPosixFile
        }
      )
    }

    const eqlsPrior = eql(priorInfo, newInfo)
    const noop = newInfo == null || eqlsPrior

    return { ...newInfo, noop }
  } catch (error) {
    log.warn("Failed to update previews. Un-showing asset " + assetId, {
      error
    })
    asset.markUnshownAndUpsert()
    throw error
  }
}
