import { partition } from "../../core/Array"
import { mkLogger } from "../../core/Logger"
import { AssetVersion } from "../../core/PhotoStructureVersions"
import { aggregateAssetFiles } from "../../core/asset-file/AssetFileAggregator"
import { isSimilarAssetFile } from "../../core/asset-file/AssetFileComparator"
import { sortAssetFiles } from "../../core/asset-file/AssetFileSorter"
import { mapAsync, partitionAsync } from "../../core/async/Promise"
import { UpdateAssetArgs } from "../../core/event/PhotoStructureEvents"
import { max, min } from "../../core/math/Vector"
import { forceContextOrSetting } from "../../core/settings/ForceContext"
import { Settings } from "../../core/settings/Settings"
import { compact, isEmpty, isNotEmpty, uniq } from "../../fe/Array"
import { notBlank } from "../../fe/Blank"
import { isTrue } from "../../fe/Boolean"
import { eql } from "../../fe/Eql"
import { Defined } from "../../fe/Maybe"
import { PromiseMaybe } from "../../fe/MaybeTypes"
import { Asset } from "../model/Asset"
import { AssetFile } from "../model/AssetFile"
import { assetPostUpsertTasks_ } from "./AssetPostUpsertTasks"
import { _updateAssetFile_ } from "./UpdateAssetFile"
import { UpdateResult, UpdateSuccessResult } from "./UpdateResult"

export async function updateAsset(cmd: UpdateAssetArgs): Promise<UpdateResult> {
  try {
    const result = await _updateAsset_(cmd)
    return {
      id: cmd.assetId,
      skipped: result?.asset == null,
      ...result
    }
  } catch (error: any) {
    return {
      id: cmd.assetId,
      error
    }
  }
}

async function _updateAsset_(cmd: UpdateAssetArgs): PromiseMaybe<
  Omit<UpdateSuccessResult, "id"> & {
    asset?: Asset
  }
> {
  const ctx = forceContextOrSetting(cmd)
  const assetId = cmd.assetId
  const log = mkLogger(`sync-file.UpdateAsset(${assetId})`)

  const asset = Asset.ops().findById(assetId)
  if (asset == null) {
    return log.throw("no such asset")
  }

  // Capture the original state of the asset's primary asset file:
  const originalAsset = asset.$clone()
  const originalPrimaryAF = asset.getShown()?.$clone()

  // first make sure all the asset files are up to date:
  // NOTE: mapAsync here is OK! ModelDbUpdater updates assets serially.
  const assetFileResults = await mapAsync({
    name: "UpdateAsset.updateAssetFile",
    arr: asset.getAssetFiles(),
    f: af =>
      _updateAssetFile_({
        // we may be "repairing" an asset, so respect the forceSync:
        ctx,
        assetFileId: af.id!,
        af
      })
  })

  if (
    assetFileResults.some(ea => isTrue(ea.deleted) || notBlank(ea.rejected))
  ) {
    // asset files were updated: reload from the db.
    asset.clear()
  }

  await backfillUpdate(asset.getAssetFiles())

  const sortedAssetFiles = sortAssetFiles(asset.getAssetFiles())

  const bestAcceptedAF = sortedAssetFiles?.[0]

  if (sortedAssetFiles == null || bestAcceptedAF == null) {
    // we don't know if the files were deleted, so we can delete the asset.
    log.warn("No existing files. Skipping for now.")
    asset.markUnshownAndUpsert()
    return {
      asset,
      rejected:
        "Empty asset (no AssetFiles after sorting). Un-showing Asset:" + assetId
    }
  }

  if (!bestAcceptedAF.isVersionUpToDate()) {
    log.warn(
      "Best file needs update, and we already tried to update all files. Skipping this asset.",
      { bestAcceptedAF }
    )
    asset.markUnshownAndUpsert()
    return {
      asset,
      rejected: `Best AssetFile:${bestAcceptedAF.id}, could not be updated. Un-showing Asset:${assetId}`
    }
  }

  const bestCapturedAt = await bestAcceptedAF.capturedAt()

  if (bestCapturedAt == null) {
    log.warn("Best file failed to extract a captured-at", { bestAcceptedAF })
    asset.markUnshownAndUpsert()
    return {
      asset,
      rejected: `Best AssetFile:${bestAcceptedAF.id}, failed to extract a captured-at. Un-showing Asset:${assetId}`
    }
  }

  const assetIdsToUpdate = new Set<number>()

  // If we have some asset files present, and some asset files non-existant,
  // assume we had clustered them correctly in the past, and leave them in the
  // current asset.

  // only care about the asset files that can be with bestAF:
  const [retainAFs, rejectAFs] = await partitionAsync(
    sortedAssetFiles,
    ea => isSimilarAssetFile(ea, bestAcceptedAF)
    // if the file is missing, kick it to another asset.
  )

  // shove all non-similar asset files into a new placeholder asset:
  if (isNotEmpty(rejectAFs)) {
    // 20220217: Don't worry about revalidating assets now. Just kick them some
    // place else:

    // cluster them and push into new Assets for later processing.

    const rejectClusters = await aggregateAssetFiles(rejectAFs)
    for (const arr of rejectClusters) {
      if (isEmpty(arr)) continue
      // We don't need to advisory-lock this new asset.
      const rejectAsset = Asset.ops().insertOne({
        capturedAtLocal: arr[0]!.capturedAtLocal,
        shown: false,
        version: 0
      })
      // Make sure we rebuild this asset later!
      assetIdsToUpdate.add(rejectAsset.id!)

      // update the in-memory asset files:
      for (const af of arr) {
        rejectAsset.addAssetFile(af)
      }
      // and rather than doing a bunch of upserts, update the db directly:
      AssetFile.dbl.runf(q =>
        q
          .update({ assetId: rejectAsset.id, shown: 0 })
          .whereIn("id", compact(arr.map(ea => ea.id)))
      )
      log.info("Pushed rejected asset files into new asset", {
        rejectAssetId: rejectAsset.id,
        rejectCluster: arr.map(ea => ({ id: ea.id, uri: ea.uri }))
      })
    }
  }

  function uniqFields<S extends keyof AssetFile>(
    s: S
  ): Defined<AssetFile[S]>[] {
    return uniq(retainAFs.map(ea => ea[s]))
  }

  const capturedAts = await Promise.all(retainAFs.map(ea => ea.capturedAt()))
  const delta = Settings.minCapturedAtPrecisionDifferentMimetypes.valueOrDefault
  const boundaries = capturedAts.map(ea => ea?.localBoundaries({ delta }))
  const startBoundary =
    min(boundaries.map(ea => ea?.start)) ??
    bestCapturedAt.localBoundaries({ delta })?.start
  const endBoundary =
    max(boundaries.map(ea => ea?.end)) ??
    bestCapturedAt.localBoundaries({ delta })?.end

  // fetch all assetfiles from other assets who have a common SHA or
  // capturedAtLocal or image hash:
  const externalAssetFiles = AssetFile.ops().all(
    AssetFile.query()
      .limit(Settings.maxContemporaryAdoptionAssets.valueOrDefault)
      .orderByRaw(
        `abs(AssetFile.capturedAtLocal - ${bestAcceptedAF.capturedAtLocal})`
      )
      .select("AssetFile.*")
      .distinct()
      .join("Asset", "Asset.id", "AssetFile.assetId")
      // don't return our own asset files:
      .where("AssetFile.assetId", "!=", assetId)
      // not updated yet
      .andWhere("Asset.version", "!=", AssetVersion)
      .andWhere(qb1 => {
        qb1 = qb1.whereIn("AssetFile.sha", uniqFields("sha")) // < same SHA is definitely the same asset

        if (startBoundary == null || endBoundary == null) {
          log.warn(
            "external asset file check: unset start or end localtime boundary",
            { capturedAts, startBoundary, endBoundary }
          )
        } else {
          qb1 = qb1.orWhereBetween("AssetFile.capturedAtLocal", [
            startBoundary,
            endBoundary
          ])
        }
        // also look at exact-match image hashes:
        return qb1
          .orWhereIn("AssetFile.meanHash", uniqFields("meanHash"))
          .orWhereIn("AssetFile.diffHash", uniqFields("diffHash"))
          .orWhereIn("AssetFile.dctHash", uniqFields("dctHash"))
      })
  )

  log.info(
    "asset file candidates for adoption: ",
    externalAssetFiles.map(ea => ea.posixPathFromGrandparent)
  )

  const similarAssetFiles = []

  for (const af of externalAssetFiles) {
    try {
      // Update the asset files to make sure isSimilarAssetFile has the
      // correct information:
      await af.updateFromFile_()
      // NOTE: we aren't upserting yet, unless it's _actually_ a similar variation
      if (isSimilarAssetFile(af, bestAcceptedAF)) {
        log.info("adopting similar asset file", { af })
        assetIdsToUpdate.add(af.assetId!)
        af.shown = false
        asset.addAssetFile(af)
        af.upsert()
        similarAssetFiles.push(af)
      }
    } catch (error) {
      log.warn(
        "Error updating external asset file. Leaving asset file record as-is.",
        { af, error }
      )
    }
  }

  log.info("fetched expansion", {
    retainAFs: retainAFs.map(ea => ea.id),
    rejectAFs: rejectAFs.map(ea => ea.id),
    externalAssetFiles: externalAssetFiles.map(
      ea => ea.posixPathFromGrandparent
    ),
    similarAssetFiles: similarAssetFiles.map(ea => ea.posixPathFromGrandparent)
  })

  // We may have pulled in asset files from other assets that weren't able to
  // be updated. Try to backfill if needed.
  await backfillUpdate([...retainAFs, ...similarAssetFiles])

  if (ctx.skipPreviews && ctx.skipAssetTagging) {
    log.info("skipping previews and tagging", ctx)
    asset.upsert()
  } else {
    await assetPostUpsertTasks_(asset, ctx)
  }

  const assetChanged = !eql(originalAsset, asset)
  const primaryAssetFileChanged = !eql(originalPrimaryAF, asset.getShown())

  const skipped =
    !assetChanged &&
    !primaryAssetFileChanged &&
    !ctx.forceRebuildPreviews &&
    !ctx.forceSync

  return log.tap({
    msg: "updateAsset() result",
    level: "info",
    result: {
      asset,
      // Tell the other asset ids that they need to be updated:
      assetIdsToUpdate: Array.from(assetIdsToUpdate),
      assetFiles: asset.assetFiles!,
      skipped
    },
    meta: {
      ctx,
      assetChanged,
      primaryAssetFileChanged
    }
  })
}

async function backfillUpdate(assetFiles: AssetFile[]) {
  const [upd, notUpd] = partition(assetFiles, ea => ea.isVersionUpToDate())

  // Can we back-fill the asset files with existing ones?
  for (const dest of notUpd) {
    const sibling = upd.find(ea => ea.sha === dest.sha)
    if (sibling != null) {
      mkLogger("UpdateAsset(" + dest.assetId + ")").info("backfilling", {
        sibling,
        dest
      })
      const result = await dest.updateFromShaSibling_(sibling)
      if (result != null) {
        result.upsert()
      }
    }
  }
}
