import { sortAssetFiles } from "../../core/asset-file/AssetFileSorter"
import { errorToS } from "../../core/error/Error"
import { UpdateAssetFileArgs } from "../../core/event/PhotoStructureEvents"
import { mkLogger } from "../../core/Logger"
import {
  forceContextOrSetting,
  RequiredForceContext
} from "../../core/settings/ForceContext"
import {
  uriEncodingVariants,
  uriIsEquivalent
} from "../../core/uri/UriNormalization"
import { isEmpty, isNotEmpty, uniq } from "../../fe/Array"
import { blank } from "../../fe/Blank"
import { Maybe } from "../../fe/MaybeTypes"
import { pick } from "../../fe/Object"
import { Asset } from "../model/Asset"
import { AssetFile } from "../model/AssetFile"
import { UpdateSuccessResult } from "./UpdateResult"

export interface UpdateAssetFileResult extends UpdateSuccessResult {
  assetFileId: number
  nativePath?: string
  error?: string
  assetDeleted?: boolean
}

export async function updateAssetFile(
  cmd: UpdateAssetFileArgs
): Promise<UpdateAssetFileResult> {
  try {
    return await _updateAssetFile_({
      ctx: forceContextOrSetting(cmd),
      assetFileId: cmd.assetFileId,
      af: AssetFile.ops().findById(cmd.assetFileId)
    })
  } catch (err) {
    return { id: cmd.assetFileId, ...cmd, error: errorToS(err) }
  }
}

export async function _updateAssetFile_({
  ctx,
  assetFileId,
  af
}: {
  ctx: RequiredForceContext
  assetFileId: number
  af: Maybe<AssetFile>
}): Promise<UpdateAssetFileResult> {
  const l = mkLogger(`updateAssetFile(${assetFileId})`)
  const f = await af?.posixFile_()

  const result = {
    id: assetFileId,
    assetFileId,
    path: f?.nativePath,
    uri: af?.uri
  }

  if (af == null) {
    return { ...result, error: "db record not found" }
  }

  if (f == null) {
    return { ...result, error: "file for URI not found" }
  }

  const deleted = await f?.isDeletedUri(af.uri)

  if (deleted == null) {
    l.info("no-op: file URI points to an unmounted volume", result)
    return { ...result, skipped: true }
  }

  const rejected = await af.whyRejected()
  if (deleted || !blank(rejected)) {
    const ea = { ...result, rejected, deleted }
    l.warn("file was deleted or rejected. Deleting.", ea)
    af.delete()
    return ea
  }

  const newUri = await f.uri_()

  // Has the scheme improved (was a volsha added)?
  if (!(await uriIsEquivalent(newUri, af.uri))) {
    const r = await handleAssetFileUriChange(af, newUri, ctx.forceSync)
    if (r != null) {
      return { ...result, ...r }
    }
  }

  // If we're on the latest asset file version, we're OK:
  if (!ctx.forceSync && (await af.matchesFile())) {
    l.info("no-op: file already matches db record", result)
    af.touch()
    return { ...result, skipped: true }
  }

  return { ...result, ...(await updateAssetFileIfNeeded(af, ctx.forceSync)) }
}

export async function handleAssetFileUriChange(
  af: AssetFile,
  newUri: string,
  forceSync: boolean
) {
  const l = mkLogger(`handleAssetFileUriChange(${af.id})`)

  // DON'T remove all "semantic equivalents": only remove the *same* URI.

  const variants = uniq([
    ...uriEncodingVariants(af.uri),
    ...uriEncodingVariants(newUri)
  ])

  l.warn("URI is changing. Avoiding duplicate keys.", {
    priorUri: af.uri,
    variants
  })

  // If there is another AssetFile with this URI, one of us needs to win:
  const afsWithNewUri = AssetFile.ops().allf(q =>
    q.whereIn("uri", variants).andWhereNot({ id: af.id })
  )

  if (isNotEmpty(afsWithNewUri)) {
    l.warn("Found other AssetFiles with equivalent URIs.", {
      afIds: afsWithNewUri.map(ea => ea.id)
    })
    const arr = [af, ...afsWithNewUri]
    const firstExistsWins = sortAssetFiles(arr)

    if (isEmpty(firstExistsWins)) {
      l.warn("sortAssetFiles returned undefined", {
        arr: arr.map(ea => pick(ea, "id", "uri")),
        variants
      })
      return { error: "sortAssetFiles returned undefined" }
    }

    const afIds = arr.map(ea => ea.id!)

    // The first AF to exist should win:
    for (const winner of firstExistsWins) {
      if (await winner.exists()) {
        // Delete the losers:
        AssetFile.dbl.runf(q =>
          q
            .whereIn(
              "id",
              afIds.filter(ea => ea !== winner.id)
            )
            .delete()
        )
        const result = updateAssetFileIfNeeded(winner, forceSync)
        Asset.dbl.runf(q =>
          q
            .update({ version: 0, updatedAt: Date.now() })
            .whereIn("id", uniq(arr.map(ea => ea.assetId)))
        )
        return result
      }
    }
  }
  l.info("no-op, no duplicate URIs.")
  return
}

export async function updateAssetFileIfNeeded(
  af: AssetFile,
  forceSync: boolean
) {
  const updated = await af.upsertIfNeeded_(undefined, forceSync)
  if (updated != null) {
    Asset.touch([af.assetId!])
  }
  return { assetFileId: af.id! }
}
