import { PosixFile } from "../../core/fs/PosixFile"
import { ExifTags } from "../../core/tags/ExifTags"
import { readTags } from "../../core/tags/ExifTool"
import { TagRoots } from "../../fe/api/Tag"
import { compactBlankish } from "../../fe/Array"
import { blank } from "../../fe/Blank"
import { Maybe } from "../../fe/MaybeTypes"

export function cameraTag(tags: Maybe<ExifTags>) {
  return tags == null || blank(tags.Make) // < only require Make!
    ? undefined
    : compactBlankish([TagRoots.Camera, tags.Make, tags.Model])
}

export async function cameraTagFile(file: PosixFile) {
  return cameraTag(await readTags(file))
}
