import { Latch } from "../../fe/Latch"
import { Endable } from "./Endable"
import { EndableWrapper } from "./EndableWrapper"

export interface Done extends Endable {
  isDone(): boolean
  donePromise(): Promise<void>
}

export class DoneWrapper extends EndableWrapper implements Done {
  readonly doneLatch = new Latch()

  isDone() {
    return !this.doneLatch.isPending() || this.ended
  }

  donePromise() {
    return this.doneLatch.promise
  }
}
