import { blank, notBlank } from "../../fe/Blank"
import { gt0 } from "../../fe/Number"

export interface UpdateErrorResult {
  id: string
  nativePath?: string
  details?: string
  error: Error | string
}

export interface UpdateSuccessResult {
  id: number
  nativePath?: string
  details?: string
  deleted?: boolean
  rejected?: string | string[]
  skipped?: boolean
  assetIdsToUpdate?: number[]
}

export type UpdateResult = UpdateErrorResult | UpdateSuccessResult

export function isUpdateErrorResult(obj: any): obj is UpdateErrorResult {
  return obj != null && notBlank(obj.error)
}

export function isUpdateSuccessResult(obj: any): obj is UpdateSuccessResult {
  return obj != null && gt0(obj.id) && blank(obj.error)
}
