import { parseJSON } from "../../core/JSON"
import { Settings } from "../../core/settings/Settings"
import { compactBlanks, isEmpty } from "../../fe/Array"
import { PromiseMaybe } from "../../fe/MaybeTypes"
import { toInt } from "../../fe/Number"
import { UpdateResult } from "../sync-file/UpdateResult"
import { WorkQueue } from "../WorkQueue"
import { enqueueAssetUpdates } from "./UpdateQueue"

/**
 * Only used by ModelDbUpdater
 */
export async function mkAssetUpdateQueue(
  processor: (assetId: number) => PromiseMaybe<UpdateResult>
): PromiseMaybe<WorkQueue> {
  if (Settings.noAssetUpdates.valueOrDefault) return
  const queueNames = compactBlanks(await enqueueAssetUpdates())
  if (isEmpty(queueNames)) return

  return WorkQueue.mk({
    queueNames,
    concurrent: false,
    processItem(qi) {
      const obj = parseJSON(qi.contents)
      return processor(obj?.assetId ?? toInt(qi.contents)!)
    },
    endWhenDone: true
  })
}
