import { thenMap } from "../../core/async/Promise"
import { PosixFile } from "../../core/fs/PosixFile"
import { Settings } from "../../core/settings/Settings"
import { ExifTags } from "../../core/tags/ExifTags"
import { readTags } from "../../core/tags/ExifTool"
import { TagRoots } from "../../fe/api/Tag"
import { compactBlanks } from "../../fe/Array"
import { mapNotBlank } from "../../fe/Blank"
import { map } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"

export function lensTag(tags: Maybe<ExifTags>) {
  return map(tags, t =>
    mapNotBlank(t.lensMake, make =>
      mapNotBlank(
        compactBlanks(
          Settings.tagFullLensModel.valueOrDefault
            ? [t.lensModel, t.lensInfo]
            : [t.lensInfo, t.lensModel]
        )[0],
        model => [TagRoots.Lens, make, model]
      )
    )
  )
}

export function lensTagFile(file: PosixFile) {
  return thenMap(readTags(file), lensTag)
}
