import { delay } from "./Delay"
import { Latch } from "./Latch"
import { Maybe } from "./MaybeTypes"
import { isPromise } from "./Promise"

export interface Throttled<T> {
  (...args: any[]): Maybe<T>
  /**
   * How many times has fn() been invoked?
   */
  count(): number
  /**
   * Would a call to apply() right now be throttled/ignored?
   */
  isThrottled(): boolean

  /**
   * Resolved when the current call to apply() is done.
   */
  donePromise(): Promise<void>

  /**
   * Wait for any prior call to finish, then re-apply with these args.
   */
  force(...args: any[]): Promise<T>

  /**
   * Minimum number of milliseconds betwee the end of the last application and
   * the start of the next application
   */
  minCallDelayMs(): number

  setMinCallDelayMs(millis: number): void
}

/**
 *
 */
export function throttle<T>({
  f,
  minCallDelayMs
}: {
  f: (...args: any[]) => T
  minCallDelayMs: number
}): Throttled<T> {
  minCallDelayMs = Math.max(50, minCallDelayMs) // 50ms lower bound
  let latch: Maybe<Latch>
  let runTs = 0
  let running = false
  let count = 0

  const onComplete = () => {
    void latch?.resolve()
    latch = undefined
    runTs = Date.now()
    running = false
  }

  const r = (...args: any[]) => {
    if (running) return
    const now = Date.now()
    if (now >= runTs + minCallDelayMs) {
      running = true
      count++
      try {
        const result = f(...args)
        if (isPromise(result)) {
          void result.finally(onComplete)
        } else {
          onComplete()
        }
        return result
      } catch (e) {
        running = false
        throw e
      }
    } else {
      return
    }
  }

  r.minCallDelayMs = () => minCallDelayMs
  r.setMinCallDelayMs = (millis: number) => {
    minCallDelayMs = Math.max(50, millis)
  }

  r.isThrottled = () => running || Date.now() <= runTs

  r.donePromise = () => {
    return running ? (latch ??= new Latch()) : Promise.resolve()
  }

  r.count = () => count

  r.force = async (...args: any[]) => {
    // wait for the prior run to finish:
    while (true) {
      if (running) {
        // Push next out to not let others start while we wait in line:
        runTs = Date.now() + minCallDelayMs * 10
        await delay(50)
      } else {
        runTs = 0
        return r(...args)! // < we know we will win
      }
    }
  }
  return r
}
