import { Knex } from "knex"
import { lazy } from "../../core/Lazy"
import { Logger, mkLogger } from "../../core/Logger"
import { whyNotSameAsset } from "../../core/asset-file/AssetFileComparator"
import { thenMap } from "../../core/async/Promise"
import { time, timedLazy } from "../../core/async/PromiseTimer"
import { SerialLaterQueue } from "../../core/async/SerialLaterQueue"
import { InternalErrorFlag } from "../../core/error/ErrorFlags"
import { PosixFile } from "../../core/fs/PosixFile"
import { imageHash } from "../../core/img/ImageHash"
import { throwIfInvalidFile_ } from "../../core/img/ValidFile"
import {
  ForceContext,
  RequiredForceContext,
  forceContextOrSetting
} from "../../core/settings/ForceContext"
import { Settings } from "../../core/settings/Settings"
import { readTags } from "../../core/tags/ExifTool"
import { isNotEmpty, uniq } from "../../fe/Array"
import { sortBy } from "../../fe/ArraySort"
import { blank } from "../../fe/Blank"
import { secondMs } from "../../fe/Date"
import { mapOr } from "../../fe/Maybe"
import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { gt0 } from "../../fe/Number"
import { MaybeSyncOrAsync } from "../../fe/SyncOrAsync"
import { Asset } from "../model/Asset"
import { AssetFile } from "../model/AssetFile"

export async function isFileInSync(
  nativePath: PosixFile | string
): Promise<boolean> {
  return new AssetFileFinder(PosixFile.for(nativePath)).alreadySynced()
}

const AssetIdCreationMutex = new SerialLaterQueue<number>()

export class AssetFileFinder {
  readonly #logger: Logger
  readonly #rejectedSiblingIds: Set<number> = new Set()

  readonly ctx: RequiredForceContext

  constructor(
    readonly file: PosixFile,
    ctx?: ForceContext
  ) {
    this.#logger = mkLogger(
      "sync-file.AssetFileFinder(" + file.baseWithGrandparent + ")"
    )
    this.ctx = forceContextOrSetting(ctx)
  }

  async prior() {
    return this.byUri()
  }

  async priorIfSynced() {
    if (this.ctx.forceSync) return undefined
    const prior = await this.prior()
    return true === (await prior?.matchesFile()) ? prior : undefined
  }

  readonly alreadySynced = timedLazy(
    "sync-file.AssetFileFinder.alreadySynced: " + this.file,
    async () =>
      this.#logger.tap({
        msg: "alreadySynced",
        result: (await this.priorIfSynced()) != null
      })
  )

  readonly newAssetFile_ = timedLazy(
    "sync-file.AssetFileFinder.newAssetFile: " + this.file,
    async () => {
      const af = await new AssetFile().updateFromFile_(this.file)
      if (af == null) {
        return this.#logger.throw("AssetFile.updateFromFile returned null", {
          fatal: false
        })
      } else {
        return af
      }
    }
  )

  /**
   * @return a persisted AssetFile if `maybeAsset` resolves to a persisted Asset
   */
  async newAssetFileForAsset(maybeAsset: MaybeSyncOrAsync<Asset>) {
    const asset = await maybeAsset
    if (asset == null) {
      // We expect the asset to be undefined if we do a query for a sibling
      // asset file and miss.
      return
    } else if (asset.id == null) {
      this.#logger.throw("newAssetFileForAsset(): given non-persisted Asset")
    } else {
      const af = await this.newAssetFile_()
      asset.addAssetFile(af)
      af.insert()
      this.#logger.debug("newAssetFileForAsset() upsert success!", { af })
      return af
    }
  }

  readonly tags_ = lazy(async () => {
    const t = await readTags(this.file)
    if (t == null) {
      throw new Error(this.file + " doesn't have metadata")
    } else {
      return t
    }
  })

  async capturedAt_() {
    return (await this.tags_()).capturedAt
  }

  readonly imageHash = lazy(() =>
    Settings.useImageHashes.valueOrDefault ? imageHash(this.file) : undefined
  )

  readonly dominantLabhashes = lazy(() =>
    thenMap(this.imageHash(), ih =>
      // the uniq _shouldn't_ be needed, but we're backstopping against prior
      // bad/buggy means:
      uniq(ih.dominantColors.filter(ea => ea.pct > 5).map(ea => ea.labhash))
    )
  )

  readonly capturedAtLocal = lazy(async () => {
    const ca = await this.capturedAt_()
    return this.#logger.tap({
      msg: "capturedAtLocal",
      result: ca?.local,
      meta: { capturedAt: ca }
    })
  })

  readonly mapSibling = (assetFile: Maybe<AssetFile>) =>
    this.newAssetFileForAsset(assetFile?.getAsset())

  readonly apply_ = timedLazy<Promise<AssetFile>>(
    "sync-file.AssetFileFinder: " + this.file,
    async () => {
      if (blank(await this.file.uri_())) {
        throw new Error("Cannot import, file URI is blank for " + this.file)
      }

      {
        // this throws if the file is invalid:
        const withExistingAsset = await this.byExistingAsset_()
        if (withExistingAsset != null) {
          this.#logger.info("Found existing asset for file")
          return withExistingAsset
        }
      }

      // this can be done outside the asset id creation mutex:
      const af = await this.newAssetFile_()

      const assetId = await AssetIdCreationMutex.enqueue(async () => {
        // did a sibling file make an asset suitable for me while I was
        // waiting for the mutex?
        const withExistingAsset = await this.byExistingAsset_()
        if (withExistingAsset?.assetId != null) {
          this.#logger.info(
            "Found existing asset for file after acquiring lock",
            { withExistingAsset }
          )
          return withExistingAsset.assetId
        }
        const asset = new Asset()
        // NOTE: we're not setting shown = true or version yet, because we haven't
        // built previews:
        asset.capturedAtLocal = af.capturedAtLocal
        asset.addAssetFile(af) // < may adopt capturedAt and rating from this
        asset.upsert()
        return asset.id!
      })

      af.assetId = assetId
      return af.upsert()
    }
  )

  // DO NOT LAZY THIS! WE CALL IT TWICE.
  private readonly byExistingAsset_ = () =>
    time(
      "sync-file.AssetFileFinder.byExistingAsset: " + this.file,
      async () => {
        const strategies = [
          // URI is the business key, so if it exists, use that
          {
            name: "byUri",
            f: () => this.byUri()
          },
          // DON'T THINK "equivalent" URIs ARE THE SAME ASSET FILE. You can't change
          // URIs for existing AssetFiles: that's a different AssetFile!
          {
            name: "byNormalizedPathUri",
            f: () => this.newAssetFileForAsset(this.assetByNormalizedPathUri())
          },
          // Same file contents must be adopted by the same asset
          {
            name: "bySha",
            f: () => this.newAssetFileForAsset(this.assetBySha())
          },
          // OK, we haven't seen this SHA, so let's validate tags can be fetched:
          {
            name: "hasTags",
            f: async () => {
              await this.tags_()
              return undefined
            }
          },
          // OK, we haven't seen this SHA, so let's validate the image.
          {
            name: "validFile",
            f: async () => {
              await throwIfInvalidFile_(this.file)
              return undefined
            }
          },
          {
            name: "byCapturedAtOrImageHash",
            f: () =>
              this.newAssetFileForAsset(this.assetByCapturedAtOrImageHash())
          }
        ]

        for (const { name, f } of strategies) {
          try {
            const af = await time(
              "sync-file.assetFileFinder." + name + ": " + this.file,
              f
            )
            if (af == null) {
              this.#logger.debug("apply(): " + name + ": returned null")
            } else {
              this.#logger.info("apply(): " + name, {
                assetFileId: af.id,
                assetId: af.assetId
              })
              return af
            }
          } catch (err) {
            this.#logger.debug("apply(): " + name + " rejected: " + err)
            throw err
          }
        }
        return undefined
      }
    )

  // Must not be cached (part of the double-checked advisory lock)
  async byUri() {
    const uri = await this.file.uri_()
    if (uri == null) {
      this.#logger.warn("byUri() uri is null", { file: this.file })
      return
    }

    // HEY FUTURE ME: I bet you want to search by SHA, right? DO NOT! The URI
    // is different, and that's a different AssetFile.

    // AssetFile.URIs are the business key--they can't change!

    return (
      AssetFile.ops().findOneBy({ uri }) ??
      AssetFile.ops().findOneBy({ uri: this.file.fileuri() })
    )
  }

  assetByNormalizedPathUri() {
    return thenMap(this.file.normalizedPathUri_(), uri =>
      Asset.findFirstByFile(qb => qb.where("AssetFile.uri", uri))
    )
  }

  // Must not be cached (part of the double-checked advisory lock)
  assetBySha() {
    return thenMap(this.file.sha(), sha =>
      Asset.findFirstByFile(qb => qb.where("AssetFile.sha", sha))
    )
  }

  async assetByCapturedAtOrImageHash(): PromiseMaybe<Asset> {
    const ca = await this.capturedAt_()
    if (null == ca) {
      // readTags is supposed to never return a Tags instance without a `.capturedAt` field.
      return this.#logger.throw(
        "Cannot import, capturedAt is null" + InternalErrorFlag
      )
    }

    if (Settings.strictDeduping.valueOrDefault) {
      return this.firstSimilarAsset(
        AssetFile.ops().allf(q => q.where({ capturedAtLocal: ca.local }))
      )
    }

    const limit = Settings.maxContemporaryAdoptionAssets.valueOrDefault

    const qb = () => {
      let q = AssetFile.query()
      if (gt0(limit)) q = q.limit(limit)
      return q.orderByRaw(`abs(capturedAtLocal - ${ca.local})`)
    }

    const delta = Math.max(
      secondMs,
      Settings.minCapturedAtPrecisionDifferentMimetypes.valueOrDefault
    )

    return time(
      "sync-file.AssetFileFinder.assetByCapturedAtAndImageHash: " + this.file,
      async () => {
        {
          const bounds = ca.localBoundaries({ delta })
          if (bounds == null) {
            this.#logger.warn(
              "Failed to fetch localBoundaries, cannot fetch similar assets by localtime",
              { ca, delta }
            )
          } else {
            const result =
              this.firstSimilarAssetByQuery(
                "captured-at",
                qb().whereBetween("capturedAtLocal", [bounds.start, bounds.end])
              ) ??
              (Settings.checkBasenameMatches.valueOrDefault
                ? this.firstSimilarAssetByQuery(
                    "basename",
                    qb().whereILike("uri", "%/" + this.file.name + "%")
                  )
                : undefined)
            if (result != null) return result
          }
        }

        const ih = await this.imageHash()
        if (ih == null) {
          this.#logger.warn("assetByCapturedAtAndImageHash: no imageHash")
          return
        }
        const modes = await this.dominantLabhashes()
        if (isNotEmpty(modes)) {
          // Expand the captured-at delta but only for matching dominant colors:
          const wideBounds = ca.localBoundaries({ delta: delta * 4 })
          if (wideBounds == null) {
            this.#logger.error(
              "Failed to fetch localBoundaries, cannot fetch similar assets by wide localtime + image hash",
              { ca, delta }
            )
          } else {
            const result = this.firstSimilarAssetByQuery(
              "wide captured-at and dominant color",
              qb()
                .whereBetween("capturedAtLocal", [
                  wideBounds.start,
                  wideBounds.end
                ])
                .andWhere(b =>
                  b
                    .whereIn("mode0", modes)
                    .orWhereIn("mode1", modes)
                    .orWhereIn("mode2", modes)
                )
            )
            if (result != null) return result
          }
        }
        // if it's exactly the same image hash, check those:
        return this.firstSimilarAssetByQuery(
          "image hash",
          qb()
            .where("meanHash", ih.meanHash)
            .orWhere("diffHash", ih.diffHash)
            .orWhere("dctHash", ih.dctHash)
        )
      }
    )
  }

  async firstSimilarAssetByQuery(context: string, qb: Knex.QueryBuilder) {
    if (this.#rejectedSiblingIds.size > 0) {
      qb = qb.whereNotIn("AssetFile.id", Array.from(this.#rejectedSiblingIds))
    }
    const result = await this.firstSimilarAsset(AssetFile.ops().all(qb))
    if (result != null) {
      this.#logger.info("Found similar asset by " + context, { result })
    }
    return result
  }

  async firstSimilarAsset(assetFiles: AssetFile[]) {
    const thisAF = await this.newAssetFile_()
    const cal = await this.capturedAtLocal()
    if (thisAF == null) {
      throw new Error("Failed to update from " + this.file)
    }
    // Look at the nearest-captured-at-time first.
    const sorted = sortBy(
      assetFiles.filter(ea => !this.#rejectedSiblingIds.has(ea.id!)),
      af => [
        mapOr(
          cal,
          ea => Math.abs(af.capturedAtLocal - ea),
          -af.capturedAtLocal
        ),
        af.id!
      ]
    )

    for (const af of sorted) {
      if (af != null) {
        await af.getNativePath()
        const siblings = AssetFile.ops().findBy({ assetId: af.assetId })
        await Promise.all(siblings.map(ea => ea.getNativePath()))
        const why = whyNotSameAsset(thisAF, af, siblings)
        if (why == null) {
          this.#logger.info("Found sibling AssetFile", af)
          return Asset.ops().findById(af.assetId!)
        } else {
          this.#rejectedSiblingIds.add(af.id!)
          this.#logger.debug(
            "Contemporary assetFile not similar: " + why,
            af?.uri
          )
        }
      }
    }
    return
  }
}
