import { Task } from "batch-cluster"
import { parseJSON } from "../../core/JSON"
import { lazy } from "../../core/Lazy"
import { mkLogger } from "../../core/Logger"
import { time } from "../../core/async/PromiseTimer"
import { isNonRetriableError } from "../../core/error/ErrorTypes"
import {
  UpdateAssetArgs,
  UpdateAssetFileArgs
} from "../../core/event/PhotoStructureEvents"
import { Settings } from "../../core/settings/Settings"
import { compactBlanks } from "../../fe/Array"
import { stringify } from "../../fe/JSON"
import { PromiseMaybe } from "../../fe/MaybeTypes"
import { gt0 } from "../../fe/Number"
import { toS } from "../../fe/toS"
import { isErrorResult } from "./ImportResult"
import { updateAsset } from "./UpdateAsset"
import { updateAssetFile } from "./UpdateAssetFile"
import { UpdateResult, isUpdateSuccessResult } from "./UpdateResult"

const logger = lazy(() => mkLogger("sync-file.UpdateTask"))

// TODO: DELETE ALL THIS STUFF. JUST USE METHODS! THERE AREN'T MULTIPLE
// IMPLEMENTATIONS! DANG YOU, PRIOR MATT!

export type UpdateCommand = UpdateAssetArgs | UpdateAssetFileArgs

export type UpdateProcessor<T extends UpdateCommand = UpdateCommand> = (
  cmd: T
) => PromiseMaybe<UpdateResult>

export function validUpdateCommandOrElse(cmd: any): UpdateCommand {
  if (!gt0(cmd.assetId ?? cmd.assetFileId)) {
    logger().throw("validUpdateCommand(): missing asset or assetFile id", {
      fatal: false,
      retriable: false,
      cmd
    })
  }
  return cmd
}

export class UpdateTask extends Task<UpdateResult> {
  private readonly name: string

  constructor(readonly cmd: UpdateCommand) {
    super(stringify(validUpdateCommandOrElse(cmd)), input => this.parse(input))
    this.name = `UpdateTask(${stringify(cmd)})`
  }

  toString() {
    return this.name
  }

  private parse(input: string): UpdateResult {
    for (const line of compactBlanks(toS(input).split("\n")).reverse()) {
      const obj = parseJSON(line)
      if (isUpdateSuccessResult(obj)) return obj
    }
    return logger().throw(this.name + ".parse() failed", { input })
  }
}

export type SyncFileCommand = UpdateCommand | { path: string; force?: boolean }

export function describeUpdateCommand(cmd: UpdateCommand): string {
  const o: any = cmd
  if (gt0(o.assetId)) {
    return "updateAsset:" + o.assetId
  } else if (gt0(o.assetFileId)) {
    return "updateAssetFile:" + o.assetFileId
  } else if (gt0(o.assetPreviewId)) {
    return "updateAssetPreviews:" + o.assetPreviewId
  } else {
    return "invalid command: " + stringify(cmd)
  }
}

export function handleUpdateCommand(cmd: UpdateCommand): Promise<UpdateResult> {
  logger().info("handleUpdateCommand()", { cmd })
  const o: any = cmd
  const result = time(
    "sync-file." + describeUpdateCommand(cmd),
    gt0(o.assetId)
      ? updateAsset(o)
      : gt0(o.assetFileId)
      ? updateAssetFile(o)
      : logger().throw("Invalid update command " + stringify(cmd))
  )
  if (isErrorResult(result)) {
    const maxRetries = Settings.maxRetries.valueOrDefault
    const isNonRetriable = isNonRetriableError(result.error)
    if (gt0(maxRetries) && gt0(cmd.retries) && isNonRetriable !== true) {
      logger().warn("Retrying failed command", { cmd, result })
      return handleUpdateCommand({
        ...cmd,
        retries: (cmd.retries ?? Settings.maxRetries.valueOrDefault) - 1
      })
    } else {
      logger().warn("Command failed, and cannot retry", {
        result,
        isNonRetriable,
        retries: cmd.retries,
        maxRetries
      })
    }
  }
  return result
}
