import { lazy } from "../core/Lazy"
import { mkLogger } from "../core/Logger"
import { durationHMS } from "../core/date/Date"
import {
  libraryOriginalsDirPosixFile,
  libraryPreviewsDirPosixFile
} from "../core/dir/LibraryDirs"
import { Settings } from "../core/settings/Settings"
import { libraryHasSettings } from "../core/settings/SettingsIO"
import { volumeMetadataTtlMs } from "../core/volumes/VolumeTtls"
import { bestVolumeForPath, volumes } from "../core/volumes/Volumes"
import { dayMs, secondMs } from "../fe/Date"
import { clamp } from "../fe/Number"
import { GB, fmtBytes } from "../fe/Units"

const logger = lazy(() => mkLogger("SetVolumesTTL"))

export async function setVolumesTTL() {
  const defaultTTL = volumeMetadataTtlMs()
  const ttl = await calcVolumesTTL()
  volumes.setTTL(Math.max(ttl ?? defaultTTL, defaultTTL))
}

export async function calcVolumesTTL() {
  if (!libraryHasSettings()) return

  // we can relax the volume timeout if there's tons of free disk on both the previews and originals directories.
  const volFree: number[] = []
  const vols = await volumes()
  const minFreeDefaultBytes = Settings.minDiskFreeGb.defaultValue * GB
  for (const dir of [
    libraryOriginalsDirPosixFile(),
    libraryPreviewsDirPosixFile()
  ]) {
    const vol = await bestVolumeForPath(dir, vols)
    if (vol == null) {
      logger().warn(
        "Can't find volume for " + dir + ", using default TTL for volumes()"
      )
      return
    }
    if (vol.available < minFreeDefaultBytes) {
      logger().warn(
        "Volume for " +
          dir +
          " is almost full: using default TTL for volumes()",
        {
          free: fmtBytes(vol.available),
          minFree: fmtBytes(Settings.minDiskFreeGb.defaultValue * GB)
        }
      )
      return
    }
    volFree.push(vol.available)
  }
  const excessFree = Math.min(...volFree) - minFreeDefaultBytes
  // We won't write faster than 25MB/s
  const secondsUntilFull = Math.round(excessFree / 25)
  const newTTL = clamp(
    volumeMetadataTtlMs(),
    dayMs,
    secondsUntilFull * secondMs
  )
  logger().info(
    "Min free volume is " +
      fmtBytes(Math.min(...volFree)) +
      ". Extending volumes() TTL to " +
      durationHMS(newTTL),
    { secondsUntilFull }
  )
  return newTTL
}
