import { batches } from "../../core/Array"
import { SimpleFile } from "../../core/fs/SimpleFile"
import { SyncFileStates, syncReport } from "../../core/fs/SyncReport"
import { Settings } from "../../core/settings/Settings"
import { nativePath2uri } from "../../core/uri/FileURI"
import { uriEncodingVariants } from "../../core/uri/UriNormalization"
import { mkAssetUrl } from "../../core/web/WebUrls"
import { blank } from "../../fe/Blank"
import { fmtHMS } from "../../fe/Date"
import { groupBy } from "../../fe/MultiMap"
import { gte } from "../../fe/Number"
import { AssetFile } from "../model/AssetFile"

/**
 * Remove the files in `files` that are already in sync with the library.
 */
export async function precheckFiles(
  files: SimpleFile[],
  updateFreshFiles = true
) {
  const uri2file = new Map<string, SimpleFile>()
  const byDir = groupBy(files, ea => ea.dir)
  for (const [dir, arr] of byDir.entries()) {
    const uri = await nativePath2uri(dir)
    for (const file of arr) {
      for (const ea of uriEncodingVariants(uri + "/" + file.base)) {
        uri2file.set(ea, file)
      }
    }
  }

  const fresh: { path: string; assetId: number; assetFileId: number }[] = []
  const inSyncAssetFileIds: number[] = []

  const maxUpdatedDeltaTs =
    Date.now() - Settings.syncChangedIntervalMs.valueOrDefault

  for (const uris of batches([...uri2file.keys()], 256)) {
    const priors = AssetFile.ops().allf(q => q.whereIn("uri", uris))

    for (const af of priors) {
      const de = uri2file.get(af.uri)!
      if (de == null) {
        syncReport().onProgress({
          path: (await af.posixFile_())?.nativePath ?? af.uri,
          from: "Precheck",
          state: SyncFileStates.failed,
          details:
            "Failed to correlate prior AssetFile with requested DirectoryEntry"
        })
      } else if (gte(af.updatedAt, maxUpdatedDeltaTs)) {
        syncReport().onProgress({
          path: de.nativePath,
          from: "Precheck",
          state: SyncFileStates.noop,
          details: "synced " + fmtHMS(Date.now() - af.updatedAt!) + " ago",
          url: mkAssetUrl(af.assetId)?.toString()
        })
        fresh.push({
          path: de.nativePath,
          assetId: af.assetId!,
          assetFileId: af.id!
        })
      } else {
        const why = await af.whyNotNoop()
        if (blank(why)) {
          syncReport().onProgress({
            path: de.nativePath,
            from: "Precheck",
            state: SyncFileStates.noop,
            details: "in sync",
            url: mkAssetUrl(af.assetId)?.toString()
          })
          inSyncAssetFileIds.push(af.id!)
          fresh.push({
            path: de.nativePath,
            assetId: af.assetId!,
            assetFileId: af.id!
          })
        } else {
          syncReport().onProgress({
            path: de.nativePath,
            from: "Precheck",
            state: SyncFileStates.enqueued,
            details: "update needed: " + why,
            url: mkAssetUrl(af.assetId)?.toString()
          })
        }
      }
    }
  }

  if (updateFreshFiles) AssetFile.touch(inSyncAssetFileIds)

  const freshPaths = new Set(fresh.map(ea => ea.path))

  return {
    fresh,
    stale: files.filter(ea => !freshPaths.has(ea.nativePath))
  }
}
