import { Maybe } from "../../fe/MaybeTypes"
import { Endable, ending } from "../async/Endable"
import { HealthCheck } from "../health/HealthCheck"
import { isTooBusy } from "./CpuUsage"

export type DoNotRunEndable = Pick<Endable, "ended">

export function doNotRun(endable?: DoNotRunEndable): boolean {
  return whyDoNotRun(endable) != null
}

export function whyDoNotRun(endable?: DoNotRunEndable): Maybe<string> {
  if (true === endable?.ended) return "endable ended"
  if (ending()) return "service is ending"
  if (isTooBusy()) return "system is too busy"
  const s = HealthCheck.summary()
  return ["ok", "warn"].includes(s.level)
    ? undefined
    : s.level + ": " + s.msg.join("\n")
}
