function escRE(s: string) {
  return matchQuotes(escapeRegExp(s))
}

function familyNameREs() {
  return Settings.tagNamesSurnames.values.map(
    ea => new RegExp("\\b(" + escRE(ea) + ")\\b", "i")
  )
}

function givenNameREs() {
  return Settings.tagNamesGiven.values.map(
    ea => new RegExp("\\b(" + escRE(ea) + ")\\b", "i")
  )
}

function familyNamePrefixeREs() {
  return mapNotEmpty(
    compactBlanks(Settings.tagNamesSurnamePrefixes.values),
    arr =>
      sortBy(arr, ea => -ea.length).map(
        ea => new RegExp(`\\b(${escRE(ea)}\\s+\\S+)`, "i")
      )
  )
}

function givenNameSurroundREs() {
  const arr = compactBlanks(Settings.tagNamesGivenSurrounds.values)
  for (const ea of arr) {
    if (ea.length !== 2) {
      logger().warn(
        "Settings.tagNamesFamilySurrounds has an invalid value:",
        ea
      )
    }
  }
  return arr.map(
    ea =>
      new RegExp(
        `(${escapeRegExp(ea.charAt(0))}\\s*[^${escapeRegExp(
          ea.charAt(0)
        )}]+\\s*${escapeRegExp(ea.charAt(1))})`,
        "i"
      )
  )
}

function familyNameSurroundREs() {
  const arr = compactBlanks(Settings.tagNamesFamilySurrounds.values)
  for (const ea of arr) {
    if (ea.length !== 2) {
      logger().warn(
        "Settings.tagNamesFamilySurrounds has an invalid value:",
        ea
      )
    }
  }
  return arr.map(
    ea =>
      new RegExp(
        `${escapeRegExp(ea.charAt(0))}\\s*([^${escapeRegExp(
          ea.charAt(0)
        )}]*)\\s*${escapeRegExp(ea.charAt(1))}`,
        "i"
      )
  )
}

const lifespanRE = /(\(\s*\d+\s*-\s*[\d?]{0,4}\s*\)?)/

const modifierRE = /([,\s]+(?:jr\.?|junior|sr\.?|senior|[iv]+\.?))$/i

const uppercaseWordsRE = /\b([A-Z']+[-A-Z']{2})\b/

const ignorableNameRE = /^[()[\]/.\-_]+$/

/**
 * Parse the given name into familyNames, givenNames, an optional modifier and
 * optional lifespan.
 */
export function parseName(compoundName: string): Maybe<{
  givenNames: string[]
  modifier: Maybe<string>
  lifespan: Maybe<string>
  familyNames: string[]
}> {
  if (blank(compoundName)) return

  // normalize spaces so "First  ( Maiden ) Last " becomes "First (Maiden) Last"
  let s = compoundName
    .replace(/\s+/g, " ")
    .replace(/\(\s+/g, "(")
    .replace(/\s+\)/g, ")")
    .trim()

  const lifespan = map(spliceCapture(s, lifespanRE), r => {
    s = r.unmatched.trim()
    // normalize whitespace:
    return r.captured.replace(/\s+/g, "")
  })

  // Strip off Bob Smith, jr.
  const modifier = map(spliceCapture(s, modifierRE), r => {
    s = r.unmatched.trim()
    return r.captured.trim()
  })

  const givenNames: string[] = []
  const givenNameEnds: string[] = []
  const familyNames: string[] = []

  for (const re of givenNameREs()) {
    const r = spliceCapture(s, re)
    if (r != null) {
      givenNames.push(r.captured)
      s = r.unmatched.trim()
    }
  }

  for (const re of givenNameSurroundREs()) {
    let r
    do {
      r = spliceCapture(s, re)
      if (r != null) {
        givenNameEnds.push(r.captured)
        s = r.unmatched.trim()
      }
    } while (r != null)
  }

  let minFamilyNameIndex: Maybe<number>

  function onCapture(r: Defined<ReturnType<typeof spliceCapture>>) {
    if (minFamilyNameIndex == null || minFamilyNameIndex > r.matchedIndex) {
      minFamilyNameIndex = min([minFamilyNameIndex, r.matchedIndex])
    }
  }

  if (Settings.tagNamesCapitalizedAsFamily.valueOrDefault) {
    let r
    do {
      r = spliceCapture(s, uppercaseWordsRE)
      if (r != null) {
        onCapture(r)
        familyNames.push(r.captured)
        s = r.unmatched.trim()
      }
    } while (r != null)
  }

  for (const re of familyNameSurroundREs()) {
    let r
    do {
      r = spliceCapture(s, re)
      if (r != null) {
        onCapture(r)
        familyNames.push(r.captured)
        s = r.unmatched.trim()
      }
    } while (r != null)
  }

  // if (Settings.tagNamesParentheticalSurnames.valueOrDefault) {
  //   let r
  //   do {
  //     r = spliceCapture(s, parenNameRE)
  //     if (r != null) {
  //       onCapture(r)
  //       familyNames.push(r.captured)
  //       s = r.remnants.trim()
  //     }
  //   } while (r != null)
  // }

  if (Settings.tagNamesLexical.valueOrDefault) {
    const commaIndex = s.indexOf(",")
    if (commaIndex >= 0) {
      minFamilyNameIndex = undefined
      familyNames.push(s.substring(0, commaIndex).trim())
      givenNames.push(s.substring(commaIndex + 1).trim())
      s = ""
    }
  }

  for (const re of familyNameREs()) {
    const r = spliceCapture(s, re)
    if (r != null) {
      onCapture(r)
      familyNames.push(r.captured)
      s = r.unmatched.trim()
    }
  }

  for (const re of toA(familyNamePrefixeREs())) {
    const r = spliceCapture(s, re)
    if (r != null) {
      onCapture(r)
      familyNames.push(r.captured)
      s = r.unmatched.trim()
    }
  }

  if (minFamilyNameIndex != null && s.length > minFamilyNameIndex) {
    // Everything to the right is a family name.
    mapNotEmpty(
      compactBlanks(s.slice(minFamilyNameIndex).split(/\s+/)),
      arr => {
        familyNames.push(...arr)
        s = s.slice(0, minFamilyNameIndex)
      }
    )
  }

  // OK, by this time we're either looking at an empty string, or [first]
  // [middle] [last] (we may have pulled off either family names or given names,
  // so everything is optional)

  const names = compactBlanks(s.split(/\s+/))

  // cleanupGivenNames(givenNames)
  const givenNameCount = givenNames.length

  // cleanupFamilyNames(familyNames)
  const familyNameCount = familyNames.length

  if (names.length > 0) {
    // So, we've got leftovers. Where should they go?
    // If we don't have a family name, and we don't have a given name:
    if (givenNameCount === 0 && familyNameCount > 0) {
      // We have a family name. Assume the remainder are given names?
      givenNames.push(...names)
    } else if (
      familyNameCount === 0 &&
      givenNameCount > 0 &&
      names.length === 1
    ) {
      familyNames.push(...names)
    } else {
      if (names.length === 1) {
        // Assume one name is a given name
        givenNames.push(...names)
      } else {
        // names.length > 1. Last or first name is family name, the remainder is given names.
        const western = Settings.tagNamesOrder.valueOrDefault === "western"

        familyNames.push(western ? names.pop()! : names.shift()!)
        givenNames.push(...names)
      }
    }
  }

  // cleanupFamilyNames(familyNames)

  return {
    givenNames: cleanupNames([...givenNames, ...givenNameEnds]),
    modifier,
    lifespan,
    familyNames: cleanupNames(familyNames),
    minFamilyNameIndex
  } as any
}

function cleanupNames(arr: Maybe<string>[]): string[] {
  return uniq(compactBlanks(arr).filter(ea => ignorableNameRE.exec(ea) == null))
}

