function join(arr: Maybe<string>[]) {
  let result = ""
  for (const ea of compactBlanks(arr)) {
    // we don't want to add a space after the modifier if it starts with a comma
    result += ea.startsWith(",") ? ea : " " + ea
  }
  return result.trim()
}

export function renderNameTag(name: string): TagPath[] {
  if (blank(name)) return []

  const fmt = Settings.tagNamesFormatter.valueOrDefault
  const fam_full = fmt === "family/fullname"
  const fam_given = fmt === "family/given"
  if (fam_full || fam_given) {
    const r = parseName(name)
    if (r == null) {
      return []
    } else {
      const suff = join([r.modifier, r.lifespan])
      const given = join(r.givenNames)
      const familyNames = compactBlanks(
        notEmptyOr(r.familyNames, [
          Settings.tagNamesDefaultFamily.valueOrDefault
        ])
      )
      return familyNames.map(fam => [
        TagRoots.Who,
        fam,
        fam_given ? join([given, suff]) : renderName(fam, given, suff)
      ])
    }
  } else {
    return [[TagRoots.Who, name.trim()]]
  }
}

function renderName(family: string, given: string, suffix: string): string {
  return join(
    Settings.tagNamesOrder.valueOrDefault === "western"
      ? [given, family, suffix]
      : [family, given, suffix]
  )
}
