import { IncomingHttpHeaders, IncomingMessage } from "http"
import https from "https"
import { URL } from "url"
import { blank } from "../../fe/Blank"
import { map, orElse } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { AppName } from "../AppName"
import { within } from "../Number"
import { version } from "../Version"
import { WrappedError } from "../error/WrappedError"
import { URI } from "../uri/URI"
import { commandTimeoutMs } from "../volumes/VolumeTtls"

export interface HttpsRequestOptions extends https.RequestOptions {
  body?: string | Buffer
}

export interface RequestResponse {
  ok: boolean
  headers: IncomingHttpHeaders
  body: string
  statusCode: Maybe<number>
  statusMessage: string | undefined
}

export async function request(
  url: string | URI | URL,
  options: HttpsRequestOptions = {}
) {
  if (options.headers == null) options.headers = {}
  if (blank(options.headers["user-agent"])) {
    options.headers["user-agent"] = AppName + " v" + version
  }

  return new Promise<RequestResponse>((resolve, reject) => {
    const req = https.request(url.toString(), options)
    req.setTimeout(orElse(options.timeout, commandTimeoutMs()), () => {
      reject(
        new WrappedError("Timeout fetching <" + url + ">", {
          doNotSend: true,
          retriable: true
        })
      )
    })

    req
      .on("response", (response: IncomingMessage) => {
        const dataChunks: (Buffer | string)[] = []
        response.setEncoding("utf8")
        response.on("data", chunk => dataChunks.push(chunk))
        response.on("end", () => {
          resolve({
            ok: within(200, 399, response.statusCode),
            headers: response.headers,
            body: dataChunks.join(""),
            statusCode: response.statusCode,
            statusMessage: response.statusMessage
          })
        })
      })
      .on("error", e => {
        reject(e)
      })

    map(options.body, ea => req.write(ea))
    req.end()
  })
}

