import { partition } from "../../core/Array"
import { lazy } from "../../core/Lazy"
import { mkLogger } from "../../core/Logger"
import { escapeRegExp } from "../../core/RegExp"
import {
  equalsIgnoreCase,
  includesIgnoreCase,
  isString,
  uniqIgnoreCase
} from "../../core/String"
import { lcdiff } from "../../core/StringSimilarity"
import { mapAsyncSerial, thenMap } from "../../core/async/Promise"
import { PosixFile } from "../../core/fs/PosixFile"
import { Settings } from "../../core/settings/Settings"
import { readRawTags } from "../../core/tags/ExifTool"
import { delimRe, pathSepRe, rawTagKeywords } from "../../core/tags/KeywordTags"
import { joinTagPath } from "../../core/tags/TagPaths"
import {
  compact,
  compactBlanks,
  isNotEmpty,
  uniq,
  uniqBy2
} from "../../fe/Array"
import { sortBy } from "../../fe/ArraySort"
import { mapNotBlank, notBlank } from "../../fe/Blank"
import { later } from "../../fe/Delay"
import { flatten } from "../../fe/Flatten"
import { mapOr } from "../../fe/Maybe"
import { TagPath, TagRoots } from "../../fe/api/Tag"
import { toS } from "../../fe/toS"
import { normalizeTagRoot, stripTagRoot } from "./Taggers"
import { isWhoTag, nameTag } from "./WhoTagger"

const logger = lazy(() => mkLogger("curators.KeywordTagger"))

const delimSpaceRe = lazy(() =>
  mapNotBlank(
    Settings.keywordDelimiters.valueOrDefault,
    ea => new RegExp(`[ /${escapeRegExp(ea)}]`)
  )
)

later(() => {
  Settings.keywordDelimiters.watchLater(() => delimSpaceRe.unset())
})

export function extractDashDashTags(s: string): string[] {
  return logger().tap({
    msg: "extractDashDashTags()",
    result: mapOr(
      toS(s).split("--")[1],
      ea => compactBlanks(mapOr(delimSpaceRe(), re => ea.split(re), [ea])),
      () => []
    ),
    meta: { s }
  })
}

/**
 * Extract the "dash-dash" tags encoded in `f`'s path
 */
export function extractPathnameTags(f: PosixFile): string[] {
  return [
    ...extractDashDashTags(f.parent().posixPath),
    ...extractDashDashTags(f.name)
  ]
}

// doesn't handle things like news:alt.goth but those don't have slashes, so who cares
const UrlRE = /\w{2,7}:\/\/\S+/g

/**
 * Given a bunch of flat or hierarchical strings, split, normalize, and dedupe
 * the paths.
 */
export function normalizeKeywordPaths(arr: (string | string[])[]): string[][] {
  const result: string[][] = []

  // handled as keywords:
  const urls: string[] = []

  const arr1: (string | string[])[] = []

  for (const ea of compact(arr)) {
    if (Array.isArray(ea)) {
      arr1.push(ea)
    } else {
      // Pull out the URLs as keywords:
      const s = toS(ea).replace(UrlRE, m => {
        urls.push(m)
        return ""
      })
      arr1.push(...mapOr(delimRe(), re => s.split(re), [s]))
    }
  }

  // Split hierarchical paths:
  const arr2 = mapOr(
    pathSepRe(),
    re => arr1.map(ea => (isString(ea) ? ea.split(re) : ea)),
    arr1
  )

  // Convert all one-array-deep keywords to simple strings:
  const arr3 = arr2.map(ea =>
    Array.isArray(ea) && ea.length === 1 ? ea[0] : ea
  )

  // Split strings into paths that contain keywordPathSeparators.
  const [paths1, kws1] = partition(arr3, Array.isArray) as [
    string[][],
    string[]
  ]

  const paths = paths1
    .map(ea => compactBlanks(ea.map(ea1 => ea1.trim())))
    .filter(isNotEmpty)

  // DON'T USE LOWERCASE HERE. We want locale-aware matching.
  const pathNames: string[] = uniqIgnoreCase(flatten(paths))

  result.push(...paths)

  const kws = [...kws1, ...urls]
    .map(ea => ea.trim())
    .filter(ea => notBlank(ea) && !includesIgnoreCase(pathNames, ea))

  const copy = Settings.keywordReparenting.valueOrDefault === "copy"
  const retain = Settings.keywordReparenting.valueOrDefault === "retain"
  const aliases = Settings.rootTagKeywordsAliases.values

  for (const ea of [...result]) {
    if (includesIgnoreCase(aliases, ea[0])) {
      // Normalize keyword roots:
      ea[0] = TagRoots.Keywords
    }
    if (ea[0] !== TagRoots.Keywords) {
      if (copy) {
        result.push([TagRoots.Keywords, ...ea])
      } else if (retain) {
        ea.unshift(TagRoots.Keywords)
      }
    }
  }

  // For all elements that are just strings, delete those that are leafNames.
  for (const kw of kws) {
    // Push all elements that are still strings into [Keywords, $ea]
    result.push([TagRoots.Keywords, kw])
  }

  return uniq(result)
}

/**
 * @return uniq paths, with most-cased-values winning over lowercase:
 */
export function dedupeKeywordPaths(arr: TagPath[]): TagPath[] {
  return uniqBy2(
    sortBy(arr.map(normalizeTagRoot), ea => {
      const j = joinTagPath(ea)
      return [j.normalize().toLowerCase(), -1 * lcdiff(j)]
    }),
    (a, b) => equalsIgnoreCase(joinTagPath(a), joinTagPath(b))
  )
}

/**
 * Normalizes hierarchical paths, extracts Who tags, and returns deduped
 * TagPaths.
 */
export function processKeywords(
  keywords: (string | string[])[],
  whoTags: TagPath[]
): TagPath[] {
  // DigiKam craps into SEVEN TAGS for faces, *yay*
  const [rawKwWhoTags, nonWhoKws] = partition(
    normalizeKeywordPaths(keywords),
    isWhoTag
  )
  const kwWhoTags = flatten(rawKwWhoTags.map(nameTag))

  // Include both "first last" and "last, first"
  const whoNames = uniq(
    flatten(
      [...kwWhoTags, ...whoTags].map(tagPath => {
        const kw = stripTagRoot(tagPath)
        return [kw.join(", "), kw.join(" "), [...kw].reverse().join(" ")]
      })
    )
  )

  const result = dedupeKeywordPaths([
    ...nonWhoKws.filter(
      kw => !includesIgnoreCase(whoNames, stripTagRoot(kw).join(" "))
    ),
    ...kwWhoTags
  ])

  // console.log("processKeywords", {
  //   keywords,
  //   whoTags,
  //   rawKwWhoTags,
  //   nonWhoKws,
  //   kwWhoTags,
  //   whoNames,
  //   result
  // })

  return logger().tap({
    msg: "processKeywords()",
    level: "info",
    result
  })
}

/**
 * Main entry point: extract and dedupe all keywords for all given files.
 */
export async function keywordTagFiles(
  files: PosixFile[],
  whoTags: TagPath[]
): Promise<TagPath[]> {
  const pathnameKeywords = Settings.tagKeywordsFromPath.valueOrDefault
    ? flatten(files.map(extractPathnameTags))
    : []
  const tagKeywords = Settings.tagKeywordsFromMetadata.valueOrDefault
    ? flatten(
        await mapAsyncSerial({
          name: "keywordTagFiles",
          arr: files,
          f: f => thenMap(readRawTags(f), rawTagKeywords)
        })
      )
    : []

  return processKeywords([...pathnameKeywords, ...tagKeywords], whoTags)
}
