import { sep } from "path"
import { lazy } from "../../core/Lazy"
import { isNotEmpty } from "../../fe/Array"
import { andList } from "../../fe/FmtList"
import { MemoizedThunk } from "../../fe/Lazy"
import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { clamp } from "../../fe/Number"
import { pick } from "../../fe/Object"
import { cmpArr } from "../../fe/Primitive"
import { Timeout, thenOrTimeout } from "../../fe/ThenOrTimeout"
import { plur } from "../../fe/Units"
import { toA } from "../../fe/toA"
import { partition } from "../Array"
import { Logger, mkLogger } from "../Logger"
import { countChars } from "../String"
import { errorToS } from "../error/Error"
import { isSidecarExt } from "../fs/FileExt"
import { Settings } from "../settings/Settings"
import { isSidecarOf } from "../tags/Sidecars"
import { isPosixMountpoint } from "../volumes/Mountpoints"
import { mkFolderUrl } from "../web/WebUrls"
import { isPaused } from "../work/IsPaused"
import { DirectoryEntry } from "./DirectoryEntry"
import { DirectoryWalker } from "./DirectoryWalker"
import { whyExcludedDirectory } from "./Excluded"
import { posixPathFrom, splitNativePath } from "./Path"
import { whyRejectFileSimple } from "./PosixFileFilters"
import {
  SyncDirStates,
  SyncFileStates,
  SyncProgress,
  syncReport
} from "./SyncReport"

export interface Next {
  done: boolean
  skipped?: boolean
}

export const Done: Next = { done: true }
export const Undone: Next = { done: false }

const ScannedPreviouslyMsg = "this was scanned in a prior incomplete sync"
/**
 * Recurses through the given directory, sending files that may be eligible for
 * import to a given fileHandler, and finished directories to a
 * directoryListener.
 */
export class DirectoryIterator {
  readonly name: string
  readonly start = Date.now()
  readonly #logger: Logger

  readonly #pendingChildFiles: DirectoryEntry[] = []
  readonly #enqueuedChildFiles: DirectoryEntry[] = []
  readonly #sidecars: DirectoryEntry[] = []
  readonly #pendingChildDirs: DirectoryEntry[] = []
  #childDelegate?: DirectoryIterator
  #done = false

  #childFileCount = 0
  #totalChildFileCount = 0
  #childDirCount = 0
  #totalChildDirCount = 0
  #timeoutCount = 0

  constructor(
    readonly dir: DirectoryEntry,
    readonly ctx: DirectoryWalker,
    readonly parent?: DirectoryIterator
  ) {
    syncReport().onProgress({
      path: dir.nativePath + sep,
      from: "DirectoryIterator",
      state: SyncDirStates.scanning
    })
    this.name = "dir.DirectoryIterator(" + dir + ")"
    this.#logger = mkLogger(this.name)
  }

  get isRoot(): boolean {
    return this.parent == null
  }

  readonly rootDir: MemoizedThunk<DirectoryEntry> = lazy(
    () => this.parent?.rootDir() ?? this.dir
  )

  readonly pathFromRoot = lazy(() => {
    return posixPathFrom({ nativePath: this.rootDir().dir }, this.dir)
  })

  isDone(): boolean {
    return this.#done
  }

  status() {
    return {
      root: this.rootDir().nativePath,
      path: this.pathFromRoot(),
      isDone: this.isDone()
    }
  }

  cancel(s: Partial<SyncProgress>) {
    // console.log(this.name + ".cancel()", s)
    if (!this.#done) {
      this.#done = true
      syncReport().onProgress({
        path: this.dir.nativePath + sep,
        from: "DirectoryIterator",
        state: SyncDirStates.canceled,
        ...s,
        elapsedMs: Date.now() - this.start
      })
    }
    return Done
  }

  depth() {
    return countChars(this.dir.nativePath, sep)
  }

  readonly nextTimeout = lazy(
    () =>
      // the closer to a root, the more patient we are, so children can clean up
      // gracefully:
      Settings.statTimeoutMs.valueOrDefault +
      clamp(0, 512, (512 - this.depth()) * 10)
  )

  /**
   * @param untilTs run until the millisecond timestamp is less than Date.now()
   * @throws if there are problems, even after retries.
   */
  // Note this doesn't need a mutex because the only consumer, DirectoryWalker,
  // already only runs one at a time.
  async next(untilTs: number) {
    // console.log(this.name + ".next()", {
    //   runMs: Date.now() - untilTs,
    //   this_done: this._done,
    //   pending_files: this.pendingChildFiles.map(ea => ea.parentAndBase),
    //   delegate_child: this.childDelegate?.dir.parentAndBase,
    //   pending_dirs: this.pendingChildDirs.map(ea => ea.parentAndBase)
    // })
    try {
      this.#logger.trace("next() starting", { runMs: untilTs - Date.now() })
      const result = await thenOrTimeout(
        this.#next(untilTs),
        this.nextTimeout()
      )
      this.#logger.trace("next() finished", { result })
      if (result === Timeout) {
        this.#timeoutCount++
        this.#logger.warn(".next() timed out", {
          statTimeoutMs: Settings.statTimeoutMs.valueOrDefault,
          timeoutCount: this.#timeoutCount
        })
        if (this.#timeoutCount > 3) {
          this.#logger.warn(
            ".next(): too many timeouts, giving up on this directory."
          )
          return this.cancel({
            state: SyncDirStates.timeout,
            details: "Failed to scan, even after 3 retries"
          })
        } else {
          return Undone
        }
      } else {
        this.#timeoutCount = 0
        return result
      }
    } catch (error) {
      this.#logger.warn("next() failed", { error })
      return this.cancel({
        state: SyncDirStates.failed,
        details: "Error thrown during scan: " + errorToS(error)
      })
    }
  }

  async #next(untilTs: number) {
    return (
      (this.#done ? Done : undefined) ??
      // Don't maybeTimeout until maybeCompleteResult, to ensure we do at least a bit of work.
      (await this.#isExcluded()) ??
      (await this.#setupChildren()) ??
      (await this.#maybePopChildFile(untilTs)) ??
      (await this.#maybePopSidecar(untilTs)) ?? // sidecars after processing children, so only valid sidecars are reported.
      (await this.#maybeDelegateToChildDir(untilTs)) ??
      // (await this.maybeTimeout(untilTs)) ?? DON'T RESTORE THIS. this will make very short runtimes hang on slow hardware
      (await this.#maybeCompleteResult())
    )
  }

  readonly #isExcluded = lazy(async () => {
    const details =
      (await whyExcludedDirectory(this.dir)) ??
      // we can't just exclude all mountpoints, because that would make
      // mountpoints un-scannable.
      (!this.isRoot && (await isPosixMountpoint(this.dir))
        ? "nested mountpoint"
        : undefined)

    if (details != null) {
      this.#logger.info("skipping ignorable dir, " + this.dir, {
        details: details
      })
      this.cancel({
        state: SyncDirStates.skipped,
        details
      })
      return Done
    }
    return // AKA continue
  })

  #maybeTimeout(untilTs: number): Next | undefined {
    return this.ctx.ended
      ? this.cancel({ details: "DirectoryWalker was ended" })
      : this.#done
      ? Done
      : isPaused() || Date.now() > untilTs
      ? Undone
      : undefined
  }

  #shouldSkipChild(child: DirectoryEntry): boolean {
    return this.#logger.tap({
      msg: "shouldSkipChild()",
      result:
        isNotEmpty(this.ctx.startAtNativePath) &&
        cmpArr(
          splitNativePath(child.nativePath),
          this.ctx.startAtNativePath,
          false // shorterIsLessThan
        ) <= 0,
      meta: {
        child: child.base
      }
    })
  }

  readonly #setupChildren = lazy(async () => {
    const [skipped, children] = partition(toA(await this.dir.children()), ea =>
      this.#shouldSkipChild(ea)
    )

    for (const ea of skipped) {
      syncReport().onProgress({
        path: ea.nativePath,
        from: "DirectoryIterator",
        state: SyncFileStates.skipped,
        details: ScannedPreviouslyMsg
      })
    }

    // NOTE: We aren't doing any child filtering here, because

    // 1. filtering children can take time

    // 2. reporting on files and directories should be done consistently, and
    //    directories already are reported by this.isExcluded.

    // Note that this isn't just a .partition, because .isFile() !== !.isDirectory()

    this.#pendingChildDirs.push(...children.filter(ea => ea.isDirectory()))
    this.#totalChildDirCount = this.#pendingChildDirs.length

    const files = children.filter(ea => ea.isFile())

    // (json is considered a sidecar type)
    const [sidecars, nonSidecars] = partition(files, isSidecarExt)

    this.#pendingChildFiles.push(...nonSidecars)
    this.#sidecars.push(...sidecars)
    this.#totalChildFileCount = this.#pendingChildFiles.length
  })

  async #maybePopChildFile(untilTs: number): PromiseMaybe<Next> {
    const toAdd: DirectoryEntry[] = []
    let timeout: Maybe<Next>

    while (timeout == null && isNotEmpty(this.#pendingChildFiles)) {
      const child = this.#pendingChildFiles.shift()!
      // Always increment
      this.#childFileCount++

      // should we "fast-forward" to where we were before?
      const whyReject = await whyRejectFileSimple(child)

      if (whyReject != null) {
        this.#logger.debug("maybePopChildFile()", { child, whyReject })
        syncReport().onProgress({
          path: child.nativePath,
          from: "DirectoryIterator",
          state: SyncFileStates.rejected,
          details: whyReject
        })
      } else {
        // The file listener (AssetFileQueue or Precheck) will handle the sync
        // report (if we do it here, we'll get multiple enqueue events):
        toAdd.push(child)
      }
      // Putting this at the bottom of the loop ensures we process at least one
      // child file. DON'T RETURN HERE, we may have files in toAdd!
      timeout ??= this.#maybeTimeout(untilTs)
      if (timeout != null) {
        this.#logger.trace("maybePopChildFile() timeout", { timeout })
      }
    }

    if (isNotEmpty(toAdd)) {
      this.#logger.trace("maybePopChildFile()", { toAdd })
      await this.ctx.fileHandler(toAdd)
      this.#enqueuedChildFiles.push(...toAdd)
    }
    return timeout
  }

  async #maybePopSidecar(untilTs: number): PromiseMaybe<Next> {
    while (isNotEmpty(this.#sidecars)) {
      const sidecar = this.#sidecars.shift()!
      if (Settings.tagAlbumFilenames.values.includes(sidecar.base)) {
        syncReport().onProgress({
          path: sidecar.nativePath,
          from: "DirectoryIterator",
          state: SyncFileStates.note,
          details: "sidecar will be checked for for album metadata"
        })
      } else {
        const src = this.#enqueuedChildFiles.filter(ea =>
          isSidecarOf(ea, sidecar)
        )
        if (isNotEmpty(src)) {
          // if this sidecar is for any of the enqueued child files, we're
          // good
          syncReport().onProgress({
            path: sidecar.nativePath,
            from: "DirectoryIterator",
            state: SyncFileStates.note,
            details:
              "sidecar will be processed with " +
              andList(src.map(ea => ea.base.normalize()))
          })
        } else {
          syncReport().onProgress({
            path: sidecar.nativePath,
            from: "DirectoryIterator",
            state: SyncFileStates.rejected,
            details:
              "sidecar will be ignored (no importable file was found to be relevant to this sidecar's name)"
          })
        }
      }
      // Remember that we don't need to _actually_ enqueue it. Sidecars are
      // handled automatically.

      // Putting this at the bottom of the loop ensures we process at least
      // one sidecar.
      const timeout = this.#maybeTimeout(untilTs)
      if (timeout != null) {
        this.#logger.trace("handleSidecars() timeout", { timeout })
        return timeout
      }
    }
    return
  }

  async #maybeDelegateToChildDir(untilTs: number): PromiseMaybe<Next> {
    let timeout: Maybe<Next>

    while (
      timeout == null &&
      (this.#childDelegate != null || isNotEmpty(this.#pendingChildDirs))
    ) {
      if (this.#childDelegate == null) {
        const child = this.#pendingChildDirs.shift()
        if (child == null) {
          // console.log("wat")
        } else {
          this.#logger.trace("delegating to child dir, " + child)
          this.#childDirCount++
          this.#childDelegate = new DirectoryIterator(child, this.ctx, this)
        }
      }

      if (this.#childDelegate != null) {
        const result = await this.#childDelegate.next(untilTs)
        if (result?.done ?? this.#childDelegate?.isDone() === true) {
          this.#childDelegate = undefined
        }
      }
      // Putting this at the bottom of the loop ensures we process at least one
      // child dir:
      timeout = this.#maybeTimeout(untilTs)
    }
    return timeout
  }

  /**
   * Note that as this method is the last in the chain, it must return a
   * response.
   */
  async #maybeCompleteResult(): Promise<Next> {
    if (
      isNotEmpty(this.#pendingChildFiles) ||
      isNotEmpty(this.#pendingChildDirs) ||
      this.#childDelegate != null
    ) {
      this.#logger.error(
        "maybeCompleteResult: not done (?!)",
        pick(
          this as any,
          "result",
          "pendingChildren",
          "childDelegate",
          "pendingChildDirs"
        )
      )
      return Undone
    } else {
      if (!this.#done) {
        this.#done = true
        const uri = await mkFolderUrl(this.dir.nativePath)
        const noSeed = uri?.with({ query: null })
        const url = noSeed?.toString()
        syncReport().onProgress({
          path: this.dir.nativePath + sep,
          state: SyncDirStates.scanned,
          from: "DirectoryIterator",
          details: `${this.#childFileCount}/${plur(
            this.#totalChildFileCount,
            "file"
          )} and ${this.#childDirCount}/${plur(
            this.#totalChildDirCount,
            "directory",
            "directories"
          )}`,
          url,
          elapsedMs: Date.now() - this.start
        })
        await this.ctx.directoryListener(this.dir)
      }

      return Done
    }
  }
}
