import { Maybe } from "../fe/MaybeTypes"
import { times } from "../fe/Number"

export class TTLArray<T> implements Iterable<T> {
  private readonly times: number[] = []
  private readonly a: T[] = []
  private readonly expirationListeners: (() => void)[] = []
  constructor(
    readonly ttlMs: number,
    readonly maxLength?: number
  ) {}

  [Symbol.iterator](): Iterator<T, any, undefined> {
    this.vacuum()
    const arr = [...this.a]
    function* iter(): IterableIterator<T> {
      for (const k of arr) {
        yield k
      }
    }
    return iter()
  }

  push(...t: T[]) {
    times(t.length, () => this.times.push(Date.now()))
    this.a.push(...t)
    if (this.maxLength != null) this.vacuum()
  }

  pushUniq(...t: T[]) {
    t.forEach(ea => {
      if (!this.includes(ea)) this.push(ea)
    })
  }

  includes(t: T) {
    this.vacuum()
    return this.a.indexOf(t) >= 0
  }

  find(predicate: (value: T, index: number) => boolean): T | undefined {
    this.vacuum()
    return this.a.find(predicate)
  }

  some(predicate: (value: T, index: number) => boolean): boolean {
    this.vacuum()
    return this.a.some(predicate)
  }

  shift() {
    this.vacuum()
    this.times.shift()
    return this.a.shift()
  }

  first() {
    this.vacuum()
    return this.a[0]
  }

  last() {
    this.vacuum()
    return this.a[this.a.length - 1]
  }

  shiftOrFirst() {
    return this.length > 1 ? this.shift() : this.first()
  }

  pop() {
    this.vacuum()
    this.times.pop()
    return this.a.pop()
  }

  onExpire(listener: () => any) {
    this.expirationListeners.push(listener)
  }

  /**
   * Returns a copy of a section of an array.
   * For both start and end, a negative index can be used to indicate an offset from the end of the array.
   * For example, -2 refers to the second to last element of the array.
   * @param start The beginning index of the specified portion of the array.
   * If start is undefined, then the slice begins at index 0.
   * @param end The end index of the specified portion of the array. This is exclusive of the element at the index 'end'.
   * If end is undefined, then the slice extends to the end of the array.
   */
  slice(start?: number, end?: number): T[] {
    this.vacuum()
    return this.a.slice(start, end)
  }

  get length(): number {
    this.vacuum()
    return this.a.length
  }

  clear(): this {
    this.times.length = 0
    this.a.length = 0
    return this
  }

  get values(): T[] {
    this.vacuum()
    return [...this.a]
  }

  map<U>(callbackfn: (value: T, index: number, array: T[]) => U) {
    this.vacuum()
    return this.a.map(callbackfn)
  }

  oldestEntryAge(): Maybe<number> {
    this.vacuum()
    return this.times[0]
  }

  /**
   * Remove all expired entries
   */
  private vacuum() {
    if (this.a.length === 0) return
    const priorLength = this.a.length
    if (this.maxLength != null) {
      const deleteCount = this.a.length - this.maxLength
      this.times.splice(0, deleteCount)
      this.a.splice(0, deleteCount)
    }
    const minTime = Date.now() - this.ttlMs
    const firstGoodIndex = this.times.findIndex(ea => ea > minTime)
    if (firstGoodIndex === -1) {
      this.clear()
    } else if (firstGoodIndex > 0) {
      this.times.splice(0, firstGoodIndex)
      this.a.splice(0, firstGoodIndex)
    }
    if (priorLength !== this.a.length) {
      for (const ea of this.expirationListeners) {
        ea()
      }
    }
  }
}
