import { toA } from "../../fe/toA"
import { clusterEvery, clusterSome } from "../Array"
import { AggregateTypes } from "../settings/AggregateTypes"
import { Settings } from "../settings/Settings"
import { isSimilarAssetFile } from "./AssetFileComparator"
import { sortAssetFiles } from "./AssetFileSorter"
import { IAssetFile } from "./IAssetFile"

export async function aggregateAssetFiles<T extends IAssetFile>(
  arr: T[]
): Promise<T[][]> {
  // clustering may not be deterministic if we don't sort beforehand.
  const sorted = toA(sortAssetFiles(arr) ?? arr)
  const some = Settings.assetAggregation.valueOrDefault === AggregateTypes.union
  return (some ? clusterSome : clusterEvery)(sorted, isSimilarAssetFile)
}
