import { lazy } from "../../core/Lazy"
import { mkLogger } from "../../core/Logger"
import { tagRefToS } from "../../core/tags/TagPaths"
import { uriToTagPath } from "../../core/web/UriToTagPath"
import { compact } from "../../fe/Array"
import { PS_LOCAL_FILE_SCHEME } from "../../fe/URI"
import { TagPath, TagRoots } from "../../fe/api/Tag"
import { Asset } from "../model/Asset"
import { AssetFile } from "../model/AssetFile"
import { modelDb } from "../model/ModelDb"
import { displayNameForVolsha } from "../tag/TagSql"

const logger = lazy(() => mkLogger("curators.FilePathTagger"))

export async function tagAssetPaths(uris: string[]): Promise<TagPath[]> {
  const result: TagPath[] = []
  for (const uri of uris) {
    try {
      const tp = uriToTagPath({ uri, isFile: true })
      if (tp == null) continue
      // `info` may not have spun up the db:
      if (uri.startsWith(PS_LOCAL_FILE_SCHEME) && modelDb() != null) {
        const volsha = tagRefToS(tp[1])
        // Fix for https://forum.photostructure.com/t/volume-id-shows-as-a-tag/754
        tp[1] = {
          name: volsha,
          displayName: await displayNameForVolsha(volsha)
        }
        logger().info("tagAssetPaths()", { uri, tp })
      }
      result.push(tp)
    } catch (err) {
      logger().warn("Failed to parse asset file URI", { uri })
    }
  }
  return result
}

export function isFsTag(t: TagPath): boolean {
  return tagRefToS(t[0]).toLowerCase() === TagRoots.fs
}

export function addFilesTagsToAsset(assetId: number) {
  const uris = AssetFile.dbl.pluckAllf<string>(q =>
    q.select("uri").where({ assetId })
  )
  return addFileUriTagsToAsset(assetId, uris)
}

export function addFileUriTagsToAsset(assetId: number, uris: string[]) {
  return Asset.addTags(
    assetId,
    compact(uris.map(uri => uriToTagPath({ uri, isFile: true })))
  )
}
