import { lazy } from "../../core/Lazy"
import { mkLogger } from "../../core/Logger"
import { AbortError } from "../../core/async/AbortError"
import {
  DoNotSendErrorFlag,
  NonRetriableErrorFlag
} from "../../core/error/ErrorFlags"
import { UpdateAssetArgs } from "../../core/event/PhotoStructureEvents"
import { submitTask_ } from "../../core/event/SharedState"
import { PosixFile } from "../../core/fs/PosixFile"
import { whyRejectFile } from "../../core/fs/PosixFileFilters"
import {
  AssetFileSyncStates,
  SyncFileStates,
  syncReport
} from "../../core/fs/SyncReport"
import { whyInvalidFile } from "../../core/img/ValidFile"
import { ContextualLogger } from "../../core/log/ContextualLogger"
import {
  ForceContext,
  RequiredForceContext,
  forceContextOrSetting
} from "../../core/settings/ForceContext"
import { isNotEmpty } from "../../fe/Array"
import { blank } from "../../fe/Blank"
import { isTrue } from "../../fe/Boolean"
import { Maybe } from "../../fe/MaybeTypes"
import { UnwrapPromiseLike } from "../../fe/UnwrapPromiseLike"
import { toA } from "../../fe/toA"
import { Library } from "../Library"
import { libraryFileFiltersFor } from "../LibraryFileFilters"
import { AssetFile } from "../model/AssetFile"
import { AssetFileFinder } from "./AssetFileFinder"
import { AssetFileRepository } from "./AssetFileRepository"
import { assetPostUpsertTasks_ } from "./AssetPostUpsertTasks"
import { ImportResult } from "./ImportResult"

export async function importFileToResult(
  file: PosixFile,
  ctx?: ForceContext
): Promise<ImportResult> {
  const l = Library.instanceRequired()
  return syncReport().wrap_({
    path: file.nativePath,
    from: "importFileToResult()",
    fn_: () =>
      new AssetFileImporter(file, l.assetFileRepository(), ctx).apply_()
  })
}

/**
 * AssetFileImporter actually imports a file into the database, and runs all
 * curators. If you want sync reporting, use importFileToResult() instead.
 */
export class AssetFileImporter {
  readonly start = Date.now()
  readonly #logger: ContextualLogger
  readonly #assetFileFinder: AssetFileFinder
  readonly ctx: RequiredForceContext
  constructor(
    readonly file: PosixFile,
    readonly repo: Maybe<AssetFileRepository>,
    ctx?: ForceContext
  ) {
    this.ctx = forceContextOrSetting(ctx)
    this.#logger = mkLogger("sync-file." + this.toString())
    this.#assetFileFinder = new AssetFileFinder(file, ctx)
  }

  // 8 is the current number of different calls to .increment in this file and
  // UpdateAssetPreviews. Not all imports will increment to this value.

  toString(): string {
    return "AssetFileImporter(" + this.file.nativePath + ")"
  }

  newAssetFile() {
    return this.#assetFileFinder.newAssetFile_()
  }

  readonly whyRejected = lazy(async () => {
    const rejected: string[] = []
    const uri = await this.file.uri_()
    if (uri == null) {
      rejected.push(
        "Cannot make URI" + DoNotSendErrorFlag + NonRetriableErrorFlag
      )
    } else {
      const deleted = await this.file.isDeletedUri(uri)
      // if deleted is undefined, we don't know if the file was deleted or not:
      // it may be on an unmounted volume.
      if (deleted === true) {
        rejected.push("File was deleted")
      } else {
        const result = await whyRejectFile(
          this.file,
          libraryFileFiltersFor(this.file)
        )
        if (result != null) rejected.push(result)
      }

      const prior = await this.prior()

      // Only delete the asset file if we're deleted or filtered
      if (isNotEmpty(rejected)) {
        if (prior != null) {
          this.#logger.info(
            "Deleting prior AssetFile, and requesting prior asset repair",
            { rejected, prior }
          )
          prior.delete()
          const uaa: UpdateAssetArgs = {
            assetId: prior.assetId!,
            forceRebuildPreviews: isTrue(prior.shown)
          }
          void submitTask_("repairAsset", uaa)
        }
      }

      // Don't re-validate files already in the db.
      // ForceSync and outdated versions should require a re-validation (so we use inSyncWithFile, not matchesFile)
      if (
        rejected.length === 0 &&
        (prior == null || !(await prior.inSyncWithFile()))
      ) {
        const why = await whyInvalidFile(this.file.nativePath)
        if (!blank(why)) {
          rejected.push(why)
        }
      }
    }

    return rejected
  })

  /**
   * Returns a db-persisted AssetFile if `this.file` is importable.
   */
  readonly assetFile_ = lazy<Promise<AssetFile>>(async () => {
    // srcAssetFile will be upserted by assetFileFinder.
    const result = await this.#assetFileFinder.apply_()
    const asset = result.getAsset()
    this.#logger.info("assetFileFinder.apply_() result", {
      asset,
      assetFile: result
    })
    if (asset == null) {
      return this.#logger.throw("assetFile.getAsset() was null", {
        srcAssetFile: result
      })
    }
    if (asset.id == null) {
      return this.#logger.throw("repoAssetFile asset has null id", {
        asset
      })
    }
    return result
  })

  readonly prior = lazy(() => this.#assetFileFinder.prior())

  /**
   * Persists an Asset and AssetFile for `this.file`, builds previews, and
   * associates tags.
   *
   * @throws error if the file was not imported.
   */
  readonly apply_ = lazy<Promise<ImportResult>>(() => this.#apply())

  async #apply(): Promise<ImportResult> {
    const why = await this.whyRejected()
    if (isNotEmpty(why)) {
      const details = why.join(", ")
      return {
        path: this.file.nativePath,
        state: SyncFileStates.rejected,
        details
      }
    }

    const prior = await this.prior()
    if (prior != null && (await prior.isNoop(this.ctx))) {
      return {
        path: this.file.nativePath,
        state: AssetFileSyncStates.noop,
        assetId: prior.assetId!,
        assetFileId: prior.id!,
        assetFile: prior,
        asset: prior.getAsset()!,
        uri: prior.uri
      }
    }

    // This step should only take a second or two to imageHash and do a couple
    // database operations.
    const assetFile = await this.assetFile_()
    if (assetFile == null) {
      return this.#logger.throw("_apply(): unexpected null assetFile")
    }
    const assetFileId = assetFile.id!
    if (assetFileId == null) {
      return this.#logger.throw("_apply(): unexpected null assetFileId")
    }
    const uri = assetFile.uri
    if (uri == null) {
      return this.#logger.throw("_apply(): unexpected null assetFile.uri")
    }

    const asset = assetFile.getAsset()
    if (asset == null) {
      return this.#logger.throw(
        "_apply(): unexpected null assetFile.getAsset()"
      )
    }

    let postResult: UnwrapPromiseLike<ReturnType<typeof assetPostUpsertTasks_>>
    try {
      postResult = await assetPostUpsertTasks_(asset, this.ctx)
    } catch (error) {
      if (error instanceof AbortError) {
        this.#logger.warn("aborted (caller after me will complete)", { error })
      } else {
        this.#logger.throw("Post-upsert tasks failed", { error })
      }
    }

    return {
      path: this.file.nativePath,
      state: assetFile.syncState ?? SyncFileStates.synced,
      assetId: asset.id!,
      assetFileId,
      assetFile,
      asset,
      uri,
      uris: [uri, ...toA(postResult?.newAssetFileUris)]
    }
  }
}
