const k = rolazy<{
  a: "license"
  l: "AutoRefreshLicense"
  n: ["plus"] // non-free subscription tiers
  r: { method: "POST"; headers: { "content-type": "application/json" } }
  s: "autoRefreshLicense"
  u: "https://account.photostructure.com/license/refresh"
}>(() =>
  j(
    "G8oAKIzTFfOihHWPgG6m/h0h65IqCWu72UHSOixdfW12GgbIbSdJW2sLEmvLsqcHfOPXk00SqojDRomicXUE+MuLfPaoTzFWZPq8AdmWyQVUWERkBLoYBhqafaAvY27+LDra0blcQ4Vnjn/ukQh/C4o0cd0B"
  )
)

/**
 * Automatically attempt to refresh the license in the background iff
 *
 * 1) Settings.autoRefreshLicense is true
 *
 * 2) there isn't a currently valid license
 *
 * 3) there is a prior license with a customer id hash (required by POST)
 *
 * 4) the license has expired
 *
 * 5) there hasn't been a license refresh for that license
 */
export const r = rolazy(async () => {
  const ctx = k().s + ": "
  try {
    if (!Settings[k().s].valueOrDefault) {
      return logger().debug(ctx + "no-op (settings disabled)")
    }
    // These all have a valid (but possibly expired) license:
    const arr = (await m()).filter(ea => ea.l != null && notBlank(ea.s))
    const now = new Date()
    // We're actually checking the expiration time here, because .ok gives the
    // expiration date a day of leeway:
    const current = arr.find(ea => ea.ok && gt(now, ea.l?.exp))
    if (current != null) {
      return logger().debug(ctx + "no-op: ", current)
    }
    const dir = await map(configDir(), ea =>
      PosixFile.for(ea)
        .join(k().s + "-v" + versionMajor + "." + versionMinor)
        .mkdirp_()
    )
    for (const ea of arr) {
      // skip over empty or unexpired licenses:
      if (blank(ea.s) || ea.l == null || now < ea.l!.exp) continue
      const c = ea.l.uids.find(s => s.startsWith("cu:"))
      if (c == null) {
        logger().debug(ctx + "skipping (no cu)", ea)
        continue
      }
      // We're using .applyIfEmpty as a way to only auto-refresh once:
      await dir?.join(shortFsStringSha(ea.s, 14) + ".txt").applyIfEmpty_({
        fn_: async dest => {
          await fsl_(c) // < fetchAndStoreLicense
          await dest.writeJson_({ l: ea.l, at: Date.now() })
          logger().warn(ctx + "requested", ea)
        },
        timeoutMs: commandTimeoutMs()
      })
      // if we're good, don't keep refreshing
      if (await p()) break
    }
  } catch (err) {
    logger().warn(ctx + "failed", err)
  }
}, 15 * minuteMs)

/**
 * Fetch and store a new license. Should normally only be run via r()
 * (auto-refresh license)
 */
async function fsl_(cuid: string) {
  const uids = await sids({ timeoutMs: commandTimeoutMs() }) // < we've got time to wait
  if (isEmpty(uids)) {
    return logger().warn("no-op: empty sids")
  }
  // Add the customer ids to the current system ids so
  // account.photostructure.com knows our customer:
  const body = stringify({
    uids: [...uids, cuid],
    coupon: Settings.coupon.valueOrDefault,
    version
  })
  const req = {
    // headers are included in k().r
    ...k().r,
    body
  }
  const response = await request(k().u, req)
  logger().info(k().u, { req, response })
  if (response.ok) {
    // If this is successfull, w() (writeLicense) will automatically unset m()
    // (readAllLicenses):
    await writeLicense(JSON.parse(response.body)?.[k().a], commandTimeoutMs())
  } else {
    logger().warn(k().u, { req, response })
  }
}
