import { thenOrElse } from "../../core/async/Promise"
import {
  AssetFileVersion,
  AssetVersion
} from "../../core/PhotoStructureVersions"
import { stripPrefix } from "../../core/String"
import { TableName } from "../model/TableName"
import { QueueItem } from "./QueueItem"
import { StatsModel } from "./StatsModel"

// we can't Omit, because then we get all the Model fields too. :\
export type WorkItem = Pick<QueueItem, "contents">

export function assetFileUpdatesUri2QueueName(uri: string) {
  return `assetFileUpdates:${AssetFileVersion}:${uri}`
}

export function assetFileUpdatesQueueName2uri(queueName: string) {
  return stripPrefix(queueName, assetFileUpdatesUri2QueueName(""))
}
export function assetUpdatesUri2QueueName(uri: string) {
  return `assetUpdates:${AssetVersion}:${uri}`
}
export function assetUpdatesQueueName2uri(queueName: string) {
  return stripPrefix(queueName, assetUpdatesUri2QueueName(""))
}

export class Queue extends StatsModel {
  static override readonly $tableName: TableName = "Queue"
  static override readonly $uniqueColumnName = "name"
  id!: number
  name!: string

  static itemCountForQueues(queueNames: string[]) {
    return thenOrElse(
      QueueItem.dbl.pluckFirstf(q =>
        q
          .countDistinct("QueueItem.id")
          .join("Queue", "Queue.id", "QueueItem.queueId")
          .whereIn("Queue.name", queueNames)
      ),
      () => 0
    )
  }

  static truncate() {
    QueueItem.dbl.runf(q => q.delete())
    Queue.dbl.runf(q => q.delete())
  }

  items() {
    return QueueItem.ops().allf(q => q.where({ queueId: this.id! }))
  }

  itemCount(): number {
    return QueueItem.dbl.pluckFirstf<number>(q =>
      q.where({ queueId: this.id! }).count()
    )
  }

  upsertWorkItems(workItems: WorkItem[]): QueueItem[] {
    return QueueItem.ops().upsert(
      workItems.map(ea => ({ queueId: this.id!, ...ea }))
    )
  }
}
