import { end, EndableRanks } from "../../core/async/Endable"
import { EndableWrapper } from "../../core/async/EndableWrapper"
import { thenMap } from "../../core/async/Promise"
import { ee } from "../../core/event/EventEmitter"
import { PosixFile } from "../../core/fs/PosixFile"
import { clearReaddirCaches } from "../../core/fs/Readdir"
import { Latch } from "../../fe/Latch"
import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { FileProcessor } from "../sync-file/ImportResult"
import { runTagMaintenance } from "../tag/TagSql"
import { DirectorySync } from "./DirectorySync"
import { Sync } from "./Sync"
import { bestPathToSync, nativePathBlocklist, UriPathTimes } from "./SyncPaths"

/**
 * Manages running Sync jobs for all valid volumes or scan paths.
 */
export class SyncRunner extends EndableWrapper implements Sync {
  private readonly _doneLatch = new Latch()

  private currentSyncUri: Maybe<UriPathTimes>
  private currentSync: Maybe<Sync>

  constructor(readonly fileProcessor: FileProcessor) {
    super("SyncRunner", () => this.endCurrentSync(), EndableRanks.first)
    void this.maybeSetupNextSync()
  }

  async progress() {
    return thenMap(this.currentSync, ea => ea.progress())
  }

  isDone(): boolean {
    return this._doneLatch.isSettled()
  }

  donePromise() {
    return this._doneLatch.promise
  }

  private endCurrentSync(): Promise<void> {
    const s = this.currentSync
    this.currentSync = undefined
    this.currentSyncUri = undefined
    return end(s)
  }

  private async setSyncPath(upt: UriPathTimes) {
    if (this.ended) return

    await clearReaddirCaches()

    // Make sure we don't fight the fs cache:
    ee().emit("clearCache")

    const s = DirectorySync.for({
      root: PosixFile.for(upt.nativePath),
      rootUri: upt.uri,
      fileProcessor: this.fileProcessor
    })

    void this.syncAndRunNext(upt, s)
  }

  async syncAndRunNext(
    upt: UriPathTimes,
    maybeDirSync: PromiseMaybe<DirectorySync>
  ) {
    try {
      const sync = (this.currentSync = await maybeDirSync)
      this.currentSyncUri = sync == null ? undefined : upt
      if (sync != null) {
        this.logger.info("syncAndRunNext(): waiting for completion...", upt)
        await sync.donePromise()
        this.logger.info("syncAndRunNext(): done", upt)
      } else {
        nativePathBlocklist.add(upt.nativePath)
        this.logger.error("syncAndRunNext(): no-op (CHECK ME!)", upt)
      }
    } catch (error) {
      this.logger.error("syncAndRunNext(): caught", { error })
    }
    return this.maybeSetupNextSync()
  }

  /**
   * Make sure we're working on the "best" directory.
   */
  async maybeSetupNextSync() {
    if (this.ended || this._doneLatch.isResolved()) {
      this.logger.info("maybeSetupNextSync(): no-op", {
        this_ended: this.ended,
        doneLatch_state: this._doneLatch.state()
      })
      return { newPath: null, msg: "ended" }
    }

    const newBest = await bestPathToSync()
    if (newBest == null) {
      void this._doneLatch.resolve()
      this.logger.info(
        "maybeSetupNextSync(): bestPathToSync() returned null, so we're done."
      )
      await this.endCurrentSync()
      await runTagMaintenance()
      return { newPath: null, msg: "all paths are synced" }
    }

    const currentSyncPath = this.currentSyncUri?.nativePath
    // If we're already working on the best, no-op
    if (currentSyncPath != null && currentSyncPath === newBest.nativePath) {
      return {
        newPath: currentSyncPath,
        msg: "no-op, current path is already the best"
      }
    } else {
      this.logger.info(
        "maybeSetupNextSync(): bestPathToSync() returned a different sync: switching to newBest",
        {
          currentSync: this.currentSyncUri,
          newBest
        }
      )
      await this.setSyncPath(newBest)
      return { newPath: newBest.nativePath, msg: newBest.why }
    }
  }
}
