import { Maybe } from "../../fe/MaybeTypes"
import { StatsModel } from "./StatsModel"

export class QueueItem extends StatsModel {
  static override readonly $tableName = "QueueItem"
  static override readonly $uniqueColumnName = "queueId,contents"
  id!: number
  queueId!: number
  contents!: string
  // HEY FUTURE ME: if you add a field here, add it to the WorkItem interface, too.

  eql(qi: Maybe<QueueItem>) {
    return (
      qi != null &&
      ((this.id != null && this.id === qi.id) ||
        (this.contents === qi.contents && this.queueId === qi.queueId))
    )
  }
}
