import { Command } from "commander"
import { Settings } from "../../core/settings/Settings"
import {
  writeLibrarySettings_,
  writeSystemSettings_
} from "../../core/settings/SettingsIO"
import { blank } from "../../fe/Blank"
import { isTrue } from "../../fe/Boolean"
import { Obj } from "../../fe/Object"
import { osConfigDirs } from "../dir/ConfigDir"
import { CommandPlugin } from "./CLI"

export const WriteSettingsArg: CommandPlugin = {
  beforeParse: (cmd: Command) =>
    cmd.option("--write-settings", "Write out system and library settings"),

  afterParse: async (opts: Obj) => {
    if (isTrue(opts.writeSettings)) {
      const systemSettingsFile = await writeSystemSettings_()
      if (systemSettingsFile == null) {
        console.info(
          "--write-settings: Failed write system settings: no system configuration directory was found.",
          { checkedDirectories: osConfigDirs() }
        )
      } else {
        console.info(
          `--write-settings: Wrote system settings to ${systemSettingsFile}`
        )
      }

      if (blank(Settings.libraryDir.valueOrDefault)) {
        console.error(
          "--write-settings: Failed to write library settings: no PS_LIBRARY_DIR is set."
        )
      } else {
        const librarySettingsFile = await writeLibrarySettings_()
        console.info(
          `--write-settings: Wrote library settings to ${librarySettingsFile}`
        )
      }
    }
  }
}
