import { lazy } from "../../core/Lazy"
import { EndableRanks, endAll, ending } from "../../core/async/Endable"
import { EndableWrapper } from "../../core/async/EndableWrapper"
import { onError } from "../../core/error/OnError"
import { ETA } from "../../core/math/ETA"
import { isNotEmpty, uniq } from "../../fe/Array"
import { mapNotBlankOr } from "../../fe/Blank"
import { secondMs } from "../../fe/Date"
import { stringify } from "../../fe/JSON"
import { Latch } from "../../fe/Latch"
import { Maybe } from "../../fe/MaybeTypes"
import { gt0, sigFigs } from "../../fe/Number"
import { fmt } from "../../fe/Units"
import { RebuildingURI } from "../../fe/api/ProgressState"
import { toS } from "../../fe/toS"
import { Library } from "../Library"
import { WorkQueue } from "../WorkQueue"
import { Asset } from "../model/Asset"
import { Progress } from "../model/Progress"
import { isUpdateSuccessResult } from "../sync-file/UpdateResult"
import { UpdateProcessor } from "../sync-file/UpdateTask"
import { mkAssetPostUpsertQueue } from "./AssetPostUpsertQueue"
import { mkAssetUpdateQueue } from "./AssetUpdateQueue"
import { Sync } from "./Sync"
import { outdatedAssetCount } from "./UpdateQueue"

/**
 * Updating file metadata...
 * Refreshing assets and previews...
 */
export class ModelDbUpdater extends EndableWrapper implements Sync {
  readonly _done = new Latch()
  readonly eta = new ETA()
  readonly updatedAssetIds = new Set<number>()

  private assetFileUpdateQueue: Maybe<WorkQueue>
  // private afq = new SimpleAssetFileUpdater() // TODO: use this instead?
  private postUpsertQueue: Maybe<WorkQueue>
  private assetUpdateQueue: Maybe<WorkQueue>

  constructor(
    readonly opts: {
      processor: UpdateProcessor
    }
  ) {
    super("ModelDbUpdater", () => this.onEnd(), EndableRanks.first)
    void this.run()
  }

  readonly _progress = lazy(
    () => Progress.insertNew({ uri: RebuildingURI, volume: "" })
    // NO TTL! This needs to be the same instance for the same process.
  )

  readonly saveSyncProgress = lazy(
    () => Progress.saveSyncState(this),
    // lazy with 1s db write throttle:
    secondMs
  )

  private async run() {
    try {
      if (this.ended || ending()) return

      // this should be a no-op (SyncService.setup should have awaited it
      // already):
      await Library.instanceRequired().ready

      // this.afq =  await mkAssetFileUpdateQueue(this.opts.processor)
      this.assetFileUpdateQueue?.ee.on("processed", this.saveSyncProgress)

      this.logger.info("Starting asset file updates", {
        afqName: this.assetFileUpdateQueue?.name,
        afqEnded: this.assetFileUpdateQueue?.ended,
        afqRunnable: this.assetFileUpdateQueue?.isRunnable(),
        workStats: this.assetFileUpdateQueue?.p?.stats()
      })

      await this.assetFileUpdateQueue?.awaitDrain()

      this.logger.info("Completed asset file updates", {
        afqName: this.assetFileUpdateQueue?.name,
        afqEnded: this.assetFileUpdateQueue?.ended,
        afqRunnable: this.assetFileUpdateQueue?.isRunnable(),
        workStats: this.assetFileUpdateQueue?.p?.stats()
      })

      await this.assetFileUpdateQueue?.end()

      this.eta.clear()
      await this.saveSyncProgress()

      if (this.ended || ending()) return

      // pq will be getting jobs from aq, so it needs to be started first...
      this.postUpsertQueue = mkAssetPostUpsertQueue()
      this.postUpsertQueue?.ee.on("processed", this.saveSyncProgress)

      this.assetUpdateQueue = await mkAssetUpdateQueue(assetId =>
        this.assetUpdater(assetId)
      )
      this.assetUpdateQueue?.ee.on("processed", this.saveSyncProgress)

      this.logger.info("Starting asset updates", {
        aqName: this.assetUpdateQueue?.name,
        aqEnded: this.assetUpdateQueue?.ended,
        workStats: this.assetFileUpdateQueue?.p?.stats()
      })

      this.logger.info("run(): waiting for asset updates to complete...")
      await this.assetUpdateQueue?.awaitDrain()
      await this.saveSyncProgress()

      await this.assetUpdateQueue?.end()

      // At this point the AssetUpdateQueue will have enqueued all necessary preview work. Wait for it to empty out, and then end.
      this.logger.info("run(): waiting for asset previews to complete...")
      await this.postUpsertQueue.awaitDrain()
      await this.postUpsertQueue.end()

      // Make sure the "we're done now" progress gets persisted:
      await this.saveSyncProgress.refresh()

      this.logger.info("run(): finished")

      if (!this.ended) {
        // We only mark _done on completion:
        void this._done.resolve()
        await Progress.saveSyncState(this)
      }
    } catch (err: any) {
      onError("ModelDbUpdater.run() failed", err)
      void this._done.reject(err)
    }
  }

  private readonly _outdatedAssetCount = lazy(() => outdatedAssetCount())

  outdatedAssetCount() {
    return (
      this.assetUpdateQueue?.pendingWorkCount() ?? this._outdatedAssetCount()
    )
  }

  private async assetUpdater(assetId: number) {
    this.updatedAssetIds.add(assetId)
    const result = await this.opts.processor({
      assetId,
      skipPreviews: true,
      skipAssetTagging: true
    })
    this.logger.info("ModelDbUpdater.assetUpdater(" + assetId + ")", { result })

    if (isUpdateSuccessResult(result)) {
      // not true that it was skipped === skipped was false
      this.postUpsertQueue!.enqueueWork([{ contents: stringify({ assetId }) }])

      const assetIdsToUpdate = result?.assetIdsToUpdate
      if (isNotEmpty(assetIdsToUpdate)) {
        // Don't update the same asset on the same rebuild run:
        const newIds = assetIdsToUpdate.filter(
          id => !this.updatedAssetIds.has(id)
        )
        if (isNotEmpty(newIds)) {
          Asset.dbl.runf(q => q.whereIn("id", newIds).update({ version: 0 }))
        }
        if (this.assetUpdateQueue == null || this.assetUpdateQueue.ended) {
          this.logger.warn(
            "non-empty assetIdsToUpdate, and AssetQueue is null/ended"
          )
        } else {
          this.assetUpdateQueue.enqueueWork(
            newIds.map(ea => ({ contents: toS(ea) }))
          )
        }
      }
    }

    return result
  }

  private async onEnd() {
    // NOTE: DO NOT resolve this._done here! end() does not mean done!
    await endAll(
      this.assetFileUpdateQueue,
      this.postUpsertQueue,
      this.assetUpdateQueue
    )
    this.assetFileUpdateQueue =
      this.postUpsertQueue =
      this.assetUpdateQueue =
        undefined
  }

  isNoOp() {
    return (
      !gt0(this.assetFileUpdateQueue?.processedCount()) &&
      !gt0(this.postUpsertQueue?.processedCount()) &&
      !gt0(this.assetUpdateQueue?.processedCount())
    )
  }

  isDone() {
    return !this._done.isPending()
  }

  donePromise() {
    return this._done.promise
  }

  async progress() {
    // We might not need to have done anything. If the queues are empty, return
    // undefined.
    if (this.ended || this.isNoOp()) return
    const p = this._progress()

    if (this.isDone()) {
      return [
        p.assignFromPojo({
          state: "done",
          hed: "Finished rebuilding your library ",
          dek: [],
          completePct: 100,
          incompletePct: 0,
          scanningPct: 0
        })
      ]
    }
    const scanningPct = 0 // everything's been scanned, so 0.

    const afq = this.assetFileUpdateQueue
    if (afq != null && !afq.isDone()) {
      const done = afq.processedCount()
      const assetFilesPending = afq.pendingWorkCount()
      if (gt0(afq.processRate.msPerEvent)) {
        this.eta.push(
          ((await this.outdatedAssetCount()) + assetFilesPending) *
            afq.processRate.msPerEvent
        )
      }
      const { completePct } = afq.percents()
      const est = this.eta.fmtEstimate()
      const hed =
        "Rebuilding your library" + mapNotBlankOr(est, ea => ", " + ea, "")

      return [
        p.assignFromPojo({
          state: "processing",
          hed,
          dek: [
            `Updating file metadata (${fmt(done)} processed, ${fmt(
              assetFilesPending
            )} remain)`
          ],
          completePct: 0,
          incompletePct: completePct,
          scanningPct
        })
      ]
    } else if (this.assetUpdateQueue != null && this.postUpsertQueue != null) {
      const aqPending = this.assetUpdateQueue.pendingWorkCount()
      // Ignore PQ: AQ is the critical path.
      this.eta.push(
        aqPending * (this.assetUpdateQueue.processRate.msPerEvent ?? secondMs)
      )

      const todo = aqPending
      const done = this.assetUpdateQueue.processedCount()
      const complete = done / (todo + done)
      const completePct = sigFigs(100 * complete, 2)
      const est = this.eta.fmtEstimate()
      const hed =
        "Rebuilding your library" + mapNotBlankOr(est, ea => ", " + ea, "")
      return p.assignFromPojo({
        state: "processing",
        hed,
        dek: uniq([
          `Refreshing assets and previews (${fmt(done)} processed, ${fmt(
            aqPending
          )} remain)`
        ]),
        completePct,
        incompletePct: 100 - completePct,
        scanningPct
      })
    }
    return
  }
}
