import { Command } from "commander"
import { CommandPlugin } from "../../core/cli/CLI"
import { Settings } from "../../core/settings/Settings"
import { isTrue } from "../../fe/Boolean"
import { Obj } from "../../fe/Object"

/**
 * Used by sync and sync-file
 */
export const RebuildArg: CommandPlugin = {
  beforeParse: (cmd: Command) =>
    cmd.option(
      "--rebuild",
      "Rebuild your library by re-importing all your photos and videos (slow!) https://photostructure.com/faq/sync-vs-rebuild/"
    ),
  afterParse: async (opts: Obj) => {
    if (isTrue(opts.rebuild)) {
      Settings.forceRebuildLibrary.envValue = true
    }
  }
}
