import { lazy } from "../../core/Lazy"
import { mkLogger } from "../../core/Logger"
import { valpath } from "../../core/Object"
import { firstSubstringIgnoreCase } from "../../core/String"
import { mapAsyncSerial } from "../../core/async/Promise"
import { parseJsonDate } from "../../core/date/ExtendedDate"
import { mapValidDate } from "../../core/date/ValidDate"
import { PosixFile } from "../../core/fs/PosixFile"
import { Settings } from "../../core/settings/Settings"
import { readRawTags } from "../../core/tags/ExifTool"
import { splitKeywords } from "../../core/tags/KeywordTags"
import { commandTimeoutMs } from "../../core/volumes/VolumeTtls"
import { isEmpty, uniqBy } from "../../fe/Array"
import { blank } from "../../fe/Blank"
import { flatten } from "../../fe/Flatten"
import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { TagPath, TagRef, TagRoots } from "../../fe/api/Tag"
import { toS } from "../../fe/toS"

const logger = lazy(() => mkLogger("curators.TypeTagger"))

export async function tagFilesWithAlbums(
  files: PosixFile[]
): Promise<TagPath[]> {
  if (isEmpty(Settings.tagAlbumFilenames.values)) {
    return []
  }

  const dirs = uniqBy(
    files.map(ea => ea.parent()),
    ea => ea.nativePath
  )
  const arr = flatten(
    dirs.map(dir => Settings.tagAlbumFilenames.values.map(ea => dir.join(ea)))
  )

  return mapAsyncSerial({
    name: "tagFilesWithAlbums",
    arr,
    f: albumFromFile,
    timeoutMs: commandTimeoutMs()
  })
}

export async function albumFromFile(
  file: PosixFile | string
): PromiseMaybe<TagPath> {
  return albumFromTags(await readRawTags(file, false))
}

export function albumFromTags(t: any): Maybe<TagPath> {
  if (t == null) return

  const title = valpath(t, Settings.tagAlbumTitle.valueOrDefault)
  const desc = valpath(t, Settings.tagAlbumDescription.valueOrDefault)

  const titlePath: string[] = blank(title)
    ? []
    : Settings.tagAlbumTitleHierarchies.valueOrDefault
    ? splitKeywords(title)
    : [title]

  const whyExclude = isEmpty(titlePath)
    ? "(blank title)"
    : firstSubstringIgnoreCase(
        Settings.tagAlbumsExcluded.values,
        [toS(title), toS(desc)].join(" ")
      )

  const result: Maybe<TagPath> =
    whyExclude == null ? [TagRoots.Albums, ...titlePath] : undefined

  if (result != null) {
    const tagref: TagRef = {
      name: result[result.length - 1] as string,
      description: desc
    }

    mapValidDate(
      parseJsonDate(valpath(t, Settings.tagAlbumDate.valueOrDefault) as any),
      (d: Date) => {
        tagref.releasedAt = d.getTime()
      }
    )
    result[result.length - 1] = tagref
  }

  return logger().tap({
    level: "info",
    msg: "albumFromFile()",
    result,
    meta: { title, desc, whyExclude }
  })
}
