import { lazy } from "../../core/Lazy"
import { mkLogger } from "../../core/Logger"
import { capitalize } from "../../core/String"
import { mapAsyncSerial } from "../../core/async/Promise"
import { PosixFile } from "../../core/fs/PosixFile"
import { readMimeType } from "../../core/tags/ReadMimeType"
import { compact, compactBlanks, uniq } from "../../fe/Array"
import { blank } from "../../fe/Blank"
import { map, orElse } from "../../fe/Maybe"
import { firstDefinedThunk } from "../../fe/Thunk"
import { TagRoots } from "../../fe/api/Tag"

const logger = lazy(() => mkLogger("curators.TypeTagger"))

export async function typeTagFiles(files: PosixFile[]): Promise<string[][]> {
  const mimetypes = uniq(
    await mapAsyncSerial({ name: "typeTagFiles", arr: files, f: readMimeType })
  )
  return compact(mimetypes.map(mimeTypeToTag))
}

const Cases = new Map(
  ["M2TS", "JPEG", "QuickTime"].map(ea => [ea.toLowerCase(), ea])
)

export function mimeTypeToTag(mt: string) {
  const [t, st] = mt.split("/")
  if (blank(t) || blank(st)) return

  return logger().tap({
    msg: "mimetypeToTag(" + mt + ")",
    result: map(friendlySubtype(st, mt), arr =>
      compactBlanks([TagRoots.Type, capitalize(t), ...arr])
    )
  })
}

const Subtypes = lazy(
  () =>
    new Map<string, string[]>([
      ["image/x-adobe-dng", ["Raw", "DNG"]],
      ["image/x-fuji-raf", ["Raw", "Fujifilm"]],
      ["video/m2ts", ["MPEG-2"]],
      ["video/mp2t", ["MPEG-2"]],
      ["video/mp4", ["MPEG-4"]],
      ["video/quicktime", ["QuickTime"]],
      ["video/x-msvideo", ["AVI"]]
    ])
)

export function friendlySubtype(subtype: string, fullMimeType: string) {
  return firstDefinedThunk([
    () => Subtypes().get(fullMimeType.trim().toLowerCase()),
    () =>
      map(subtype.match(/^x-(\w{4,})-\w{3}/), m => ["Raw", capitalize(m[1])]),
    // () => map(subtype.match(/^\w{3,4}/), () => [subtype.toUpperCase()]),
    () => [
      fixCase(subtype.replace(/^(?:x-|vnd\.|prs\.)/i, "").replace(/-/g, " "))
    ]
  ])!
  // if (RawSubtypes().has(s.trim())) {
  //   return ["Raw", capitalize(s.split("-")[1])]
  // }
  // const c = Cases.get(s.toLowerCase())
  // if (c != null) return [c]
  // return s.replace(/^x-/i, "").split("-").map(fixCase)
}

export function fixCase(s: string) {
  if (s.match(/[a-z\d]{3,4}/i) != null) {
    return s.toUpperCase()
  }
  return orElse(Cases.get(s.trim().toLowerCase()), () => capitalize(s))
}
