import { lazy } from "../../core/Lazy"
import { secondMs } from "../../fe/Date"
import { clamp } from "../../fe/Number"
import { isTest } from "../NodeEnv"
import { EndableRanks } from "../async/Endable"
import { EndableInterval } from "../async/EndableInterval"
import { oneAtATime } from "../async/OneAtATime"
import { Settings } from "../settings/Settings"
import { doNotRun } from "../work/DoNotRun"
import { DirectoryEntry } from "./DirectoryEntry"
import { DirectoryIterator } from "./DirectoryIterator"
import { containedByNativePath, splitNativePath } from "./Path"

/**
 * Facades a DirectoryIterator to run periodically for only a set time slice.
 */
export class DirectoryWalker extends EndableInterval {
  // lazy so tests can muck with it:
  static IntervalMs = lazy(() => 2 * secondMs)

  runMs: number

  readonly highPriority = true // the only high priority chunked worker!
  private readonly iterator: DirectoryIterator
  readonly startAtNativePath?: string[]

  constructor(
    readonly root: DirectoryEntry,
    readonly fileHandler: (files: DirectoryEntry[]) => any,
    readonly directoryListener: (dir: DirectoryEntry) => any,
    startAtNativePath?: string,
    runMs?: number
  ) {
    super({
      name: "dir.DirectoryWalker(" + root.nativePath + ")",
      callback: () => this.runChunk(),
      intervalMs: DirectoryWalker.IntervalMs(),
      rank: EndableRanks.first
    })
    this.runMs =
      runMs ??
      (clamp(1, 100, Settings.cpuBusyPercent.valueOrDefault) / 100) *
        DirectoryWalker.IntervalMs()
    if (startAtNativePath != null) {
      if (
        containedByNativePath({
          ancestor: this.root.nativePath,
          descendant: startAtNativePath
        })
      ) {
        this.startAtNativePath = splitNativePath(startAtNativePath)
      } else {
        this.logger.error("bad startAtNativePath: ignoring.", {
          root: this.root.nativePath,
          startAtNativePath
        })
      }
    }
    this.iterator = new DirectoryIterator(root, this)
    void this.runChunk()
  }

  readonly runChunk = oneAtATime({
    runLaterIfBusy: false,
    fn: async () => {
      if (!isTest && doNotRun(this)) return
      const result = await this.iterator.next(Date.now() + this.runMs)
      this.logger.debug("runSlice()", { result })
      if (result?.done === true) {
        this.logger.info("runSlice(): completed iteration")
        void this.end()
      }
    }
  })
}
