export interface SyncServiceOptions {
  setupExecutor: boolean
}

export type SyncServiceStatus = UnwrapPromiseLike<
  ReturnType<SyncService["status"]>
>

export const HandledTasks: TaskName[] = [
  "runTagMaintenance",
  "checkOperations",
  "repairAsset",
  "repairAssetFile"
]

/**
 * SyncService hosts the AllDrivesSync or DirectorySync instances, and feeds a
 * batch-cluster-managed set of sync-file processes (which do the actual work
 * of importing files into PhotoStructure)
 */
export class SyncService {
  private readonly logger = mkLogger("SyncService")
  readonly service: Service
  readonly exitWhenDone: boolean
  private readonly progressUpdater = lazy(
    () => new ProgressUpdater(() => this.sync())
  )
  #processCount = 0 // used for tmp file cleanups

  #work = new Promises("SyncService")

  /**
   * @param importPath if provided, will override the library settings for
   * what drives or directories to import.
   */
  constructor() {
    this.exitWhenDone =
      Settings.exitWhenDone.valueOrDefault ||
      Settings.argvScanPaths.isNotEmpty()

    addTaskProcessor(this.taskListeners)

    this.service = new Service({
      name: "sync",
      stdinReceiver: this.#stdinReceiver
    })

    void this.#setup().catch(cause =>
      onError(
        new WrappedError("SyncService.setup() failed", {
          cause,
          fatal: true
        })
      )
    )
  }

  readonly #stdinReceiver = async (stdin: string) => {
    this.logger.info("stdinReceiver", { stdin })
    for (const input of compactBlanks(splitLines(stdin))) {
      const cmd = parseJSON(input) as any
      // ubuntu adds single quotes when drag-and-dropping: strip those.
      const path = stripPrefixSuffix(
        toS(cmd?.path ?? cmd?.file ?? cmd?.nativePath ?? cmd).trim(),
        { prefix: "'", suffix: "'" }
      )
      if (notBlank(path)) {
        stdoutWrite(
          await this.service.promises.enqueue({
            name: "processFile",
            l: () => this.processFile(path)
          })
        )
      }
    }
  }

  async done(): Promise<boolean> {
    const s = await this.sync()
    return s == null || s.isDone()
  }

  #onProcessed() {
    this.#processCount++
    if (this.#processCount % 50 === 0) {
      void cleanup_()
    }
  }

  async status() {
    const s = await this.sync.prior()
    return {
      libraryDir: Settings.libraryDir.value,
      ending: ending(),
      done: await this.done(),
      sync: s?.name,
      progress: await s?.progress(),
      summary: HealthCheck.summary(),
      jobs: this.service.promises.stats(),
      processCount: this.#processCount
    }
  }

  readonly processFile: FileProcessor = async (
    nativePath: string
  ): Promise<ImportResult> => {
    const currentProcessing = this.#work.pendingWithName("processFile")
    this.logger.info("processFile()", {
      nativePath,
      currentPaths: currentProcessing.map(ea => ea.payload)
    })
    return (
      // We're not _asking)/enqueuing, we're _just pushing_ so cleanup tasks
      // know that we're still working on something.

      // Job throttling happens before this method gets called.
      this.#work.push(
        "processFile",
        () => this.#processFile(nativePath, Settings.maxRetries.valueOrDefault),
        nativePath
      )
    )
  }

  async #processFile(
    nativePath: string,
    retries: number
  ): Promise<ImportResult> {
    this.logger.info("#processFile()", { nativePath, retries })

    // WorkQueue will already be promise-throttling this.
    try {
      const result = await importFileToResult(PosixFile.for(nativePath))
      if (isTest) {
        stdoutWrite(result)
      }

      if (isErrorResult(result)) {
        this.logger.warn("#processFile() error result", {
          nativePath,
          result,
          retries
        })

        return false !== isRetriableError(result.error) && retries > 0
          ? this.#processFile(nativePath, retries - 1)
          : result
      } else {
        return result
      }
    } catch (error: any) {
      this.logger.error("#processFile() caught error", {
        nativePath,
        retries,
        error
      })
      const we = toWrappedError(error, { path: nativePath })

      if (false !== error.retriable && retries > 0) {
        await delay(Settings.minDelayBetweenRetriesMs.valueOrDefault)
        return this.#processFile(nativePath, retries - 1)
      } else {
        onError("Failed to process file " + nativePath, { we })
        return {
          path: nativePath,
          state: SyncFileStates.failed,
          error: errorToS(we)
        }
      }
    } finally {
      this.#onProcessed()
    }
  }

  async #handleTask(task: StoredTask<any>) {
    // IMPORTANT! If we're using enqueue here, we don't want anything to
    // subsequently work.enqueue or work.push, as that could cause deadlocks.
    this.logger.info("enqueueing task", { task })

    return this.#work.enqueue({
      name: "#handleTask(" + task?.name + ")",
      l: async () => {
        await Library.instanceReady()
        this.logger.info("handling task", { task })

        switch (task.name) {
          case "runTagMaintenance":
            await runTagMaintenance()
            return "success"

          case "checkOperations":
            await checkPendingOperations()
            return "success"

          case "repairAsset":
            const ra: StoredTask<"repairAsset"> = task
            await handleUpdateCommand({
              forceSync: true,
              skipPreviews: false,
              forceRebuildPreviews: true,
              ...ra.args[0]
            })
            return "success"

          case "repairAssetFile":
            const raf: StoredTask<"repairAssetFile"> = task
            await handleUpdateCommand({
              forceSync: true,
              ...raf.args[0]
            })
            return "success"

          default:
            throw new InternalError("unexpected task: " + stringify(task))
        }
      }
    })
  }

  readonly taskListeners: TaskProcessor = {
    name: "SyncService",
    accept: (task: StoredTask<any>) => HandledTasks.includes(task.name),
    apply: async (task: StoredTask<any>) => {
      try {
        return this.logger.tap({
          msg: "task complete",
          level: LogLevels.warn,
          result: await this.#handleTask(task),
          meta: { task }
        })
      } catch (error) {
        this.logger.warn("task failed", { task, error })
        throw error
      }
    }
  }

  readonly #setup = lazy(async () => {
    // setTimeoutHandler(onTimeout)
    // Note that if the library path changes, we're expecting `main` to shut
    // us down--we don't need to do anything.
    ee().on("updateReadyToInstall", () => {
      this.logger.warn("We're about to upgrade: shut down ASAP!")
      // This is our exit(), not node's exit--it will end Endables:
      void exit({
        reason: "Upgrade is ready--exiting now.",
        status: 0
      })
    })

    if (await rejected(this.service.ready)) {
      this.logger.warn("setup(): service.ready was rejected, exitting.")
      return
    }

    this.logger.info("setup()", {
      library: Settings.libraryDir.valueOrDefault,
      exitWhenDone: this.exitWhenDone,
      argvScanPaths: Settings.argvScanPaths.value,
      scanPaths: Settings.scanPaths.value,
      argv: process.argv
    })

    await libraryHealthCheckSetup()

    // Just by invoking these lazy instances, the cleanup intervals will get
    // scheduled:
    imageCacheCleanup()
    readdirCleanup()

    await setupShimDelegates()
    installBadShaShim()

    await m()
    // If we *can* refresh the license, let's not start before we've got it...
    await thenOrTimeout(r(), commandTimeoutMs())

    const library = Library.instanceRequired()

    await library.ready

    // Are all the health checks ok?
    const summary = await HealthCheck.awaitSettled()
    if (isHealthCheckLevelFatal(summary.level, serviceName())) {
      await exit({
        reason: "Health check failed: " + summary.msg.join(": "),
        status: 1
      })
    }

    // before we start sync, let's also wait for the janitorial tasks to
    // complete.

    await runTagMaintenance()

    listenForInvalidFiles()

    if (Settings.forceRebuildLibrary.valueOrDefault) {
      this.logger.info(
        "setup(): forceRebuildLibrary is true: setting all assets to require updates..."
      )
      forceRebuildLibrary()
    }

    if (Settings.forceSync.valueOrDefault) {
      await rmStatDbDirs()
      ee().emit("clearCache")
      Operation.ensurePendingOp({ name: "forceRestartSync", value: "" })
    }

    if (Settings.dropWorkQueues.valueOrDefault) {
      this.logger.warn("Deleting all prior work queues...")
      QueueItem.dbl.runf(q => q.delete())
      ee().emit("clearCache")
    }

    await SharedState.instance()?.setup()

    // Wait for any pending operations, but not for too long:
    await thenOrTimeout(checkPendingOperations(), minuteMs)

    await this.sync()

    // Now we can set up the progress updater:
    this.progressUpdater()

    // Only add this handler once we've got a sync to play with. This overrides
    // the default status handler.
    this.service.setInputHandler("--force-rebuild-library", async () => {
      this.logger.warn("--force-rebuild-library requested")
      await end(this.modelDbUpdater.clear())
      await end(this.sync.clear())
      forceRebuildLibrary()
      await this.sync()
      stdoutWrite({ forceRebuildLibrary: "started" })
    })
    this.service.setInputHandler("--recount-tags", () => updateAllTagCounts())
    this.service.setInputHandler("--check-sync", async () =>
      stdoutWrite({ checkSync: await this.maybeRestartSyncOnVolumeChange() })
    )

    this.service.setInputHandler("--cleanup", async () => cleanup_())
    this.service.setInputHandler("--clear-cache", async () =>
      ee().emit("clearCache")
    )

    if (isTest) {
      onProgressEvt.setShim(async ea => console.log(stringify(ea)))
    }

    ee().on("volumesChanged", () => this.maybeRestartSyncOnVolumeChange())

    new EndableInterval({
      name: "SyncService.maybeRestartSync()",
      callback: () => this.maybeRestartSync(),
      intervalMs: hourMs,
      initialDelayMs: hourMs,
      rank: EndableRanks.first
    })

    new EndableInterval({
      name: "SyncService.setVolumesTTL()",
      callback: () => setVolumesTTL(),
      intervalMs: dayMs / 4,
      initialDelayMs: 5 * minuteMs,
      rank: EndableRanks.first
    })

    new EndableWrapper(
      "SyncService end status",
      async () => this.logger.info("end status", await this.status()),
      EndableRanks.first
    )
  })

  async cancelSync() {
    const s = await this.sync()
    if (s != null && !s.isDone()) {
      this.logger.info("cancelSync(): ending current sync...")
      await s.end()
    } else {
      this.logger.info("cancelSync(): No sync to end.")
    }
  }

  #wrapWork<T extends { donePromise(): Promise<any> }>(
    name: string,
    t: Maybe<T>,
    reTag = false
  ): Maybe<T> {
    if (t == null) return
    void this.#work
      .push(name, t.donePromise())
      .then(
        () => {
          this.logger.info(name + " completed.")
        },
        err => onError(name + " failed", err)
      )
      .finally(async () => {
        if (reTag)
          await this.#work.push(
            "forceRunTagMaintenance",
            forceRunTagMaintenance()
          )
        void this.sync.refresh()
      })
    return t
  }

  private readonly modelDbUpdater = lazy(() => {
    if (Settings.noModelUpdates.valueOrDefault) {
      this.logger.info("modelDbUpdater: skipUpdates is set.")
      return
    } else {
      return this.#wrapWork(
        "modelDbUpdater",
        new ModelDbUpdater({
          // WorkQueue will already be promise-throttling this:
          processor: handleUpdateCommand
        }),
        true
      )
    }
  })

  private readonly directoryTaggerOp = lazy(() =>
    this.#wrapWork("directoryTaggerOp", new DirectoryTaggerOperation())
  )

  async restartSync() {
    await end(this.sync.clear(), minuteMs) // in case it's in the middle of a sync-file
    return this.sync()
  }

  async maybeRestartSyncOnVolumeChange() {
    const s = await this.sync.prior()
    if (s == null || s instanceof SyncRunner) {
      // if the sync runner is done, make a new one:
      if (s == null || s.ended || s.isDone()) {
        this.logger.info(
          "maybeRestartSyncOnVolumeChange(): prior sync ended, restarting..."
        )
        await this.sync.refresh()
        return "refresh"
      } else {
        this.logger.info(
          "maybeRestartSyncOnVolumeChange(): calling maybeSetupNextSync()..."
        )
        const result = await s.maybeSetupNextSync()
        return stringify(result)
      }
    } else {
      this.logger.info(
        "maybeRestartSyncOnVolumeChange(): no-op, current sync is " + s.name
      )
      return "noOp:" + s.name
    }
  }

  async maybeRestartSync() {
    const lastSetAgoMs = this.sync.lastSetAgoMs()
    if (lt(lastSetAgoMs, ShortCommandTimeoutMs)) {
      this.logger.info("maybeRestartSync(): sync was touched recently.", {
        lastSetAgoMs
      })
      return
    }
    const s = await this.sync.prior()
    if (s instanceof SyncRunner && s.ended) {
      this.logger.info("maybeRestartSync(): SyncRunner ended. Restarting.")
      // if the sync runner is done, make a new one:
      void this.sync.refresh()
    } else {
      this.logger.info(
        "maybeRestartSync(): We're busy doing something else or the current SyncRunner hasn't ended. no-op."
      )
    }
  }

  readonly sync = lazy(async () => {
    setIsSyncing(true)
    await Library.instanceRequired().ready

    if (Settings.argvScanPaths.isEmpty()) {
      const mdu = this.modelDbUpdater()
      if (false === mdu?.isDone()) {
        return mdu
      }
      const dto = this.directoryTaggerOp()
      if (false === dto?.isDone()) {
        return dto
      }
    }

    // If we're given argv scan paths, give those to Syncs to handle--they
    // know how to handle files and directories.

    const argvPaths = Settings.argvScanPaths.value

    const result = isNotEmpty(argvPaths)
      ? Syncs.forPaths(argvPaths, this.processFile)
      : new SyncRunner(this.processFile)
    void this.waitForSyncAndCleanup(result)

    return result
  })

  async waitForSyncAndCleanup(s: Maybe<Syncs | SyncRunner>) {
    try {
      // Let sync spin up: TODO SITS WHY
      await delay(secondMs)
      await s?.donePromise()

      // Allow scheduled work to complete:
      await delay(secondMs)
      await this.service.promises.awaitSettled()
      await this.#work.awaitSettled()
      await forceRunTagMaintenance()
      if (isTest) stdoutWrite(SyncCompleted)
      // don't do this in a finally: it won't get called!
      setIsSyncing(false)
      if (this.exitWhenDone) {
        await exit({
          reason: "Finished",
          status: 0
        })
      }
    } catch (err) {
      setIsSyncing(false)
      if (this.exitWhenDone) {
        await exit({
          reason: "Error: " + err,
          status: 13
        })
      } else {
        this.logger.error("Sync error", err)
      }
    }
  }
}
