import { Command } from "commander"
import { CommandPlugin } from "../../core/cli/CLI"
import { Settings } from "../../core/settings/Settings"
import { isTrue } from "../../fe/Boolean"
import { Obj } from "../../fe/Object"
import { ExitWhenDone } from "./ExitWhenDone"

/**
 * Used by sync and sync-file
 */
export const ExitWhenDoneArg: CommandPlugin = {
  beforeParse: (cmd: Command) =>
    cmd.option(
      ExitWhenDone,
      "Exit after jobs are completed. Defaults to false unless paths are specified on the command line."
    ),
  afterParse: (opts: Obj) => {
    if (isTrue(opts.exitWhenDone)) {
      Settings.exitWhenDone.envValue = true
    }
  }
}
