import { Knex } from "knex"
import { mkLogger } from "../../core/Logger"
import {
  AssetFileVersion,
  AssetVersion
} from "../../core/PhotoStructureVersions"
import { max } from "../../core/math/Vector"
import { nativePath2uri } from "../../core/uri/FileURI"
import { PSLIB_ROOT_URI } from "../../core/uri/pslib"
import { volumes } from "../../core/volumes/Volumes"
import { isNotEmpty } from "../../fe/Array"
import { Maybe } from "../../fe/MaybeTypes"
import { thenCollect } from "../../fe/Promise"
import { toS } from "../../fe/toS"
import { Asset } from "../model/Asset"
import { AssetFile } from "../model/AssetFile"
import { Operation, OperationNames } from "../model/Operation"
import {
  Queue,
  assetFileUpdatesQueueName2uri,
  assetUpdatesUri2QueueName
} from "../stats/Queue"

function addIdsToQueue(q: Queue, ids: number[]) {
  return q.upsertWorkItems(ids.map(ea => ({ contents: toS(ea) })))
}

export async function currentUriRoots(): Promise<string[]> {
  const uris = await thenCollect(volumes(), async vol =>
    nativePath2uri(vol.mountpoint, vol)
  )
  return [...uris, PSLIB_ROOT_URI].map(toS)
}

export async function enqueueAssetFileUpdates(): Promise<string[]> {
  return thenCollect(currentUriRoots(), enqueueAssetFileUpdatesForUri)
}

async function enqueueAssetFileUpdatesForUri(uri: string) {
  const log = mkLogger("enqueueAssetFileUpdatesForUri(" + uri + ")")
  // Always make the queue:
  const queue = Queue.ops().upsertOne({
    name: assetFileUpdatesQueueName2uri(uri)
  })
  // Only enqueue new files once:
  await Operation.applyOnce(
    {
      name: OperationNames.enqueueAssetFileUpdates,
      version: AssetFileVersion,
      value: uri
    },
    () => {
      log.info("starting", { queue: queue.name })
      AssetFile.dbl.pluckBatched({
        onResults: (arr: number[]) => addIdsToQueue(queue, arr),
        qb: (qb: Knex.QueryBuilder, priors: Maybe<number[]>) => {
          qb = qb
            .select("id")
            .orderBy("id", "asc")
            .where("version", "<", AssetFileVersion)
            .andWhereLike("uri", uri + "%")
          if (isNotEmpty(priors)) {
            qb = qb.andWhere("id", ">", max(priors)!)
          }
          return qb
        }
      })
    }
  )
  // Only return the queue if there's work previously enqueued, or were just enqueued:
  const assetFileCount = queue.itemCount()
  log.log(
    assetFileCount === 0 ? "debug" : "info",
    "Added outdated assetFiles:",
    {
      queue: queue.name,
      assetFileCount
    }
  )

  return assetFileCount ? queue.name : undefined
}

export async function outdatedAssetCount() {
  const uris = await currentUriRoots()
  return Asset.dbl.pluckFirstf<number>(q =>
    q
      .count("Asset.id")
      .distinct()
      .leftJoin("AssetFile", "AssetFile.assetId", "Asset.id")
      .where("Asset.version", "<", AssetVersion)
      .andWhere(q2 =>
        uris.forEach(uri => q2.whereLike("AssetFile.uri", uri + "%"))
      )
  )
}

export async function enqueueAssetUpdates() {
  return thenCollect(currentUriRoots(), enqueueAssetUpdatesForUri)
}

export async function enqueueAssetUpdatesForUri(uri: string) {
  const log = mkLogger("enqueueAssetUpdatesForUri(" + uri + ")")
  const queue = Queue.ops().upsertOne({
    name: assetUpdatesUri2QueueName(uri)
  })

  await Operation.applyOnce(
    {
      name: OperationNames.enqueueAssetUpdates,
      version: AssetVersion,
      value: uri
    },
    () => {
      log.info("starting", { queue })
      return Asset.dbl.pluckBatched({
        onResults: arr => addIdsToQueue(queue, arr),
        qb: (qb: Knex.QueryBuilder, priors: Maybe<number[]>) => {
          qb = qb
            .select("Asset.id")
            .distinct()
            .leftJoin("AssetFile", "AssetFile.assetId", "Asset.id")
            .orderBy("Asset.id", "asc")
            .where("Asset.version", "<", AssetVersion)
            .andWhereLike("AssetFile.uri", uri + "%")
          if (isNotEmpty(priors)) {
            qb = qb.andWhere("Asset.id", ">", max(priors)!)
          }
          return qb
        }
      })
    }
  )
  const assetCount = queue.itemCount()
  if (assetCount === 0) return

  log.info("Added outdated assets:", {
    queue: queue.name,
    assetCount
  })
  return queue.name
}
