try {
  require("source-map-support").install()
} catch {
  //
}
import { CLI } from "../core/cli/CLI"
import { ColorArgs } from "../core/cli/ColorArgs"
import { WriteSettingsArg } from "../core/cli/WriteSettingsArg"
import { isElectron } from "../core/platform/Platform"
import { Settings } from "../core/settings/Settings"
import { ServiceNames } from "../fe/ServiceNames"
import { DropWorkQueuesArg } from "./cli/DropWorkQueuesArgs"
import { ExitWhenDoneArg } from "./cli/ExitWhenDoneArg"
import { ForceArg } from "./cli/ForceArg"
import { LogArgs } from "./cli/LogArgs"
import { NoFilterArg } from "./cli/NoFilterArg"
import { RebuildArg } from "./cli/RebuildArg"
import { SkipUpdateArg } from "./cli/SkipUpdateArg"
import { SyncService } from "./sync/SyncService"

async function run() {
  if (isElectron) {
    new SyncService() // < no CLI within electron
  } else {
    const cmd = new CLI(
      ServiceNames.sync,
      "[files-or-directories...]",
      "If paths are provided on the command line, they should be fully-qualified.\n\n* Paths to directories will be scanned recursively.\n* Paths to files will be imported if they pass configured filters.\n\nNote that sync will spawn 1 or more `worker` processes to work in parallel."
    )
      .add(
        RebuildArg,
        ForceArg,
        DropWorkQueuesArg,
        NoFilterArg,
        SkipUpdateArg,
        ExitWhenDoneArg,
        WriteSettingsArg,
        LogArgs,
        ColorArgs
      )
      .parse()

    Settings.argvScanPaths.envValue = cmd.args
    new SyncService()
  }
}

void run()
