import { mkLogger } from "../../core/Logger"
import { bestExistingAssetFile } from "../../core/asset-file/AssetFileSorter"
import { AbortError } from "../../core/async/AbortError"
import { LastOneInWins } from "../../core/async/LastOneInWins"
import { WrappedError } from "../../core/error/WrappedError"
import { AssetPreviewInfo } from "../../core/img/AssetPreviews"
import { Previews } from "../../core/img/Previews"
import { transcode_ } from "../../core/img/Video"
import { ContextualLogger } from "../../core/log/ContextualLogger"
import { RequiredForceContext } from "../../core/settings/ForceContext"
import { isEmpty } from "../../fe/Array"
import { defer } from "../../fe/Defer"
import { Maybe } from "../../fe/MaybeTypes"
import { gt0 } from "../../fe/Number"
import { Library } from "../Library"
import { AssetTaggingResult, tagAndUpsertAsset_ } from "../curators/AssetTagger"
import { Asset } from "../model/Asset"
import { mayCopyAssetsToLibrary } from "./AssetFileRepository"
import { updateAssetPreviews_ } from "./UpdateAssetPreviews"

const logger = defer(() => mkLogger("sync-file.AssetPostUpsertTasks"))
const lastOneInWins = defer(() => new LastOneInWins<number>("Asset tasks"))

export function assetPostUpsertTasks(asset: Asset, ctx: RequiredForceContext) {
  try {
    return assetPostUpsertTasks_(asset, ctx)
  } catch (error) {
    if (error instanceof AbortError) {
      logger().warn("aborted, next caller will clean up my mess", { error })
      return
    } else {
      throw error
    }
  }
}

/**
 * Runs all the steps needed to finish importing a given asset:
 *
 * - copyAssetsToLibrary
 * - updateAssetPreviews
 * - video transcoding, if necessary
 * - tagAndUpsertAsset
 *
 * If all is well, the asset will be marked as shown. If anything fails, the
 * asset will be marked as unshown.
 */
export function assetPostUpsertTasks_(asset: Asset, ctx: RequiredForceContext) {
  const log = logger().addContext(`.postUpsertAssetTasks_(Asset:${asset?.id})`)
  if (ctx.skipPreviews && ctx.skipAssetTagging) {
    log.info("skipped (skipPreviews && skipAssetTagging)")
    return
  }
  if (asset == null) {
    return log.throw("unexpected null asset", { asset })
  }
  const assetId = asset.id
  if (assetId == null) {
    return log.throw("unexpected null asset.id", { asset })
  } else {
    return lastOneInWins().enqueue(assetId, async ac => {
      try {
        return _postUpsertAssetTasks_(log, asset, ctx, ac.signal)
      } catch (error: any) {
        // < TODO SITS TYPING
        if (error instanceof AbortError) {
          log.warn("aborted, next caller will clean up my mess", { error })
          return
        }
        const causes = [error]
        log.error("failed, marking asset unshown", { error })
        try {
          asset.markUnshownAndUpsert()
        } catch (upsertError: any) {
          log.error("failed to mark asset unshown", { upsertError })
          causes.push(upsertError)
        }
        throw new WrappedError("Failed to post-process assset " + asset.id, {
          causes
        })
      }
    })
  }
}

async function _postUpsertAssetTasks_(
  log: ContextualLogger,
  asset: Asset,
  ctx: RequiredForceContext,
  ac: AbortSignal
) {
  const assetId = asset.id!
  const assetFiles = asset.getAssetFiles({ refresh: true })
  const result = {
    newAssetFileUris: [] as string[],
    previewsUpdated: false,
    tagResult: undefined as Maybe<AssetTaggingResult>,
    transcoded: false
  }

  log.throwIfAborted(ac)

  if (isEmpty(assetFiles)) {
    await Asset.remove({
      assetId,
      blocklistShas: false,
      unlinkAssetFiles: false
    })
    log.throw(`Asset:${assetId} had no asset files. Deleted.`)
  }

  // copy valid files to the library: (must be before we do the previews, so
  // we have a full set of asset files to work from)

  if (await mayCopyAssetsToLibrary()) {
    const repo = Library.instanceRequired().assetFileRepository()
    for (const af of assetFiles) {
      log.throwIfAborted(ac)
      try {
        if (await af.exists()) {
          const libAf = await repo.maybeCopyToLibrary_(af, ctx)
          if (libAf != null) {
            result.newAssetFileUris.push(libAf.uri)
          }
        }
      } catch (error) {
        log.warn(
          "Failed to copy variation to library, but we'll try to continue",
          {
            af,
            error
          }
        )
      }
    }
  }

  const info = ctx.skipPreviews
    ? undefined
    : await updateAssetPreviews_({
        asset,
        assetFiles: asset.getAssetFiles(),
        ...ctx,
        ac
      })
  const primary = await primaryOrShownAssetFile_(asset, info)

  if (primary == null) {
    return log.throw("null primaryOrShownAssetFile_()", { asset })
  }

  asset.markShownAndUpsert_(primary)
  if (ctx.skipAssetTagging) {
    log.info("skipping asset tagging")
  } else {
    result.tagResult = await tagAndUpsertAsset_(asset)
  }

  if (ctx.skipPreviews !== true && asset.isVideo()) {
    const primaryPosixFile = await asset.getShown()?.posixFile_()
    if (primaryPosixFile == null) {
      return log.throw("null asset.getShown().posixFile", { asset })
    }
    result.transcoded =
      null !=
      (await transcode_(
        primaryPosixFile,
        Previews.instance()!.ap(assetId).mp4(),
        { force: ctx.forceRebuildPreviews }
      ))
  }
  return result
}

async function primaryOrShownAssetFile_(
  asset: Asset,
  info: Maybe<AssetPreviewInfo>
) {
  // Can we pull from info?
  if (gt0(info?.assetFileId)) {
    const af = asset
      .getAssetFiles({ refresh: true })
      .find(ea => ea.id === info?.assetFileId)
    if (af == null) {
      return logger().throw("AssetPreviewInfo referenced unknown assetFileId", {
        asset,
        info
      })
    } else {
      return af
    }
  }
  // I guess we need to check what's in the db already:
  const prior = asset.getShown()
  if (prior != null) return prior

  // Crap, resort to best existing:
  return bestExistingAssetFile(asset.getAssetFiles())
}
